<template>
    <div>

    </div>
</template>

<script>
    import ExcelJS from 'exceljs';
    export default {
        props:[
            'excelData'
        ],
        data() {
            return {
                
            }
        },
        methods: {
            exportExcel(){
                // console.log('test')
                
                Promise.all([this.exportTable(),this.exportReadinnes()])
            },
            async exportTable() {
                let val = this.excelData
                // Create a new workbook
                const workbook = new ExcelJS.Workbook();
                // Add a worksheet
                const worksheet = workbook.addWorksheet('Sheet1');
                // Define the column headers
                const headers = ['Description','Required','Actual'];
                // Add the headers to the worksheet
                const headerRow = worksheet.addRow(headers);

             headerRow.eachCell((cell) => {
                cell.alignment = { horizontal: 'center' };
                cell.fill = {
                    type: 'pattern',
                    pattern: 'solid',
                    fgColor: { argb: 'FFFF00' } // Change 'FFFF00' to the desired color code
                };
                cell.font = { bold: true };
               
            });

             const selectedColumns = [
                'Detail',
                'Required',
                'Actual'
             ];
             // Add the data rows to the worksheet
             val.tableData.forEach(data => {
                     const row = selectedColumns.map(column => data[column] || '');
                     let excelRow = worksheet.addRow(row);

                        // Check if data.Title is equal to '1'
                        if (data.Title === '1') {
                            // Loop through each cell in the newly added row
                            excelRow.eachCell((cell) => {
                                cell.font = { bold: true };
                            });
                        }
                        // Find the index of the "Required" and "Actual" columns
                        const requiredIndex = selectedColumns.indexOf('Required');
                        const actualIndex = selectedColumns.indexOf('Actual');

                        if (data.Required && requiredIndex !== -1) {
                            // Set the horizontal alignment to center for the "Required" column
                            excelRow.getCell(requiredIndex + 1).alignment = { horizontal: 'center' };
                        }

                        if (data.Actual && actualIndex !== -1) {
                            // Set the horizontal alignment to center for the "Actual" column
                            excelRow.getCell(actualIndex + 1).alignment = { horizontal: 'center' };
                        }
             });
            //  // Set the column widths
             worksheet.columns.forEach((column,i) => {
                if(i == 0){
                    column.width = 70;
                }
                 
             });
            worksheet.eachRow((row) => {
                row.eachCell((cell) => {
                    cell.border = {
                        top: { style: 'thin' },
                        left: { style: 'thin' },
                        bottom: { style: 'thin' },
                        right: { style: 'thin' },
                    };
               
                });
            });
                // Generate a buffer from the workbook
                const buffer = await workbook.xlsx.writeBuffer();
    
                // Create a Blob from the buffer
                const blob = new Blob([buffer], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
    
                // Create a download link
                const url = window.URL.createObjectURL(blob);
                const link = document.createElement('a');
                link.href = url;
                link.download = `${this.excelData.teamName}-${this.excelData.month}-Equipment List.xlsx`;
    
                // Trigger the download
                link.click();
    
                // Clean up the URL object
                window.URL.revokeObjectURL(url);
            },



            async exportReadinnes() {
                // Create a new workbook
                // console.log(this.readinessData)
                const workbook = new ExcelJS.Workbook();
                // Add a worksheet
                const worksheet = workbook.addWorksheet('Sheet1');
                // Define the column headers
                const headers = ['','','TE','ACTUAL','%'];
                // Add the headers to the worksheet
                const headerRow = worksheet.addRow(headers);

             headerRow.eachCell((cell) => {
                cell.alignment = { horizontal: 'center' };
                cell.fill = {
                    type: 'pattern',
                    pattern: 'solid',
                    fgColor: { argb: 'FFFF00' } // Change 'FFFF00' to the desired color code
                };
                cell.font = { bold: true };
               
            });
                const selectedColumns = [
                    'Category',
                    'Sub',
                    'Required',
                    'Actual',
                    'Percent'
                ];
                // Add the data rows to the worksheet
                this.readinessData.forEach(data => {
                        const row = selectedColumns.map(column => data[column] || '');
                        let excelRow = worksheet.addRow(row);

                            // Check if data.Title is equal to '1'
                            // if (data.Title) {
                            //     // Loop through each cell in the newly added row
                            //     excelRow.eachCell((cell) => {
                            //         cell.font = { bold: true };
                            //     });
                            // }
                            // Find the index of the "Required" and "Actual" columns
                            const requiredIndex = selectedColumns.indexOf('Required');
                            const actualIndex = selectedColumns.indexOf('Actual');
                            const percentIndex = selectedColumns.indexOf('Percent');

                            if (data.Required && requiredIndex !== -1) {
                                // Set the horizontal alignment to center for the "Required" column
                                excelRow.getCell(requiredIndex + 1).alignment = { horizontal: 'center' };
                            }

                            if (data.Actual && actualIndex !== -1) {
                                // Set the horizontal alignment to center for the "Actual" column
                                excelRow.getCell(actualIndex + 1).alignment = { horizontal: 'center' };
                            }
                            if (data.Percent && percentIndex !== -1) {
                                // Set the horizontal alignment to center for the "Actual" column
                                excelRow.getCell(percentIndex + 1).alignment = { horizontal: 'center' };
                            }
                });
                //  // Set the column widths
                worksheet.columns.forEach((column,i) => {
                    if(i == 0 || i == 1){
                        column.width = 40;
                    }
                    
                });
                worksheet.eachRow((row) => {
                    row.eachCell((cell) => {
                        cell.border = {
                            top: { style: 'thin' },
                            left: { style: 'thin' },
                            bottom: { style: 'thin' },
                            right: { style: 'thin' },
                        };
                
                    });
                });
                // Generate a buffer from the workbook
                const buffer = await workbook.xlsx.writeBuffer();
    
                // Create a Blob from the buffer
                const blob = new Blob([buffer], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
    
                // Create a download link
                const url = window.URL.createObjectURL(blob);
                const link = document.createElement('a');
                link.href = url;
                link.download = `${this.excelData.teamName}-${this.excelData.month}-Equipment Readiness Report.xlsx`;
    
                // Trigger the download
                link.click();
    
                // Clean up the URL object
                window.URL.revokeObjectURL(url);
            }



            
        },
        computed:{
            readinessData(){
                let data = []
                
                

                this.excelData.totalTable.forEach((item,i) => {
                    
                
                    let obj = {
                        Title :null,
                        Category :null,
                        Sub: null,
                        Required : null,
                        Actual: null,
                        Percent:null,
                        Total:null,
                        Primary:null,
                        Secondary:null,
                    }
                   
                    if(item.Category == item.Sub){
                        obj.Title = 1
                        obj.Category = item.Category
                        obj.Required = item.Category != 'MOBILITY' && item.Category != 'FIREPOWER'?item.totalRequired:''
                        obj.Actual = item.Category != 'MOBILITY' && item.Category != 'FIREPOWER'?item.totalActual:''
                        obj.Percent = item.percent && item.Category != 'MOBILITY' && item.Category != 'FIREPOWER'?item.percent.toFixed(2)+'%':''
                    }
                    if(item.Category != item.Sub){
                        obj.Sub = item.Sub
                        obj.Required = item.totalRequired
                        obj.Actual = item.totalActual
                        obj.Percent = item.percent?item.percent.toFixed(2)+'%':''
                    }
                   
                   
               
                    
                    data.push(obj)
                    if(item.break){
                        let nextLine = {
                            Title : 2,
                            Category : '',
                            Sub : 'TOTAL',
                            Required : item.info == 'primary' ? this.excelData.totalPrimaryRequired : this.excelData.totalSecondaryRequired,
                            Actual : item.info == 'primary' ? this.excelData.totalPrimaryActual : this.excelData.totalSecondaryActual,
                            Percent : item.info == 'primary' ? this.excelData.averagePrimary + '%' : this.excelData.averageSecondary + '%'
                        }
                        data.push(nextLine)
                    }

                    if(item.break && i + 1){
                        let nextLine = {
                            Title : 3,
                            Category : '',
                            Sub : item.info == 'primary' ? 'MISSION ESSENTIAL EQUIPMENT (80%)' : 'SECONDARY EQUIPMENT',
                            Required : '',
                            Actual : '',
                            Percent : item.info == 'primary' ? this.excelData.mee.toFixed(2) + '%' : this.excelData.see.toFixed(2) + '%'
                        }
                        data.push(nextLine)
                    }

                    if(!this.excelData.totalTable[i +1]){
                        let nextLine = {
                            Title : 3,
                            Category : '',
                            Sub : 'MEE +SE',
                            Required : '',
                            Actual : '',
                            Percent : Number(this.excelData.mee.toFixed(2)) +  Number(this.excelData.see.toFixed(2)) + '%'
                        }
                        data.push(nextLine)
                    }

                });
                return data
            }
        }

        
    }
</script>

