<template>
    <div>
        <v-dialog v-model="remarksDialog" width="40%" persistent scrollable>
            <v-card elevation="2">
                <v-card-title style="background-color: #0275d8; color: white;">
                    SUBMIT
                    <v-spacer/><v-btn  @click="remarksDialog = false" icon><v-icon color="white">mdi-close-circle-outline</v-icon></v-btn>
                </v-card-title>
                <v-card-text >
                    <br>
                    <div v-show="remarksData.length == 0"  v-for="(item,i) in questionData" :key="i" > 
                        <label>{{ item.Question }}</label>
                        <v-textarea
                            dense
                            outlined
                            auto-grow 
                            v-model="remarks[i]"
                        />
                            <!-- <input type="hidden" v-model="question[i]" :value="item.id" /> -->
                    </div>
                    <div v-show="remarksData.length > 0">
                        <h3 style="text-align:center;">To edit assessment, please go to the '{{ table?table.toUpperCase():null }} READINESS RATING' tab</h3>
                    </div>
                    
                </v-card-text>

                <v-card-actions>
                    <v-btn color="success" block medium @click="submit()">SUBMIT</v-btn>
                </v-card-actions>
                <v-overlay :value="overlay">
                    <v-progress-circular
                        indeterminate
                        size="64"
                    ></v-progress-circular>
                </v-overlay>
            </v-card>
        </v-dialog>

    </div>
</template>

<script>
    import axios from 'axios'
    import moment from 'moment'
    import { mapState,mapActions  } from 'vuex';
 
    export default {
        props: [
            'getMaintenance',
            'getEquipment',
            'getTraining',
            'getApprovedMet',
            'getFacility',
            'getPersonnel',
            'getAll'
        ],
        data:()=>({
            remarksDialog:false,
            remarks:[],
            month:null,
            team : null,
            overlay:false,
            table: null
        }),
        methods:{
            ...mapActions([
                'getLastMonth'
            ]),
            openDialog(month,team,table){
                // console.log('open')
                this.remarksDialog = true
                this.overlay = true
                this.month = month
                this.team = team
                this.table = table
                // console.log(this.remarksData,'ceo')
            },
            submit(){
                this.overlay = true
                if(this.remarksData.length > 0){
                    // this.sendMail()
                    this.update()
                }else{
                    axios({
                    method:'post',
                    url:`${this.$baseUrl}api/insertRemarks`,
                    data:{
                        remarks : this.remarks,
                        Month : this.month,
                        Team : this.team,
                        table : this.table,
                        Name : this.$store.state.user.name,
                        Time : moment().format('YYYY-MM-DD')
                    }
                    }).then(res=>{
                        if(res.status == 200){
                            // this.sendMail()
                            this.update()
                        }
                    })
                }
                
                
            },
            update(){
                let user = this.$store.state.user
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/updateSubmit`,
                    data : {
                        Department : user.main,
                        Section : user.unit,
                        Team : user.main == 'D01'?user.office:user.unit,
                        Month : this.month,
                        Table : this.table,
                        Name : user.name,
                        Time : moment().format('YYYY-MM-DD HH:mm:ss')

                    }
                }).then(async res=>{
                    if(res.data != 'error'){
                        alert('Submitted')
                        // await this.$store.dispatch('getLastMonth');
                        this.overlay = false
                        this.remarksDialog = false
                        this.table == 'personnel'?this.getPersonnel():''
                        this.table == 'training'?(this.getTraining(),this.getApprovedMet('approved')):''
                        this.table == 'equipment'?this.getEquipment():''
                        this.table == 'maintenance'?this.getMaintenance():''
                        this.table == 'facility'?this.getFacility():''
                        this.table == 'n3'?this.getAll():''
                        // this.sendMail() //uncomment if email function is ok
                    }else{
                        alert('You are not allowed to submit this report')
                        this.overlay = false
                        this.remarksDialog = false
                    }
                    
                })
            },
            // email function
            sendMail(){
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/sendMail`,
                    data : {
                        Department : this.user.main,
                        Section : this.user.unit,
                        Team : this.user.main == 'D01'?this.user.office:this.user.unit,
                        TeamName : this.user.main == 'D01'?this.user.officeName:this.user.unitName,
                        Month : this.formattedDate,
                        Table : this.table,
                        Name : this.user.name,
                        Time : moment().format('YYYY-MM-DD HH:mm:ss'),
                        Status : 'OIC'
                    }
                }).then(async res=>{
                    if(res.status == 200){
                        alert('Submitted')
                        await this.$store.dispatch('getLastMonth');
                        this.overlay = false
                        this.remarksDialog = false
                        this.table == 'training'?(this.getTraining(),this.getApprovedMet('approved')):''
                        this.table == 'equipment'?this.getEquipment():''
                        this.table == 'maintenance'?this.getMaintenance():''
                        this.table == 'facility'?this.getFacility():''
                    }else{
                        alert('Report not sent') 
                    }
                    
                    
                })
            }
        },
        computed:{
            ...mapState([
                'questionData',
                'remarksData',
                'user'
            ]),
            formattedDate() {
                if (this.month) {
                    const date = new Date(this.month);
                    const month = date.toLocaleString('default', { month: 'long' });
                    const year = date.getFullYear();
                    return `${month} ${year}`;
                }
                return null;
        },
        },
        watch:{
            questionData(val){
                // console.log(val)
                if(val){
                    this.overlay = false
                }
            }
        },
        created() {
        // this.remarks = this.questionData.map((item) => ({
        //     id: item.id,
        //     remarks: ''
        // }));
        },
    }
</script>

