<template>
    <div>
        <v-dialog v-model="reportDialog" persistent scrollable>
            <v-card>
                <v-card-title style="background-color: #0275d8; color: white;">
                    PERSONNEL READINESS
                    <v-spacer/><v-btn  @click="reportDialog = false" icon><v-icon color="white">mdi-close-circle-outline</v-icon></v-btn>
                </v-card-title>
                <br>
                    <v-card-text>
                        <h1><center>{{ team }}</center></h1>
                        <h2><center>{{ formattedDate }}</center></h2>
                        <v-col cols="12" md="12" lg="12" xl="12">
                            <v-tabs v-model="personel_tab" justify-center>
                                <v-tab href="#pqr" @click="pqr('pqr')" v-if="tableData">PERSONNEL QUALIFICATION RATING</v-tab>
                                <v-tab href="#psg" @click="psg('psg')" v-if="tableData.length > 0">PERSONNEL STRENGTH BY GRADE</v-tab>
                                <v-tab href="#prs" @click="prs('prs')" v-if="tableData.length > 0">PERSONNEL READINESS RATING</v-tab>
                            </v-tabs>
                        </v-col>
                        <v-tabs-items v-model="personel_tab">
                            <v-tab-item :value="'pqr'">
                                        <v-container>
                                            <v-simple-table
                                                fixed-header
                                                class="elevation-1 custom-table"
                                                height="80vh"
                                            >
                                                <thead>
                                                    <tr>
                                                        <th>DESCRIPTION/POSITION</th>
                                                        <th width="25%">NAME</th>
                                                        <th>RANK REQUIRED</th>
                                                        <th>ACTUAL</th>
                                                        <th>POINTS</th>
                                                        <th>AFPOS</th>
                                                        <th>ACTUAL</th>
                                                        <th>POINTS</th>
                                                        <th>REQUIRED</th>
                                                    
                                                    </tr>
                                                </thead>
                                                <tbody v-for="(item, i) in tableData" :key="i">
                                                    <tr v-if="item.GroupBranch">
                                                        <td style="font-weight:bold;font-size:15px;">{{ item.GroupBranch }}</td>
                                                    </tr>
                                                    <tr>
                                                        <td style="text-align:left !important">{{ item.PositionName }}</td>
                                                        <td style="text-align:left !important">{{ item.RankName }} {{ item.Name }} {{ item.ServiceCode }}</td>
                                                        <td>{{ item.requiredGradeName }}</td>
                                                        <td>{{ item.RankCode }}</td>
                                                        <td>{{ item.actualGrade }}</td>
                                                        <td>{{ item.requiredAfposName }}</td>
                                                        <td>{{ item.AfposName }}</td>
                                                        <td>{{ item.actualAfpos }}</td>
                                                        <td>{{ item.requiredMp }}</td>
                                                    </tr>
                                                </tbody>
                                            </v-simple-table>
                
                                            <v-overlay :value="overlay">
                                                <v-progress-circular
                                                    indeterminate
                                                    size="64"
                                                ></v-progress-circular>
                                            </v-overlay>
                                        </v-container>
                            
                            </v-tab-item>
                            <!-- psg tab -->
                            <v-tab-item :value="'psg'">
                                <v-container>
                                    <v-row>
                                        <v-col cols="12" md="3" lg="3" xl="3">
                                            <v-card>
                                                <v-simple-table
                                                    class="elevation-1 custom-table"
                                                    fixed-header
                                                >
                                                    <thead>
                                                        <tr>
                                                            <th colspan="2">TOTAL</th>
                                                        </tr>
                                                    </thead>
                                                        <tbody>
                                                            <tr>
                                                                <td>TOE</td>
                                                                <td  class="total">{{ totalToe }}</td>
                                                            </tr>
                                                            <tr>
                                                                <td>ACTUAL</td>
                                                                <td  class="total">{{ totalActual }}</td>
                                                            </tr>
                                                            <tr>
                                                                <td colspan="2" style="background-color:#0275d8;color:white;"><b>PSG RATING</b></td>
                                                            </tr>
                                                            <tr>
                                                                <td colspan="2"  class="total">{{ psgRating }}%</td>
                                                            </tr>
                                                        </tbody>
                                                    
                                                </v-simple-table>
                                            </v-card>
                                        </v-col>
                                        <v-col cols="12" md="9" lg="9" xl="9">
                                            <v-card max-width="100%">
                                                <v-simple-table
                                                    fixed-header
                                                    class="elevation-1 custom-table"
                                                    height="80vh"
                                                >
                                                    <thead>
                                                        <tr>
                                                        <th>GRADE</th> 
                                                        <th>TOE</th> 
                                                        <th>ACTUAL</th> 
                                                        </tr>
                                                    </thead>
                                                    <tbody v-for="(item,i) in psgTable" :key="i">
                                                        <tr v-if="item.GroupDivision">
                                                            <td style="font-weight:bold;font-size:15px;">{{ item.GroupDivision }}</td>
                                                        </tr>
                                                        <tr v-if="item.GradeName != '-'">
                                                            <td >{{ item.GradeName }}</td>
                                                            <td >{{ item.countRequired }}</td>
                                                            <td >{{ item.countActual?item.countActual:'' }}</td>
                                                        </tr>
                                                        <template v-if="!item.GroupDivision && (!psgTable[i + 2] || psgTable[i + 1].GroupDivision)">
                                                            <tr style="background-color:lightgray;" v-if="item.GradeName != '-'">
                                                                <td style="font-weight:bold;font-size:15px;">Sub-Total</td>
                                                                <td style="font-weight:bold;font-size:15px;">{{ item.totalCountRequired }}</td>
                                                                <td style="font-weight:bold;font-size:15px;">{{ item.totalCountActual }}</td>
                                                            </tr>
                                                        </template>
                                                    </tbody>
                                                </v-simple-table>
                                            </v-card>
                                        </v-col>
                                    </v-row>
                                </v-container>
                            </v-tab-item>
                            <!--  -->
                            <v-tab-item :value="'prs'">
                                <v-container>
                                    <v-card elevation="2">
                                            <v-simple-table
                                                class="custom-table"
                                            >
                                            <thead>
                                                <tr>
                                                    <th>RATING</th>
                                                    <th>PERCENTAGE</th>
                                                    <th>WEIGHT<br>PERCENTAGE</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr>
                                                    <td>PSGR (50%)</td>
                                                    <td>{{ psgRating }}%</td>
                                                    <td>{{ psgRating05 }}%</td>
                                                </tr>
                                                <tr>
                                                    <td>Personnel Qualification Rating (50%)</td>
                                                    <td>{{ pqrRating05 }}%</td>
                                                    <td>{{ pqrRatingTotal}}%</td>
                                                </tr>
                                                <tr>
                                                    <td>PRR (40%)</td>
                                                    <td>{{ gradeRating }}%</td>
                                                    <td>{{ gradeRating04 }}%</td>
                                                </tr>
                                                <tr>
                                                    <td>PSR (60%)</td>
                                                    <td>{{ afposRating }}%</td>
                                                    <td>{{ afposRating06}}%</td>
                                                </tr>
                                                <tr>
                                                    <td>TOTAL (PSGR + PQR)</td>
                                                    <td></td>
                                                    <td>{{ totalRating }}%</td>
                                                </tr>
                                            </tbody>  
                                            </v-simple-table>
                                        
                                    </v-card>
                                </v-container>
                         
                                <v-divider />
                                <br>
                                <v-container>
                                <h3>Assessment:</h3>
                                
                                    <v-card elevation="2">
                                        
                                            <v-simple-table
                                                class="custom-table"
                                            >
                                            
                                            <tbody v-for="(val, e) in remarksData" :key="e">
                                                <tr>
                                                    <td style="font-weight: bold;text-align: left !important;background-color: lightgray;">{{ val.Question }}</td>
                                                </tr>
                                                <tr height="90%">
                                                    <td style="text-align: left !important;white-space: pre-wrap;">{{ val.Remarks }}</td>
                                                </tr>
                                            </tbody>  
                                            </v-simple-table>
                                    
                                    </v-card>
                            </v-container>
                            </v-tab-item>
                        </v-tabs-items>
                    </v-card-text>
                
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
    import {mapState} from 'vuex'       
    export default {
        data:()=>({
            reportDialog:false, 
            tableData:[], 
            personel_tab:'pqr',
            generateDate:null,
            team:null,
            tableContainer:[],
     
        }),
        methods:{
           async openDialog(item){
                this.generateDate = item.Month
                this.team = item.UnitTeamName
                this.reportDialog = true
                await this.$store.dispatch('getLastMonth')
                await this.$store.dispatch('diplayRemarks')
                this.tableContainer = this.lastMonthData
            },
            decryptData(encryptedData) {
                const decryptedData = atob(encryptedData);
                return decryptedData;
            },
            pqr(tab){
                this.personel_tab = tab
            },
            psg(tab){
                this.personel_tab = tab
            },
            prs(tab){
                this.personel_tab = tab
                this.$store.dispatch('diplayRemarks','personnel')
            },
            getTable(val){
                if(val == 'noData'){
                    this.tableData = [{}]
                }else{
                    try{
                        const encryptedData = val;
                        const decryptedData = this.decryptData(encryptedData);
                        let tempData = JSON.parse(decryptedData);
                            this.tableData = tempData.reduce((total,item)=>{
                                let index = total.findIndex(res=> res.BranchCode == item.BranchCode)
                                if(index == -1){
                                    let obj = {...item}
                                    obj.GroupBranch = item.BranchName
                                    obj.TotalMP = Number(item.requiredMp)
                                    total.push(obj)
                                }else{
                                    if (!isNaN(item.requiredMp)) {
                                        total[index].TotalMP += Number(item.requiredMp);
                                    }
                                    total[index].count += 1
                                    item.total = total[index].TotalMP
                                    total.push(item)
                                }
                                return total
                            },[]).filter(r=>{
                                // let num1 = parseInt(this.val1.match(/\d+/)[0]); // Extract the numeric part of val1
                                // let num2 = parseInt(this.val2.match(/\d+/)[0]); // Extract the numeric part of val2
                                // this.result = num1 - num2;
                                if(r.requiredGradeName.includes(r.RankCode)){
                                    r.actualGrade = 1
                                }else if(r.RankCode && r.RankCode.match(/\d+/) - r.requiredGradeName.match(/\d+/) == 1){
                                    r.actualGrade = 1
                                }else{
                                    r.actualGrade = 0
                                }
                                if(r.RequiredAfpos == r.AfposCode || r.RequiredAfpos == 'AF011' && r.AfposCode){
                                    r.actualAfpos = 1
                                }else{
                                    r.actualAfpos = 0
                                }
                                return r
                                
                            })
                            // console.log(this.tableData,'ceo')
                
                        } catch(error){
                            console.error('Error fetching and decrypting grade data:', error);
                        }
                }
            },
        },
        computed:{
            ...mapState([
                'teamItems',
                'lastMonthData',
                'branchItems',
                'positionItems',
                'rankItems',
                'serviceItems',
                'afposItems',
                'psgTable',
                'gradePoints',
                'afposPoints',
                'overlay',
                'remarksData'
            ]),
            formattedDate() {
                if (this.generateDate) {
                    const date = new Date(this.generateDate);
                    const month = date.toLocaleString('default', { month: 'long' });
                    const year = date.getFullYear();
                    return `${month} ${year}`;
                }
                return null;
            },
             // function to get total TOE for psg tab
        totalToe(){
            if(this.psgTable.length > 0){
                return this.psgTable.reduce((total, item) => {
                    if (item.TotalRequired) {
                        return total + item.TotalRequired;
                    }
                    return total;
                    }, 0);
            }else{
                return 0
            }
        },
        // function to get total Actual for psg tab
        totalActual(){
            if(this.psgTable.length > 0){
                return this.psgTable.reduce((total, item) => {
                    if (item.TotalActual) {
                        return total + item.TotalActual;
                    }
                    return total;
                    }, 0);
            }else{
                return 0
            }
        },
        // function to get total psg for psg tab
        psgRating(){
            if(this.psgTable.length > 0){
               let result
                result = this.totalActual / this.totalToe * 100
               return result.toFixed(2)
            }else{
                return 0
            }
        },
        gradeRating(){
            let result
            result = this.gradePoints / this.totalActual * 100
            return result.toFixed(2)
        },
        gradeRating04(){
            let result
            result = this.gradeRating * 0.4
            return result.toFixed(2)
        },
        afposRating(){
            let result
            result = this.afposPoints / this.totalActual * 100
            return result.toFixed(2)
        },
        afposRating06(){
            let result
            result = this.afposRating * 0.6
            return result.toFixed(2)
        },


        psgRating05(){
            let data
                data = this.psgRating?this.psgRating * 0.5:0
            return data.toFixed(2)
        },
        pqrRating05(){
            let data
                data = this.pqrRatingTotal * 0.5
            return data.toFixed(2)
        },
        pqrRatingTotal(){
            let data
                data = (this.gradeRating * 0.4)+(this.afposRating * 0.6)
            return data.toFixed(2) 
        },
        totalRating(){
            let data
                data = Number(this.psgRating05) + Number(this.pqrRating05)
            return data
        },

        },
        watch:{
            lastMonthData(val){
                if(val){
                    this.getTable(val)
                }
            },
            tableData(val){
                if(val){
                    this.$store.dispatch('getPsgTable')
                    this.$store.dispatch('getPoints')
                }
            },
            tableContainer(val){
                if(val){
                    this.getTable(val)
                }
            }
        }
    }
</script>

<style lang="scss" scoped>

</style>