<template>
    <div>
        <v-dialog v-model="printDialog" persistent width="300px">
            <v-card>
                <v-card-title style="background-color: #0275d8;color:white;">Download File
                    <v-spacer /><v-btn  @click="printDialog = false" icon><v-icon color="white">mdi-close-circle-outline</v-icon></v-btn>
                </v-card-title>
                <br />
                <br />
                <v-card-text>
                    <v-checkbox 
                        v-model="selected"
                        label="Print PDF"
                        value="pdf"
                    />
                    <v-checkbox 
                        v-model="selected"
                        label="Extract Excel"
                        value="excel"
                    />
                </v-card-text>
                <v-card-actions>
                    <v-btn 
                        color="success" 
                        :disabled="selected.length > 0?false:true" 
                        block 
                        @click="download()"
                        :loading="loading"
                    >
                        DOWNLOAD
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
    
    export default {
        props:[
            'printPdf',
            'printExcel'
        ],
        data() {
            return {
                printDialog:false,
                selected:[],
                loading:false,
                
            }
        },
        methods: {
           async openDialog(){
                this.printDialog = true
    
            },
            download(){
                const confirmed = window.confirm('Are you sure you want to download this report?');
                if(confirmed){
                    this.loading = true
                    if(this.selected.includes('pdf')){
                        this.printPdf()
                        this.printDialog = false
                    }
                    if(this.selected.includes('excel')){
                        this.printExcel()
                        this.printDialog = false
                    }
                }
               
               
            }
        },
        watch:{
            printDialog(val){
                if(!val){
                    this.selected = []
                    this.loading = false
                }
            }
        }
    }
</script>

<style lang="scss" scoped>

</style>