<template>
    <div>
        <h1><center>UPDATE TRAINING</center></h1>
        <v-divider/>
        <v-col cols="12" md="12" lg="12" xl="12">
            <v-tabs v-model="tab" justify-center>
                <v-tab href="#tab1" @click="tab1('tab1')" >MET</v-tab>
                <v-tab href="#tab2" @click="tab2('tab2')" >ACTIVITY</v-tab>
            </v-tabs>
        </v-col>
        <br>
        
            <v-tabs-items v-model="tab">
                <v-tab-item :value="'tab1'">
                    <v-row>
                        <v-col cols="12" md="3" lg="3" cl="3">
                            <v-card elevation="3" class="justify-center" >
                                <v-card-title style="background-color: #0275d8;color:white;"><center>CONTROL PANEL</center></v-card-title>
                                <br />
                                <v-card-text>
                                    <v-form ref="add" @submit.prevent="add">
                                        <v-autocomplete 
                                            label="Category"
                                            item-text="CategoryName"
                                            item-value="CategoryCode"
                                            dense
                                            outlined
                                            :items="categoryItems"
                                            v-model="category"
                                            @change="getDepartment()"
                                            :rules="rules.required"
                                            clearable
                                        
                                        />
                                        <v-autocomplete 
                                            label="Department"
                                            item-text="UnitDepartmentName"
                                            item-value="UnitDepartmentCode"
                                            dense
                                            outlined
                                            :items="departmentItems"
                                            v-model="department"
                                            @change="getSection()"
                                            :rules="rules.required"
                                            :clearable="category?true:false"
                                        />
                                        <v-autocomplete 
                                            label="Unit"
                                            item-text="UnitSectionName"
                                            item-value="UnitSectionCode"
                                            dense
                                            outlined
                                            :items="sectionItems"
                                            v-model="section"
                                            @change="getTeam()"
                                            :rules="rules.required"
                                            :clearable="category && department?true:false"
                                        />
                                        <v-autocomplete 
                                            label="Sub Unit"
                                            item-text="UnitTeamName"
                                            item-value="UnitTeamCode"
                                            dense
                                            outlined
                                            :items="teamItems"
                                            v-model="team"
                                            :rules="rules.required"
                                            v-if="category == 'CA01'"
                                           
                                            :clearable="category && department && section?true:false"
                                        />
                                        <v-autocomplete
                                            v-model="selectedYear"
                                            label="Select Year"
                                            dense
                                            outlined
                                            :items="year"
                                            @change="getTable()"
                                        />
                                        <div v-show="(category == 'CA01' && team && selectedYear || category != 'CA01' && section && selectedYear) && tableData.length == 0">
                                            <input type="file" ref="fileInput" @change="handleFileSelect" accept=".csv"   class="file-input"/>
                                            <v-btn color="success" dark block medium @click="upload()" >UPLOAD</v-btn>
                                        </div>
                                        <div v-show="tableData.length > 0">
                                            <v-btn color="error" dark block medium @click="deleteTable('training')" >DELETE</v-btn>
                                        </div>
                                        
            
                                    </v-form>
                                </v-card-text>
                            </v-card>
                        </v-col>
                        <v-col cols="12" md="9" xl="9" lg="9">
                            <v-card elevation="3" >
                                <!-- <v-card-text> -->
                                    <v-simple-table
                                        fixed-header
                                        class="elevation-1 custom-table"
                                    >
                                        <thead>
                                            <tr>
                                                <th>Mission Essential Task</th>
                                                <th>Quarter1</th>
                                                <th>Quarter2</th>
                                                <th>Quarter3</th>
                                                <th>Quarter4</th>
                                                <!-- <th>Required</th> -->
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="(item,i) in tableData" :key="i">
                                                <td>{{ item.Met }}</td>
                                                <!-- <td>{{ item.ProgramMet }}</td> -->
                                                <td>{{ item.Quarter1 }}</td>
                                                <td>{{ item.Quarter2 }}</td>
                                                <td>{{ item.Quarter3 }}</td>
                                                <td>{{ item.Quarter4 }}</td>
                                                <!-- <td>{{ item.Required }}</td> -->
                                            </tr>
                                        </tbody>
                                    </v-simple-table>
                                <!-- </v-card-text> -->
                            </v-card>
                        </v-col>
                    </v-row>
                   
                </v-tab-item>

                <v-tab-item :value="'tab2'">
                    <!-- <v-row>
                        <v-col cols="12" md="3" lg="3" xl="3">
                            <v-autocomplete
                                v-model="selectedYear"
                                label="Select Year"
                                dense
                                outlined
                                :items="year"
                                @change="getMetTable()"
                            ></v-autocomplete>
                           
                        </v-col>
                        <v-col cols="12" md="3" lg="3" xl="3" v-show="selectedYear && metTable.length == 0">
                            <input type="file" ref="fileInput" @change="handleFileSelect" accept=".csv"   class="file-input"/>
                        </v-col>
                        <v-col cols="12" md="2" lg="2" xl="2" v-show="selectedYear && metTable.length == 0">
                            <v-btn color="success" @click="uploadActivity()" large>UPLOAD</v-btn>
                        </v-col>
                    </v-row> -->
                    <v-row>
                        <v-col cols="12" md="3" lg="3" xl="3">
                       
                            <v-card elevation="3" class="justify-center">
                                <v-card-title class="d-flex justify-center" style="background-color: #0275d8; color: white;">
                                    CONTROL
                                </v-card-title>
                                <v-container>
                                    <div>
                                        <br>
                                               
                                        <v-autocomplete 
                                            label="Category"
                                            item-text="CategoryName"
                                            item-value="CategoryCode"
                                            dense
                                            outlined
                                            :items="categoryItems"
                                            v-model="category"
                                            @change="getDepartment()"
                                            :rules="rules.required"
                                            clearable
                                        
                                        />
                                        <v-autocomplete 
                                            label="Department"
                                            item-text="UnitDepartmentName"
                                            item-value="UnitDepartmentCode"
                                            dense
                                            outlined
                                            :items="departmentItems"
                                            v-model="department"
                                            @change="getSection()"
                                            :rules="rules.required"
                                            :clearable="category?true:false"
                                        />
                                        <v-autocomplete 
                                            label="Unit"
                                            item-text="UnitSectionName"
                                            item-value="UnitSectionCode"
                                            dense
                                            outlined
                                            :items="sectionItems"
                                            v-model="section"
                                            @change="getTeam()"
                                            :rules="rules.required"
                                            :clearable="category && department?true:false"
                                        />
                                        <v-autocomplete 
                                            label="Sub Unit"
                                            item-text="UnitTeamName"
                                            item-value="UnitTeamCode"
                                            dense
                                            outlined
                                            :items="teamItems"
                                            v-model="team"
                                            :rules="rules.required"
                                            v-if="category == 'CA01'"
                                            @change="getTable(),changeTeam()"
                                            
                                            :clearable="category && department && section?true:false"
                                        />
                                  
                                        <v-autocomplete
                                            v-model="selectedYear"
                                            label="Select Year"
                                            dense
                                            outlined
                                            :items="year"
                                            @change="getMetTable()"
                                        />
                                      
                                        <!-- <v-col cols="12" md="3" lg="3" xl="3" > -->
                                            <!-- <input block type="file" ref="fileInput" @change="handleFileSelect" accept=".csv"   class="file-input"  v-show="selectedYear && metTable.length == 0" /> -->
                                        <!-- </v-col> -->
                                        <!-- <v-col cols="12" md="2" lg="2" xl="2" > -->
                                            <!-- <v-btn color="success" @click="uploadActivity()" large block v-show="selectedYear && metTable.length == 0">UPLOAD</v-btn> -->
                                        <!-- </v-col> -->
                                   
            
                                    </div>
                                    <v-div  v-show="selectedYear && metTable.length == 0">
                                        <input block type="file" ref="fileInput" @change="handleFileSelect" accept=".csv"   class="file-input"  />
                                        <v-btn color="success" @click="uploadActivity()" large block>UPLOAD</v-btn>
                                    </v-div>
                                    <div v-show="metTable.length > 0">
                                        <v-btn color="error" dark block medium @click="deleteTable('training_met')" >DELETE</v-btn>
                                    </div>
                                </v-container>
                            </v-card>
                        </v-col>
                   
                   
                        <v-col cols="12" md="9" xl="9" lg="9">
                            <div v-show="metTable.length > 0">
                                <v-simple-table >
                                    <thead>
                                        <tr>
                                            <th>ACTIVITY FOR YEAR {{ this.selectedYear }}</th>
                                            <th>UpdatedDate</th>
                                            <th>UpdatedBy</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(item,i) in metTable" :key="i">
                                            <td style="text-align:left !important;">{{ item.Met }}</td>
                                            <td >{{ item.UpdatedDate }}</td>
                                            <td >{{ item.UpdatedBy }}</td>
                                        </tr>
                                    </tbody>
                                </v-simple-table>
                            </div>
                        </v-col>
                    
                    </v-row>
                    
                </v-tab-item>
                
            </v-tabs-items>
            <v-overlay :value="overlay">
                <v-progress-circular
                    indeterminate
                    size="64"
                ></v-progress-circular>
            </v-overlay>

    </div>
</template>

<script>
    import axios from 'axios'
    import Papa from 'papaparse';
    import moment from 'moment';
    import DeleteRequired from '../Details/DeleteTable'
    import { mapState } from 'vuex';

    export default {
        data() {
            return {
                category:null,
                categoryName:null,
                categoryItems:[],
                division:null,
                department:null,
                newDepartmentName:null,
                departmentItems:[],
                section:null,
                newSectionName:null,
                sectionItems:[],
                team:null,
                newTeamName:null,
                teamItems:[],
                tableData:[],
                rules:{
                    required: [
                        v => !!v || 'Field is required'
                    ],
                },
                tempFile:[],
                uploadFile:[],
                overlay:false,
                tab:'tab1',
                selectedYear: null,
                metTable:[]

            }
        },
        methods:{
            tab1(val){
                this.tab = val
            },
            tab2(val){
                this.tab = val
                this.getDepartment()
            },
            
            getCategory(){
                this.tableData = []
                this.department = null
                this.section = null
                this.team = null
                axios({
                    method:'get',
                    url:`${this.$baseUrl}api/getCategory`,
                }).then(r=>{
                    this.categoryItems = r.data
                })
            },
            getDepartment(){
                this.tableData = []
                this.section = null
                this.team = null
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/getDepartment`,
                    data:{
                        data : this.category,
                        list : this.departmentRadio
                    }
                }).then(r=>{
                    // console.log(r.data)
                    this.departmentItems = r.data
                })
            },
            getSection(){
                this.tableData = []
                this.team = null
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/getSection`,
                    data:{
                        data : this.department,
                        list : this.sectionRadio
                    }
                }).then(r=>{
                    this.sectionItems = r.data

                })
            },
            getTeam(){
                
                this.tableData = []
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/getTeam`,
                    data:{
                        data : this.section,
                        list : this.radioTO
                    }
                }).then(r=>{
                    this.teamItems = r.data
                    // if(this.category != 'CA01'){
                    //     this.getTable()
                    // }
                   
                })
            },
            handleFileSelect(event) {
                const file = event.target.files[0];
                const reader = new FileReader();

                reader.onload = (e) => {
                    const contents = e.target.result;

                    const parsedData = Papa.parse(contents, {
                    header: true,
                    encoding: 'UTF-8',
                    });

                    const rows = parsedData.data;

                    // Process each row
                    const headers = Object.keys(rows[0]);

                    for (let i = 0; i < rows.length; i++) {
                    const row = rows[i];
                    const rowData = {};

                    for (let j = 0; j < headers.length; j++) {
                        let value = row[headers[j]];

                        // Check if value exists before applying replace
                        // if (value) {
                        //     value = value.replace(/\r/g, '');
                        // }

                        rowData[headers[j]] = value;
                    }

                    this.tempFile.push(rowData);
                    }
                    console.log(this.tempFile,'data')
                };
        
                reader.readAsText(file);
            },
            forUpload(val){
                // let no = 0
                for (let i = 0; i < val.length - 1; i++) {
                    const item = val[i];
             
                    let obj = {
                        Department : this.department,
                        Section : this.section,
                        Team : this.category == 'CA01'?this.team:this.section,
                        Met : item.met,
                        // ProgramMet : item.program_met,
                        Required1 : item.quarter1?1:0,
                        Required2 : item.quarter2?1:0,
                        Required3 : item.quarter3?1:0,
                        Required4 : item.quarter4?1:0,
                        Quarter1 : item.quarter1,
                        Quarter2 : item.quarter2,
                        Quarter3 : item.quarter3,
                        Quarter4 : item.quarter4,
                        UpdatedDate : moment().format('YYYY-MM-DD HH:mm:ss'),
                        UpdatedBy : this.$store.state.user.name
                    }
                    this.uploadFile.push(obj)
                    
                }
                    console.log(this.uploadFile,'this.uploadFile')
                    // let no = 1
                // for (const item of val) {
                // //    /
                //         let obj = {
                //             Department : this.department,
                //             Section : this.section,
                //             Team : this.category == 'CA01'?this.team:this.section,
                //             Met : item.met,
                //             // ProgramMet : item.program_met,
                //             Required : (item.quarter1?1:0) + (item.quarter2?1:0) + (item.quarter3?1:0) + (item.quarter4?1:0),
                //             Quarter1 : item.quarter1,
                //             Quarter2 : item.quarter2,
                //             Quarter3 : item.quarter3,
                //             Quarter4 : item.quarter4,
                //             UpdatedDate : moment().format('YYYY-MM-DD HH:mm:ss'),
                //             UpdatedBy : this.$store.state.user.name
                //         }
                //         this.uploadFile.push(obj)
                // }
                // console.log(this.uploadFile,'this.uploadFile')
            },
            upload(){
                this.overlay = true
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/insertTrainingReq`,
                    data:{
                        data : this.uploadFile
                    }
                }).then(res=>{
                    if(res.data == 'success'){
                        console.log(res.data)
                        this.$refs.fileInput.value = '';
                        this.tempFile = []
                        this.getTable()
                        this.overlay = false
                    }
                   
                })
            },

            
            getTable(){
                this.overlay = true
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/trainingTableReq`,
                    data:{
                        Team : this.category == 'CA01'?this.team:this.section,
                        Year : this.selectedYear
                    }
                }).then(res=>{
                    this.tableData = res.data
                    this.overlay = false
                })
            },

            uploadActivity(){
                
                const data = []
                for (let i = 0; i < this.tempFile.length; i++) {
                    const item = this.tempFile[i];
                    let obj ={
                        code : `ACT${this.selectedYear}${i}`,
                        activity : item.activity,
                        year : this.selectedYear,
                        department : this.department,
                        section : this.section,
                        team : this.department == 'D01'?this.team:this.section,
                        name: this.user.name,
                        time: moment().format('YYYY-MM-DD HH:mm:ss')
                    }
                    data.push(obj)
                }
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/importActivity`,
                    data:{
                        data : data
                    }
                    }).then(res=>{
                        if(res.status == 200){
                            this.getMetTable()
                        }
                       
                    })
             
            },
            getMetTable(){
                this.overlay = true
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/getMetItems`,
                    data:{
                        year : this.selectedYear,
                        team : this.department == 'D01'?this.team:this.section
                    }
                }).then(res=>{
                    this.metTable = res.data
                    this.overlay = false
                })
            },
            changeTeam(){
                this.selectedYear = null
                this.metTable = []
            },
            deleteTable(table){
                const confirmed = window.confirm('Are you sure you want to delete this information?');
                if(confirmed){
                    this.overlay = true
                    let data = {
                        Table : table,
                        Team : this.department == 'D01'?this.team:this.section,
                        Name: this.user.name,
                        Time: moment().format('YYYY-MM-DD HH:mm:ss'),
                        Year : this.selectedYear,
                        url : `${this.$baseUrl}api/deleteRequired`
                    }
                    if(table == 'training_met'){
                        return DeleteRequired(data,this.getMetTable)
                    }else{
                        return DeleteRequired(data,this.getTable)
                    }
                   
                }
            }

        },
        computed:{
            year(){
                let data = []
                let start = 2023
                for (let i = start; i < 2040; i++) {
                    data.push(i)
                }
                return data
            },
            ...mapState([
                'user'
            ])

        },
        watch:{
            tempFile(val){
                if(val){
                    this.forUpload(val)
                }
            },
        },
        mounted(){
            this.getCategory()
        }
    }
</script>

<style scoped>
.file-input {
    /* Hide the default file input */
     /* Add your desired styles */
     /* For example: */
     padding: 10px;
     background-color: #f0f0f0;
     color: #333;
     border: 1px solid #ccc;
     border-radius: 4px;
     cursor: pointer;
   }
</style>