<script>
import axios from 'axios';
import moment from 'moment';
import Details from '../Details/RedConDetails.vue';
import AllPrint from './AllPrint.vue'
import StatusTimeline from '../Details/StatusTimeline.vue'
import SubmitDialog from '../Details/SubmitDialog.vue'
import Approved from '../Details/Approved'
import DeclinedDialog from '../Details/DeclinedDialog'
import DeclinedCard from '../Details/DeclinedCard'


import { mapState } from 'vuex';
    export default {
        components:{
            Details,
            AllPrint,
            StatusTimeline,
            SubmitDialog,
            DeclinedDialog,
            DeclinedCard
        },
        data() {
            return {
                selectedUnit:null,
                generateDate:null,
                department:null,
                section:null,
                team:null,
                generateMonthDialog: false,
                actualMP:[],
                tableData:[],
                psgTable:[],
                trainingTable:[],
                equipmentTable:[],
                maintenanceTable:[],
                facilityTable:[],
                all_tab:'',
                editing:false,
                editIndex: null,
                statusN3:0,
                declinedData:false,
                timelineData:{},

            }
        },

        methods: {
            personnelTab(tab){
                this.all_tab = tab
            },
            trainingTab(tab){
                
                this.all_tab = tab
            },
            equipmentTab(tab){
                this.all_tab = tab
            },
            maintenanceTab(tab){
                this.all_tab = tab
            },
            facilityTab(tab){
                this.all_tab = tab
            },
            overallTab(tab){
                this.all_tab = tab
            },
            async callGetTimeline() {
                if (this.$refs.StatusTimeline) {
                    await this.$refs.StatusTimeline.getTimeline();
                } else {
                    console.error("StatusTimeline ref not available yet.");
                }
            },
           
          async  generate(){
              this.$store.state.loadingPage = true
              this.$store.state.monthData = this.generateDate
                Promise.all([
                    this.getPsgTable(),
                    this.getTrainingData(),
                    this.getEquipment(),
