            // personnel
          async  getPsgTable(){
                if(this.selectedUnit == 'office'){
                    this.$store.state.monthData = this.generateDate
                    this.$store.state.sectionData = this.section
                    this.$store.state.teamData = this.team
                    this.$store.dispatch('getPoints')
                    axios({
                        method:'post',
                        url:`${this.$baseUrl}api/reportPsgTable`,
                        data:{
                            department : this.department,
                            section : this.section,
                            team : this.team,
                            Month : this.generateDate,
                            selected : this.selectedUnit
                        }
                    }).then(res=>{
                        if(this.selectedUnit){
                            this.psgTable = res.data.reduce((total,item)=>{
                                let index = total.findIndex(res=> res.DivisionCode == item.DivisionCode)
                                if(index == -1){
                                    let obj = {...item}
                                    obj.count = 1
                                    obj.GroupDivision = item.DivisionName
                                    obj.TotalRequired = item.countRequired
                                    obj.TotalActual = item.countActual
                                    total.push(obj)
                                }else{
                                
                                    total[index].TotalRequired += item.countRequired
                                    total[index].TotalActual += item.countActual
                                    total[index].count += 1
                                    item.totalCountRequired = total[index].TotalRequired
                                    item.totalCountActual = total[index].TotalActual
                                    total.push(item)
                                }
                                return total
                            },[])
                            console.log(this.psgTable,'ceo')
                        }
                    })
                }else{
                    axios({
                        method:'post',
                        url:`${this.$baseUrl}api/personnelAll`,
                        data:{
                            Month : this.generateDate,
                            selected : this.selectedUnit,
                            department : this.department
                        }
                    }).then(res=>{
                        this.psgTable = res.data.reduce((total,item)=>{
                            let i
                            if(this.selectedUnit == 'unit'){
                                 i = total.findIndex(res=> res.UnitSectionCode == item.UnitSectionCode)
                            }else if(this.selectedUnit == 'all'){
                                 i = total.findIndex(res=> res.UnitDepartmentCode == item.UnitDepartmentCode)
                            }else{
                                i = total.findIndex(res=> res.UnitTeamCode == item.UnitTeamCode)
                            }
                            if(i == -1){
                                item.actualMP = item.actualMP?item.actualMP:0
                                item.RequiredMP = item.RequiredMP?item.RequiredMP:0
                                item.GradePoint = item.GradePoint?item.GradePoint:0
                                item.AfposPoint = item.AfposPoint?item.AfposPoint:0
                                // if(['unit','hpn'].includes(this.selectedUnit)){
                                item.psgr = item.actualMP ? (item.actualMP / item.RequiredMP) * 100 : 0;
                                // } else {
                                //     item.psgr = item.actualMP ? (item.actualMP / item.RequiredMP) * 100 / item.length : 0;
                                // }
                                item.psgr50 = item.actualMP?item.psgr * 0.5:0
                                // if (['unit','hpn'].includes(this.selectedUnit)) {
                                item.prr = item.actualMP?(item.GradePoint / item.actualMP) * 100:0
                                // } else {
                                    // item.prr = item.actualMP?(item.GradePoint / item.actualMP) * 100  / item.length:0
                                // }
                               
                                item.prr40 = item.actualMP?item.prr * 0.4:0
                                // if (['unit','hpn'].includes(this.selectedUnit)) {
                                item.psr = item.actualMP?(item.AfposPoint / item.actualMP) * 100:0
                                // } else {
                                //     item.psr = item.actualMP?(item.AfposPoint / item.actualMP) * 100 / item.length:0
                                // }
                                
                                item.psr60 = item.actualMP?item.psr * 0.6:0
                                item.pqr = item.actualMP?item.prr40 + item.psr60:0
                                item.pqr50 = item.actualMP?item.pqr * 0.5:0
                                item.readiness = item.actualMP?item.psgr50 + item.pqr50:0
                                total.push(item)
                            }
                            return total
                        },[])
                        // console.log(this.psgTable,'ceo')
                    })
                }
                
            },
            // training
            async  getTrainingData(){

                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/trainingReportData`,
                    data:{
                        department : this.department,
                        Section : this.section,
                        Team : this.team,
                        Month : this.generateDate,
                        selected : this.selectedUnit
                    }
                }).then(res=>{
                    if(this.selectedUnit == 'office'){
                        // console.log(res.data,'trainig')
                        // this.trainingTable = res.data
                        this.trainingTable = res.data.reduce((total,item)=>{
                            let i = total.findIndex(r=> r.id == item.id)
                            if(i == -1){
                                    if(this.quarterDate == 1){
                                        item.Required = item.Required1
                                        item.Actual = item.Actual1
                                    }else if(this.quarterDate == 2){
                                        item.Required = item.Required1 + item.Required2
                                        item.Actual = item.Actual1 + item.Actual2

                                    }else if(this.quarterDate == 3){
                                        item.Required = item.Required1 + item.Required2 + item.Required3
                                        item.Actual = item.Actual1 + item.Actual2 + item.Actual3
                                    }else if(this.quarterDate == 4){
                                        item.Required = item.Required1 + item.Required2 + item.Required3 + item.Required4
                                        item.Actual = item.Actual1 + item.Actual2 + item.Actual3 + item.Actual4
                                    }
                                    item.colPercentage = item.Actual / item.Required * 100
                        
                                total.push(item)
                            }
                            return total
                        },[])
                        console.log(this.trainingTable,'trainig')
                    }else{
                        // console.log(res.data,'trainig')
                        this.trainingTable = res.data.reduce((total,item)=>{
                            let i
                            if(this.selectedUnit == 'unit'){
                                 i = total.findIndex(res=> res.UnitSectionCode == item.UnitSectionCode)
                            }else if(this.selectedUnit == 'all'){
                                 i = total.findIndex(res=> res.UnitDepartmentCode == item.UnitDepartmentCode)
                            }else{
                                i = total.findIndex(res=> res.UnitTeamCode == item.UnitTeamCode)
                            }
                            let metVal = (Number(item.Actual) / item.Required)
                           
                            if(i == -1){
                                item.orac = item.ActualMp && item.totalAssigned?(Number(item.ActualMp) / item.totalAssigned) * 100:0
                               //*******old computation ****/
                                // if(this.selectedUnit == 'unit' || this.selectedUnit == 'hpn'){
                                    // item.orac40 = item.orac?item.orac * 0.4:0
                                // }else{
                                //     item.orac40 = item.orac?item.orac * 0.4 / item.length:0
                                // }
                                
                                // if(this.selectedUnit != 'all'){
                                    // item.Mett = !isNaN(item.Actual) && metVal?metVal * 100:0
                                // }else{
                                //     item.Mett = !isNaN(item.Actual) && metVal?metVal * 100 / item.length:0
                                // }

                                item.orac40 = item.orac?item.orac * 0.4:0
                                item.ActualMet = item.Actual
                                item.RequiredMet = item.Required
                                item.Mett = !isNaN(item.Actual) && metVal?metVal * 100:0
                                item.Mett60 = item.Mett?item.Mett * 0.6 :0
                                item.trr = Number(item.orac40) + Number(item.Mett60)

                                total.push(item)
                            }else{
                                total[i].ActualMet = item.Actual
                                total[i].RequiredMet = item.Required
                                // if(this.selectedUnit == 'unit' || this.selectedUnit == 'hpn'){
                                    // total[i].Mett = !isNaN(item.Actual) && metVal?metVal * 100:0
                                // }else{
                                //     total[i].Mett = !isNaN(item.Actual) && metVal?metVal * 100 / item.length:0
                                // }
                                total[i].Mett = !isNaN(item.Actual) && metVal?metVal * 100:0
                                total[i].Mett60 = total[i].Mett?total[i].Mett * 0.6:0
                                // console.log(total[i].orac40,'total[i].orac40',total[i].Mett60,'total[i].Mett60')
                                total[i].trr = Number(total[i].orac40) + Number(total[i].Mett60)
                            }
                            return total
                        },[])

                        // console.log(this.trainingTable)
                    }
                    
                    this.getActualMp()
                    // console.log(this.trainingTable,'trainingTable')
                })
            },

