        computed:{
            ...mapState([
                'user',
                'gradePoints',
                'afposPoints',
                'loadingPage',
                'remarksData'
            ]),
            
             // function to show month name
            formattedDate() {
                    if (this.generateDate) {
                        const date = new Date(this.generateDate);
                        const month = date.toLocaleString('default', { month: 'long' });
                        const year = date.getFullYear();
                        return `${month} ${year}`;
                    }
                    return null;
            },
            unitItems(){
                const user = this.$store.state.user;
                let data
                if(user.rights == '1' || ['1', '3', '4','8'].includes(user.res) && user.main == 'D01'){
                    if(['T38','T14',].includes(user.office)){
                        data = [
                            {text:'My Office', value:'office'},
                            {text:'Unit', value:'unit'},
                            {text:'HPN', value:'hpn'},
                            {text:'All', value:'all'},
                        ]
                    }else{
                        data = [
                            {text:'My Office', value:'office'},
                            {text:'Unit', value:'unit'},
                            {text:'All', value:'all'},
                        ] 
                    }
                    
                }else{
                    data = [
                    {text:'Unit', value:'unit'},
                    {text:'Sub Unit', value:'office'},
                    ]
                }
                
                return data
            },
            reasonDetails(){
                let data = {
                    table : 'n3',
                    month : this.generateDate,
                    unit : this.user.office
                }
                return data
            },

            ///////////personnel/////////////
             // function to get total TOE for psg tab
             personnelOverall(){
                let obj = {
                    RequiredMP : 0,
                    actualMP : 0,
                    psgr : 0,
                    psgr50 : 0,
                    GradePoint : 0,
                    AfposPoint : 0,
                    prr : 0,
                    prr40 : 0,
                    psr : 0,
                    psr60:0,
                    pqr:0,
                    pqr50:0,
                    all : 0
                }
                for (const item of this.psgTable) {
                    obj.RequiredMP += item.RequiredMP
                    obj.actualMP += item.actualMP
                    obj.psgr = ( obj.actualMP / obj.RequiredMP) * 100
                    obj.psgr50 = obj.psgr * 0.5
                    obj.GradePoint += item.GradePoint
                    obj.AfposPoint += item.AfposPoint
                    obj.prr = ( obj.GradePoint / obj.actualMP) * 100
                    obj.prr40 = obj.prr * 0.4
                    obj.psr = ( obj.AfposPoint / obj.actualMP) * 100
                    obj.psr60 = obj.psr * 0.6
                    obj.pqr = obj.prr40 + obj.psr60
                    obj.pqr50 = obj.pqr * 0.5
                    obj.all = obj.psgr50 + obj.pqr50
                }
                return obj
             },
            totalToe(){
                if(this.psgTable.length > 0){
                    return this.psgTable.reduce((total, item) => {
                        if (item.TotalRequired) {
                            return total + item.TotalRequired;
                        }
                        return total;
                        }, 0);
                }else{
                    return 0
                }
            },
            // function to get total Actual for psg tab
            totalActual(){
                if(this.psgTable.length > 0){
                    return this.psgTable.reduce((total, item) => {
                        if (item.TotalActual) {
                            return total + item.TotalActual;
                        }
                        return total;
                        }, 0);
                }else{
                    return 0
                }
            },
            // function to get total psg for psg tab
            psgRating(){
                if(this.psgTable.length > 0){
                let result
                    result = (this.totalActual / this.totalToe) * 100
                    // console.log(result,'psgRating')
                return result
                }else{
                    return 0
                }
            },
            gradeRating(){
                let result
                result = (this.gradePoints / this.totalActual) * 100
                // console.log(result,'gradeRating')
                return result
            },
            gradeRating04(){
                let result
                result = this.gradeRating * 0.4
                return result
            },
            afposRating(){
                let result
                result = (this.afposPoints / this.totalActual) * 100
                // console.log(result,'afposRating')
                return result
            },
            afposRating06(){
                let result
                result = this.afposRating * 0.6
                return result
            },


            psgRating05(){
                let data
                    data = this.psgRating?this.psgRating * 0.5:0
                return data
            },
            pqrRating05(){
                let data
                    data = this.pqrRatingTotal * 0.5
                return data
            },
            pqrRatingTotal(){
                let data
                    data = (this.gradeRating * 0.4)+(this.afposRating * 0.6)
                return data
            },
            totalRating(){
                let data
                    data = Number(this.psgRating05) + Number(this.pqrRating05)
                return data == Infinity?0:data;
            },
////////////////////training computed//////////////////
        quarterDate(){
            if(this.generateDate){
                let month = parseInt(this.generateDate.substr(5, 2), 10);
                let data = null;

                if (month >= 1 && month <= 3) {
                    data = '1';
                } else if (month >= 4 && month <= 6) {
                    data = '2';
                } else if (month >= 7 && month <= 9) {
                    data = '3';
                } else if (month >= 10 && month <= 12) {
                    data = '4';
                }
                return data
            }else{
                return false
            }
          
        },
            trainingOverall(){
                let obj = {
                        assigned_all : 0,
                        actaulMp_all : 0,
                        orac_all : 0,
                        orac40_all : 0,
                        required_all : 0,
                        actualMet_all : 0,
                        mett_all : 0,
                        mett60_all : 0,
                        all : 0
                }
                for (const item of this.trainingTable) {
                    
                    // let metVal = (Number(item.Actual) / item.Required)
                    obj.assigned_all += item.totalAssigned
                    obj.actaulMp_all += Number(item.ActualMp)
                    obj.orac_all = (obj.actaulMp_all / obj.assigned_all) * 100
                    obj.orac40_all = obj.orac_all?obj.orac_all * 0.4:0
                    // obj.orac40_all = obj.orac40_all / this.trainingTable.length
                    obj.required_all += Number(item.RequiredMet)
                    obj.actualMet_all += Number(item.ActualMet)
                    obj.mett_all = (obj.actualMet_all /obj.required_all) * 100
                    // obj.mett_all = obj.mett_all / this.trainingTable.length
           
                    obj.mett60_all = obj.mett_all?obj.mett_all * 0.6:0
                    obj.all = obj.mett60_all + obj.orac40_all 
                }
         
                return obj
             },
            //total actual MP base on month
            totalActualMp(){
                if(this.trainingTable.length > 0){
                    return this.actualMP.reduce((total, item) => {
                        if (item.ActualMp && item.Month == this.generateDate) {
                            return total + Number(item.ActualMp);
                        }
                        return total;
                        }, 0);
                }else{
                    return 0
                }
               
              
            },
            totalRequired(){
                if(this.trainingTable.length > 0){
                    return this.trainingTable.reduce((total, item) => {
                        if(item.Month <= this.generateDate && item.Required){
                        return total + Number(item.Required);
                    }
                    return total;
                    }, 0);
                }else{
                    return 0
                }
            },
        
        
            totalOrac(){
                if(this.trainingTable.length > 0){
                    return this.trainingTable.reduce((total, item) => {
                    if(item.Month <= this.generateDate && item.Actual){
                        return total + Number(item.Actual);
                    }
                    return total;
                    }, 0);
                }else{
                    return 0
                }
               
            },
            // function to get total psg for psg tab
            metPercentage(){
                if(this.trainingTable.length > 0){
                let result
                    result = (this.totalOrac / this.totalRequired * 100)
                return result
                }else{
                    return 0
                }
            },

            // function to get total Actual for psg tab
            totalAssigned(){
                if(this.psgTable.length > 0){
                    return this.psgTable.reduce((total, item) => {
                        if (item.TotalActual) {
                            return total + item.TotalActual;
                        }
                        return total;
                        }, 0);
                }else{
                    return 0
                }
            },
            
            oracPercentage(){
                if(this.trainingTable.length > 0){
                let result
                    result = (this.totalActualMp / this.totalAssigned * 100)
                return result
                }else{
                    return 0
                }
            },
            trRating(){
                // console.log(this.totalOrac,'this.totalOrac')
                // console.log(this.totalRequired,'this.totalRequired')
                // console.log(this.totalAssigned,'this.totalAssigned')
                if(this.trainingTable.length > 0){
                let result
                let met = ((this.totalOrac / this.totalRequired) * 100) * (0.60)
                let orac = ((this.totalActualMp / this.totalAssigned) * 100) *( 0.40)
                    result = ((Number(orac) + Number(met)))
                    return result == Infinity?0:result;
                }else{
                    return 0
                }
            },


        ///////////equipment /////////////////////
        equipmentOverall(){
            // const table = this.all_tab == 'equipmentTab'?this.equipmentTable:this.all_tab == 'maintenanceTab'?
            //             this.maintenanceTable:this.equipmentTable
            let obj = {
                    // reqGround_all : 0,
                    // reqAir_all : 0,
                    // reqSea_all : 0,
                    // reqIndi_all : 0,
                    // reqCrew_all : 0,
                    // reqComms_all : 0,
                    // reqMed_all : 0,
                    // reqDen_all : 0,
                    // reqQuart_all : 0,
                    // actGround_all : 0,
                    // actAir_all : 0,
                    // actSea_all : 0,
                    // actIndi_all : 0,
                    // actCrew_all : 0,
                    // actComms_all : 0,
                    // actMed_all : 0,
                    // actDen_all : 0,
                    // actQuart_all : 0,
                    // avePri_all : 0,
                    // aveSec_all : 0,
                    // totalPri_all : 0,
                    // totalSec_all : 0,
                    // count : 0,
                    // all : 0,
                    count: 0,
                    totalReadiness: 0,
                    all: 0,
                }
                for (const item of this.equipmentTable) {
                    // obj.reqGround_all += item.ReqGround
                    // obj.reqAir_all += item.ReqAir
                    // obj.reqSea_all += item.ReqSea
                    // obj.reqIndi_all += item.ReqIndi
                    // obj.reqCrew_all += item.ReqCrew
                    // obj.reqComms_all += item.ReqComms
                    // obj.reqMed_all += item.ReqMed
                    // obj.reqDen_all += item.ReqDen
                    // obj.reqQuart_all += item.ReqQuart
                    // obj.actGround_all += item.ActGround
                    // obj.actAir_all += item.ActAir
                    // obj.actSea_all += item.ActSea
                    // obj.actIndi_all += item.ActIndi
                    // obj.actCrew_all += item.ActCrew
                    // obj.actComms_all += item.ActComms
                    // obj.actMed_all += item.ActMed
                    // obj.actDen_all += item.ActDen
                    // obj.actQuart_all += item.ActQuart
                    // obj.avePri_all += item.avePrimary?item.avePrimary / this.equipmentTable.length:0
                    // obj.aveSec_all += item.AveSecondary?item.AveSecondary / this.equipmentTable.length:0
                    // obj.totalPri_all += item.TotalPrimary / this.equipmentTable.length 
                    // obj.totalSec_all += item.TotalSecondary / this.equipmentTable.length 
                    // obj.count +=  item.Readiness?1:0
                    // obj.all += item.Readiness / obj.count
                    if (item.Readiness) {
                        obj.count++; // Increment count for items with Readiness being truthy
                        obj.totalReadiness += item.Readiness; // Accumulate total Readiness
                    }
                    
                   
                }
                if (obj.count > 0) {
                    obj.all = obj.totalReadiness / obj.count;
                }
                return obj
        },
        totalTable(){
                const key = ['MOBILITY','Ground','Sea','Air','FIREPOWER','Individual','Crew Served Weapon','COMMUNICATIONS','MEDICAL','DENTAL','QUARTERMASTER'];

                return key.map(val => {
                const matchedObject = this.equipmentTable.find(current => current.Sub === val || current.Category === val);
                
                if (matchedObject) {
                    const category = matchedObject.Category || val;
                    const sub = matchedObject.Sub || val;

                    const totalRequired = this.equipmentTable.reduce((acc, current) => {
                    return acc + (current.Sub === val || current.Category === val ? Number(current.Required) : 0);
                    }, 0);

                    const totalActual = this.equipmentTable.reduce((acc, current) => {
                    return acc + (current.Sub === val || current.Category === val ? Number(current.Actual) : 0);
                    }, 0);

                    const percent = totalActual > totalRequired ?100: totalRequired !== 0 ? (totalActual / totalRequired) * 100 : 0;

                    return {
                        Category: category,
                        Sub: sub,
                        totalRequired: totalRequired,
                        totalActual: totalActual,
                        percent: percent,
                        title : val == 'MOBILITY' ||  val == 'FIREPOWER'?1:0,
                        info : category == 'MOBILITY' ||  category == 'FIREPOWER' ||  category == 'COMMUNICATIONS'?'primary':'secondary',
                        break : val == 'COMMUNICATIONS' || val == 'QUARTERMASTER'?1:0
                    };
                } else {
                    return {
                        Category: val === 'Ground' || val === 'Sea' || val === 'Air' || val === 'Individual' ? 'MOBILITY' : val === 'Crew Served Weapon' ? 'FIREPOWER' : val,
                        Sub: val,
                        totalRequired: 0,
                        totalActual: 0,
                        percent: 0,
                        title : val == 'MOBILITY' ||  val == 'FIREPOWER'?1:0,
                        info : val === 'Ground' || val === 'Sea' || val === 'Air' || val === 'Individual' || val === 'Crew Served Weapon' ||  val == 'COMMUNICATIONS'?'primary':'secondary',
                        break : val == 'COMMUNICATIONS' || val == 'QUARTERMASTER'?1:0
                    };
                }
                });
            },

            totalPrimaryRequired() {

                return this.equipmentTable.reduce((acc, current) => {
                    return acc + (current.Info === 'primary'? Number(current.Required) : 0);
                    }, 0);

                },
            totalSecondaryRequired() {
                return this.equipmentTable.reduce((acc, current) => {
                    return acc + (current.Info === 'secondary'? Number(current.Required) : 0);
                    }, 0);
            },

            totalPrimaryActual() {

                return this.equipmentTable.reduce((acc, current) => {
                    return acc + (current.Info === 'primary'? Number(current.Actual) : 0);
                    }, 0);

            },
            totalSecondaryActual() {
                return this.equipmentTable.reduce((acc, current) => {
                    return acc + (current.Info === 'secondary'? Number(current.Actual) : 0);
                    }, 0);
            },

            averagePrimary() {
                let totalPercentage = 0;
                let itemCount = 0;
                for (const item of this.totalTable) {
                    if (item.percent && item.info == 'primary' && item.Sub != 'MOBILITY'  && item.Sub != 'FIREPOWER') {
                        let percentage = item.percent;
                        if(percentage){
                            // console.log(percentage)
                            totalPercentage += percentage
                            itemCount++;
                        }
                    }
                }
                if (itemCount === 0) {
                    return 0; // To avoid division by zero
                }
                return this.roundUpValue((totalPercentage / itemCount)); // Calculate average and round to 2 decimal places
            },
            averageSecondary() {
                let totalPercentage = 0;
                let itemCount = 0;
                for (const item of this.totalTable) {
                    if (item.percent && item.info == 'secondary') {
                        let percentage = item.percent;
                        if(percentage){
                            // console.log(percentage)
                            totalPercentage += percentage
                            itemCount++;
                        }
                    }
                }
                if (itemCount === 0) {
                    return 0; // To avoid division by zero
                }
                return this.roundUpValue((totalPercentage / itemCount)); // Calculate average and round to 2 decimal places
            },
            mee(){
                return this.averagePrimary * 0.8
            },
            see(){
                return this.averageSecondary * 0.2
            },
            readinessRating(){
                return this.mee + this.see
            },



            // ///////maintenance/////////////
            maintenanceOverall(){
                let obj = {
                    count: 0,
                    totalReadiness: 0,
                    all: 0,
                }

                for (const item of this.maintenanceTable) {
                    if (item.Readiness) {
                        obj.count++; // Increment count for items with Readiness being truthy
                        obj.totalReadiness += item.Readiness; // Accumulate total Readiness
                    }
                    
                }
                if (obj.count > 0) {
                    obj.all = obj.totalReadiness / obj.count;
                }
                return obj
            },
            totalTableM(){
                const key = ['MOBILITY','Ground','Sea','Air','FIREPOWER','Individual','Crew Served Weapon','COMMUNICATIONS','MEDICAL','DENTAL','QUARTERMASTER'];

                return key.map(val => {
                const matchedObject = this.maintenanceTable.find(current => current.Sub === val || current.Category === val);
                
                if (matchedObject) {
                    const category = matchedObject.Category || val;
                    const sub = matchedObject.Sub || val;

                    const totalRequired = this.maintenanceTable.reduce((acc, current) => {
                    return acc + (current.Sub === val || current.Category === val ? Number(current.Actual) : 0);
                    }, 0);

                    const totalActual = this.maintenanceTable.reduce((acc, current) => {
                    return acc + (current.Sub === val || current.Category === val ? Number(current.ActualM) : 0);
                    }, 0);

                    const percent = totalRequired !== 0 ? (totalActual / totalRequired) * 100 : 0;

                    return {
                        Category: category,
                        Sub: sub,
                        totalRequired: totalRequired,
                        totalActual: totalActual,
                        percent: percent,
                        title : val == 'MOBILITY' ||  val == 'FIREPOWER'?1:0,
                        info : category == 'MOBILITY' ||  category == 'FIREPOWER' ||  category == 'COMMUNICATIONS'?'primary':'secondary',
                        break : val == 'COMMUNICATIONS' || val == 'QUARTERMASTER'?1:0
                    };
                } else {
                    return {
                        Category: val === 'Ground' || val === 'Sea' || val === 'Air' || val === 'Individual' ? 'MOBILITY' : val === 'Crew Served Weapon' ? 'FIREPOWER' : val,
                        Sub: val,
                        totalRequired: 0,
                        totalActual: 0,
                        percent: 0,
                        title : val == 'MOBILITY' ||  val == 'FIREPOWER'?1:0,
                        info : val === 'Ground' || val === 'Sea' || val === 'Air' || val === 'Individual' || val === 'Crew Served Weapon' ||  val == 'COMMUNICATIONS'?'primary':'secondary',
                        break : val == 'COMMUNICATIONS' || val == 'QUARTERMASTER'?1:0
                    };
                }
                });
            },

            totalPrimaryRequiredM() {

                return this.maintenanceTable.reduce((acc, current) => {
                    return acc + (current.Info === 'primary'? Number(current.Actual) : 0);
                    }, 0);

                },
            totalSecondaryRequiredM() {
                return this.maintenanceTable.reduce((acc, current) => {
                    return acc + (current.Info === 'secondary'? Number(current.Actual) : 0);
                    }, 0);
            },

            totalPrimaryActualM() {

                return this.maintenanceTable.reduce((acc, current) => {
                    return acc + (current.Info === 'primary'? Number(current.ActualM) : 0);
                    }, 0);

            },
            totalSecondaryActualM() {
                return this.maintenanceTable.reduce((acc, current) => {
                    return acc + (current.Info === 'secondary'? Number(current.ActualM) : 0);
                    }, 0);
            },

            averagePrimaryM() {
                let totalPercentage = 0;
                let itemCount = 0;
                for (const item of this.totalTableM) {
                
                    if (item.percent && item.info == 'primary' && item.Sub != 'MOBILITY'  && item.Sub != 'FIREPOWER') {
                        // console.log(item,'item')
                        let percentage = item.percent;
                        if(percentage){
                        
                            // console.log(percentage)
                            totalPercentage += percentage
                            itemCount++;
                            
                        }
                    }
                }
                if (itemCount === 0) {
                    return 0; // To avoid division by zero
                }
                return this.roundUpValue((totalPercentage / itemCount)); // Calculate average and round to 2 decimal places
            },
            averageSecondaryM() {
                let totalPercentage = 0;
                let itemCount = 0;
                for (const item of this.totalTableM) {
                    if (item.percent && item.info == 'secondary') {
                        let percentage = item.percent;
                        if(percentage){
                            // console.log(percentage)
                            totalPercentage += percentage
                            itemCount++;
                        }
                    }
                }
                if (itemCount === 0) {
                    return 0; // To avoid division by zero
                }
                return this.roundUpValue((totalPercentage / itemCount)); // Calculate average and round to 2 decimal places
            },
            meeM(){
                return this.averagePrimaryM * 0.8
            },
            seeM(){
                return this.averageSecondaryM * 0.2
            },
            readinessRatingM(){
                return this.meeM + this.seeM
            },
            //////facility readiness
            facilityOverlall(){
                let obj = {
                    // quantitative_all : 0,
                    // qualitative_all : 0,
                    // all : 0,
                    count: 0,
                    totalReadiness: 0,
                    all: 0,
                }

                for (const item of this.facilityTable) {
                    //  obj.quantitative_all  += item.Quantitative?item.Quantitative / this.facilityTable.length:0
                    //  obj.qualitative_all  += item.Qualitative?item.Qualitative / this.facilityTable.length:0
                    //  obj.all  += item.Readiness?item.Readiness / this.facilityTable.length:0
                    if (item.Readiness) {
                        obj.count++; // Increment count for items with Readiness being truthy
                        obj.totalReadiness += item.Readiness; // Accumulate total Readiness
                    }
                    
                }
                if (obj.count > 0) {
                    obj.all = obj.totalReadiness / obj.count;
                }
                return obj
            },
            facilityData(){
                return this.facilityTable.reduce((total,item)=>{
                    let i = total.findIndex(res=> res.Info == item.Info)

                    if(i == -1){
                        let obj = {...item}
                        obj.count = 1
                        obj.countQuanRating = 0
                        obj.countQualRating = 0
                        obj.quanRating = item.Rating
                        obj.qualRating = item.overall
                        obj.quanPercent = 0
                        obj.qualPercent = 0
                        obj.facilityRating = 0
                        total.push(obj)
                    }else{
                        total[i].count += 1
                        total[i].quanRating += item.Rating
                        total[i].qualRating += item.overall
                        if(item.Rating){
                            total[i].countQuanRating += 1
                        }
                        if(item.overall){
                            total[i].countQualRating += 1
                        }
                        total[i].quanPercent  = (total[i].quanRating / total[i].countQuanRating) * 0.5;
                        total[i].qualPercent  = (total[i].qualRating / total[i].countQuanRating) * 0.5;
                        item.ratingQuanPercent = (total[i].quanRating / total[i].countQuanRating) * 0.5;
                        item.ratingQualPercent = (total[i].qualRating / total[i].countQualRating) * 0.5;
                        total[i].facilityRating = item.ratingQuanPercent +  item.ratingQualPercent
                        // }
                    
                    
                    

                        total.push(item)
                    }
                    return total
                },[])

            },
            facilityReport(){
           
           return this.facilityData.reduce((total,item)=>{
         
            let i = total.findIndex(res=> res.Info == item.Info)
            if(i == -1){
                let obj = {...item}
                obj.Required = item.Required
                obj.Actual = item.Actual
                obj.Quantitative = 0
                obj.Qualitative = 0
                obj.total = 0
                obj.weight = 0
                total.push(obj)
            }else{
                total[i].Required += item.Required
                total[i].Actual += item.Actual
                total[i].Quantitative = item.ratingQuanPercent * 2
                total[i].Qualitative = item.ratingQualPercent * 2
                total[i].total = (total[i].Quantitative + total[i].Qualitative ) / 2
                total[i].weight =  total[i].total?total[i].total / 2:0
            }
            return total
            },[])

        },
        readinessFacility(){
            let val = 0
            for (const item of this.facilityReport) {
                if(item.weight){
                    val += item.weight
                }
            }
            return val
        },
            // over all readiness
            overAll(){
                let obj = {
                    personnel : this.personnelOverall.all?this.personnelOverall.all:0,
                    training : this.trainingOverall.all?this.trainingOverall.all:0,
                    equipment : this.equipmentOverall.all?this.equipmentOverall.all:0,
                    maintenance : this.maintenanceOverall.all?this.maintenanceOverall.all:0,
                    facility : this.facilityOverlall.all?this.facilityOverlall.all:0,
                    all: 0
                }
                obj.all = (obj.personnel + obj.training + obj.equipment + obj.maintenance + obj.facility) / 5
                return obj
  

            },
            TotalReadiness(){
                let val = []
                val.push(this.totalRating,this.trRating,this.readinessRating,this.readinessRatingM,this.readinessFacility)
                let sum = val.reduce((total,item)=>{
                    return total + item;
                },0)
                let result = sum /val.length
               
                return result != 'Infinity'?result:0;
            }
        },
        watch: {
            selectedUnit(val){
                if(val == 'unit'){
                    this.section = this.$store.state.user.main
                    this.team = null
                }
                else if(val == 'office'){
                    this.section = null
                    this.team = this.$store.state.user.main == 'D01'?this.$store.state.user.office:this.$store.state.user.unit
                }
                else{
                    this.section = null
                    this.team = null
                    if(this.user.office == 'T12'){
                        this.all_tab = 'personnelTab'
                    }else if(this.user.office == 'T15'){
                        this.all_tab = 'equipmentTab'
                    }else if(this.user.office == 'T19'){
                        this.all_tab = 'trainingTab'
                    }
                }
            },
            declinedData(val){
            if(val){
                this.statusn3Logs()
            }
        }
           
    
        },
        mounted() {
           this.selectedUnit = 'office'
           this.department = this.$store.state.user.main
        },
    }
</script>

