<template>
    <div>
        <h1><center>Training Readiness</center></h1>
        <h2><center>{{ formattedDate }}</center></h2>
        <v-divider/>
        <v-divider/>
        <v-col cols="12" md="12" lg="12" xl="12">
                <v-tabs v-model="training_tab" justify-center>
                    <v-tab href="#orac" @click="orac('orac')" > OPERATIONALLY READY & AVAILABLE CREW</v-tab>
                    <v-tab href="#mett" @click="mett('mett')" v-if="tableData.length > 0">MISSION ESSENNTIAL TASK TRAINING</v-tab>
                    <v-tab href="#trr" @click="trr('trr')" v-if="tableData.length > 0">TRAINING READINESS RAITING</v-tab>
                </v-tabs>
        </v-col>
        <v-tabs-items v-model="training_tab">
            <v-tab-item :value="'orac'">
                <v-row>
                    <v-col cols="12" md="3" lg="3" xl="3">
                        <v-card elevation="3" class="justify-center">
                            <v-card-title class="d-flex justify-center" style="background-color: #0275d8; color: white;">
                                CONTROL
                            </v-card-title>
                            <v-container>
                                <!-- <div> -->
                                    <br>
                                   
                                    <v-row>
                                        <v-col cols="12"  v-if="user.rights == '1' || ['8','3'].includes(user.res) && user.main == 'D01'?true:false">
                                            <v-autocomplete 
                                                label="Unit"
                                                dense
                                                outlined
                                                style="margin-top:-30px;"
                                                v-model="department"
                                                :items="departmentItems"
                                                item-text="UnitDepartmentName"
                                                item-value="UnitDepartmentCode"
                                               
                                            />
                                        </v-col>
                                        <v-col cols="12" md="12" lg="12" xl="12" v-if="user.rights == '1' || ['8','3'].includes(user.res)?true:false">
                                            <v-autocomplete 
                                                label="Sub-Unit"
                                                dense
                                                outlined
                                                style="margin-bottom:-40px;margin-top:-30px;"
                                                v-model="team"
                                                :items="teamItems"
                                                item-text="UnitTeamName"
                                                item-value="UnitTeamCode"
                                            />
                                        </v-col>
                                        <v-col cols="12" md="12" lg="12" xl="12">
                                            <v-dialog ref="dialog" v-model="generateMonthDialog" :return-value.sync="generateDate" persistent width="290px">
                                                <template v-slot:activator="{ on, attrs }">
                                                    <v-text-field v-model="formattedDate" label="Select Month" prepend-inner-icon="mdi-calendar" readonly v-bind="attrs" v-on="on" dense outlined class="text-center" ></v-text-field>
                                                </template>
                                                <v-date-picker v-model="generateDate" type="month" scrollable>
                                                    <v-spacer></v-spacer>
                                                    <v-btn text color="primary" @click="generateMonthDialog = false">Cancel</v-btn>
                                                    <v-btn text color="primary" @click="[$refs.dialog.save(generateDate),tableData = []]">OK</v-btn>
                                                </v-date-picker>
                                            </v-dialog>
                                        </v-col>
                            
                                        <v-col cols="12" md="12" lg="12" xl="12">
                                            <v-btn color="warning" medium block @click="generate()"  class="mb-2" :disabled=" !generateDate || generateDate > monthnow ?true:false">GENERATE</v-btn>
                                            <v-btn color="primary" medium block class="mb-2" :disabled="tableData.length == 0 || !approved.Status || !approved.Status && ['1','2'].includes(user.approver)?true:false" @click="print()">PRINT</v-btn>
                                            <v-btn v-show="this.user.approver == 2" color="error" medium block class="mb-2" :disabled="tableData.length == 0 || approved.Status != 1 && user.approver == 2 ?true:false" @click="declined(generateDate,team,user.approver)">DECLINED</v-btn>
                                            <v-btn v-show="this.user.approver == 3" color="error" medium block class="mb-2" :disabled="tableData.length == 0 || approved.Status != 2 && user.approver == 3 ?true:false" @click="declined(generateDate,team,user.approver)">DECLINED</v-btn>
                                            <v-btn v-show="this.user.approver == 4" color="error" medium block class="mb-2" :disabled="tableData.length == 0 || approved.Status != 3 && user.approver == 4 ?true:false" @click="declined(generateDate,team,user.approver)">DECLINED</v-btn>
                                            <v-btn v-show="this.user.approver == 5" color="error" medium block class="mb-2" :disabled="tableData.length == 0 || approved.Status != 4 && user.approver == 5 && disableApproved?true:false" @click="declined(generateDate,team,user.approver)">DECLINED</v-btn>
                                            <v-btn v-show="this.user.approver == 1" color="success" medium block @click="submitData(generateDate,team)" :disabled="tableData.length == 0 || !disableSubmit?true:false">SUBMIT</v-btn>
                                            <v-btn v-show="this.user.approver == 2" color="success" medium block @click="approvedData(generateDate,team,'2')" :disabled="tableData.length == 0  || approved.Status != 1 ?true:false">CONCURRENCE</v-btn> <!-- OIC-->
                                            <v-btn v-show="this.user.approver == 3" color="success" medium block @click="approvedData(generateDate,team,'3')" :disabled="tableData.length == 0  || approved.Status != 2 ?true:false">CONCURRENCE</v-btn> <!-- EX-O-->
                                            <v-btn v-show="this.user.approver == 4" color="success" medium block @click="approvedData(generateDate,team,'4')" :disabled="tableData.length == 0  || approved.Status != 3 ?true:false">CONCURRENCE</v-btn> <!-- DEPUTY-->
                                            <v-btn v-show="this.user.approver == 5" color="success" medium block @click="approvedData(generateDate,team,'5')" :disabled="tableData.length == 0  || approved.Status != 4 && disableApproved?true:false">APPROVED</v-btn> <!-- COMMANDER-->
                                        </v-col>
                                       
                                    </v-row>
                                    <div v-if="tableData.length > 0">
                                        <br />
                                        <hr />
                                        <center>
                                            <StatusTimeline 
                                                ref="StatusTimeline" 
                                                :month="generateDate" 
                                                :department="department" 
                                                :team="team" 
                                                :table="'training'"
                                                @fromTimeline="handledTimelineData"

                                            />
                                        </center>
                                        
                                    </div>
                                        
                                <!-- </div> -->
                                    
                            </v-container>
                        </v-card>
                    </v-col>
        
                    <v-col cols="12" md="9" lg="9" xl="9">
                        <v-card>
                            <v-simple-table class="elevation-1 custom-table">
                                <thead>
                                    <tr>
                                        <th>UNIT</th>
                                        <th>NR OF PERSONNEL<br>PER UMD</th>
                                        <th>NR ACTUAL<br>ASSIGNED</th>
                                        <th>ORAC</th>
                                        <th>PERCENTAGE</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td>{{ unitName?unitName:'-' }}</td>
                                        <td>{{ totalToe }}</td>
                                        <td>{{ totalAssigned }}</td>
                            
                                        <td :style="approved.Status == 0 && this.tableData.length > 0?'cursor:pointer;background-color:lightyellow;':''" 
                                            @dblclick="editingAct = true;"
                                            v-if="this.tableData.length> 0" >
                                            <template v-if="editingAct && approved.Status == 0">
                                                <v-text-field type="number" v-model="actualMp" dense outlined @blur="checkMp(actualMp)"/>
                                            </template>
                                            <template v-else>
                                                <v-tooltip bottom  transition="slide-x-reverse-transition">
                                                    <template v-slot:activator="{on: tooltip}">
                                                        <div style="white-space: pre-wrap;" v-on="{...tooltip}">{{totalActualMp }}</div>
                                                    </template>
                                                    <span>Double click to edit</span>
                                                </v-tooltip>
                                              </template>
                                        </td>
                                        <td>{{ oracPercentage?oracPercentage.toFixed(2)+'%':0 }}</td>
                                        <!-- <td @dblclick="editingAct = true; editIndex = i" v-else>
                                            <template v-if="editingAct && editIndex === i  ">
                                                <v-text-field type="text" v-model="val.actualMp" dense outlined @blur="checkMp(val.actualMp)"/>
                                            </template>
                                            <template v-else>
                                                <div style="white-space: pre-wrap;">{{ val.actualMp }}</div>
                                              </template>
                                        </td> -->
                                    </tr>
                                </tbody>
                            </v-simple-table>
                        </v-card>
                        <v-divider />
                        <br />
                        <div v-if="reasonTable">
                            <h3>NOTE :</h3>
                            <h4 style="white-space: pre-wrap;">{{ reasonTable }}</h4>
                        </div>
                        
                    </v-col>
                </v-row>
                <!-- reason card history -->
                <v-row>
                    <v-col cols="12" xl="3" md="3" lg="3">
                        <DeclinedCard ref="DeclinedCard" :details="reasonDetails"/>
                    </v-col>
                </v-row>
            </v-tab-item>
            <!-- ORAC TABLE -->
            <v-tab-item :value="'mett'">
                <v-container>
                    <v-card>
                        <v-simple-table class="elevation-1 custom-table" height="70vh" fixed-header>
                            <thead>
                                <tr>
                                    <th>MISSION ESSENTIAL TASK</th>
                                    <th>1st Qtr</th>
                                    <th>2nd Qtr</th>
                                    <th>3rd Qtr</th>
                                    <th>4th Qtr</th>
                                    <th>Required</th>
                                    <th>Actual</th>
                                    <th>Percentage</th>

                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(item,i) in tableData" :key="i">
                                    <td>{{item.Met}}</td>
    
                                    <td @click="updateMet(item,'Quarter1')" :class="item.Quarter1?'background':''">
                                        <v-tooltip bottom  transition="slide-x-reverse-transition">
                                            <template v-slot:activator="{on: tooltip}">
                                                <span   v-if="item.Quarter1" v-on="{...tooltip}">{{ item.Quarter1 }}</span>
                                            </template>
                                            <span>Click to open details</span>
                                        </v-tooltip>
                                    </td>
                        
                                    <td @click="updateMet(item,'Quarter2')" :class="item.Quarter2?'background':''">
                                        <v-tooltip bottom  transition="slide-x-reverse-transition">
                                            <template v-slot:activator="{on: tooltip}">
                                                <span  v-if="item.Quarter2" v-on="{...tooltip}">{{ item.Quarter2 }}</span>
                                            </template>
                                            <span>Click to open details</span>
                                        </v-tooltip>
                                    </td>
                                    <td @click="updateMet(item,'Quarter3')"  :class="item.Quarter3?'background':''">
                                        <v-tooltip bottom  transition="slide-x-reverse-transition">
                                            <template v-slot:activator="{on: tooltip}">
                                                <span  v-if="item.Quarter3" v-on="{...tooltip}">{{ item.Quarter3 }}</span>
                                            </template>
                                            <span>Click to open details</span>
                                        </v-tooltip>
                                    </td>
                                    <td @click="updateMet(item,'Quarter4')" height="60px" :class="item.Quarter4?'background':''">
                                        <v-tooltip bottom  transition="slide-x-reverse-transition">
                                            <template v-slot:activator="{on: tooltip}">
                                                <span  v-if="item.Quarter4" v-on="{...tooltip}">{{ item.Quarter4 }}</span>
                                            </template>
                                            <span>Click to open details</span>
                                        </v-tooltip>
                                    </td>
    
                
                                    <td >{{ item.Required }}</td>
                                    <td >{{ item.Actual }}</td>
                                    <td>{{item.colPercentage?item.colPercentage.toFixed(2)+'%':''}}</td>
                                   
                                </tr>
                                <tr v-if="tableData.length > 0">
                                    <td colspan="5"></td>
                                    <td class="total">{{ totalRequired }}</td>
                                    <td class="total">{{ totalOrac }}</td>
                                    <td class="total">{{ metPercentage.toFixed(2) }}%</td>
                                </tr>
                            </tbody>
                        </v-simple-table>
                    </v-card>
                   
                </v-container>
            </v-tab-item>

            <!-- TRR -->
            <v-tab-item :value="'trr'">
                <v-container>
                    <v-card>
                        <v-simple-table class="elevation-1 custom-table">
                            <thead>
                                <tr>
                                    <th>UNIT</th>
                                    <th>PERCENTAGE<br>ORAC</th>
                                    <th>PERCENTAGE<br>METT</th>
                                    <th>TRR</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td rowspan="2">{{ unitName }}</td>
                                    <td>{{ oracPercentage?oracPercentage.toFixed(2)+'%':0 }}</td>
                                    <td>{{ metPercentage?metPercentage.toFixed(2)+'%':0 }}</td>
                                    <td rowspan="2">{{ trRating?trRating.toFixed(2)+'%':0 }}</td>
                                </tr>
                                <tr>
                                    <td>{{ oracValue?oracValue.toFixed(2)+'%':0 }}</td>
                                    <td>{{ metValue?metValue.toFixed(2)+'%':0 }}</td>
                                </tr>
                                <tr>
                                    <td colspan="3"></td>
                                    <td :class="redConStyle(redConResult(trRating))">{{ redConResult(trRating) }}</td>
                                </tr>
                            </tbody>
                        </v-simple-table>
                    </v-card>
                </v-container>
                <v-divider />
                <br>
                <v-container>
                <!------------------assessment table------------------------- -->
                <h3>Assessment:</h3>
                
                    <v-card elevation="2">
                        
                            <v-simple-table
                                class="custom-table"
                            >
                            
                            <tbody v-for="(val, e) in remarksData" :key="e">
                                <tr>
                                    <td style="font-weight: bold;text-align: left !important;background-color: lightgray;">{{ val.Question }}</td>
                                </tr>
                                <tr height="90%">
                                    <td @dblclick="editing = true; editIndex = e" style="text-align: left !important;white-space: pre-wrap;">
                                        <template v-if="editing && editIndex === e">
                                            <div contenteditable  @blur="updateRemarks(val)" style="white-space: pre-wrap;">{{ val.Remarks }}</div>
                                        </template>
                                        <template v-else>
                                            <div style="white-space: pre-wrap;">{{ val.Remarks }}</div>
                                        </template>
                                </td>
                                </tr>
                            </tbody>  
                            </v-simple-table>
                    
                    </v-card>
            </v-container>
            </v-tab-item>
        </v-tabs-items>

        <!-- update met dialog  -->
        <v-dialog v-model="metDialog" persistent width="60%" scrollable>
            <v-card>
                <v-card-title style="background-color: #0275d8;color:white;">{{ metDialogTitle }}
                    <v-spacer /><v-btn  @click="metDialog = false" icon><v-icon color="white">mdi-close-circle-outline</v-icon></v-btn>
                </v-card-title>
                <br />
                <v-card-text>
                    <v-simple-table
                        class="custom-table"
                        fixed-header
                    >
                        <thead>
                            <tr>
                                <th>ACTUAL MET</th>
                                <th>DATE</th>
                
                            </tr>
                        </thead>
                        <tbody v-for="(val,i) in dialogTable" :key="i" >
                            <!-- {{ val }} -->
                            <tr v-show="val.quarter == quarterNumber">
                                <td v-if="val.approved  == 1">
                                    {{ val.ActualMet}}
                                </td>
                                <td v-else>
                                    <v-autocomplete
                                        v-model="val.ActualMet" 
                                        dense
                                        outlined
                                        style="margin-top:5%"
                                        :items="metItems"
                                        item-text="Met"
                                        item-value="Met"
                                        clearable
                                        v-if="val.quarter == quarterNumber"
                                     />

                                     <v-autocomplete
                                        v-model="metDetail" 
                                        dense
                                        outlined
                                        style="margin-top:5%"
                                        :items="metItems"
                                        item-text="Met"
                                        item-value="Met"
                                        clearable
                                        v-else
                                     />
                                </td>
                               
                                    
                              
                                <td v-if="val.approved  == 1 ">
                                    {{ val.date }}
                                </td>
                                <td v-else>
                                    <v-menu
                                        v-model="val.menu"
                                        :close-on-content-click="false"
                                        :nudge-right="40"
                                        transition="scale-transition"
                                        offset-y
                                        min-width="auto"
                                    >
                                        <template v-slot:activator="{ on, attrs }">
                                        <v-text-field
                                            v-model="val.date"
                                            label="Inclusive Date"
                                            prepend-inner-icon="mdi-calendar"
                                            readonly
                                            v-bind="attrs"
                                            v-on="on"
                                            dense
                                            outlined
                                            clearable
                                            style="margin-top:5%"
                                            v-if="val.quarter == quarterNumber"
                                           
                                        />
                                        <v-text-field
                                            v-model="metDate"
                                            label="Inclusive Date"
                                            prepend-inner-icon="mdi-calendar"
                                            readonly
                                            v-bind="attrs"
                                            v-on="on"
                                            dense
                                            outlined
                                            clearable
                                            style="margin-top:5%"
                                            v-else
                                           
                                        />
                                        </template>
                                        <v-date-picker
                                        v-model="val.date"
                                        @change="checkMonth(val.date)"
                                        :allowed-dates="disableMonth(quarterNumber)"
                                        @input="val.menu = false"
                                        v-if="val.quarter == quarterNumber"
                                        ></v-date-picker>

                                        <v-date-picker
                                        v-model="metDate"
                                        @change="checkMonth(metDate)"
                                        :allowed-dates="disableMonth(quarterNumber)"
                                        @input="val.menu = false"
                                        v-else
                                        ></v-date-picker>
                                    </v-menu>
                                </td>
                            </tr>
                        </tbody>

                        
                    </v-simple-table>
                    <br />
                </v-card-text>
                <v-card-actions>
                    <v-btn block color="success" medium @click="updateTraining()" :disabled="quarterDisable.includes(generateDate) && this.approved.Status == 0?false:true">UPDATE</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <!-- end  update met dialog-->

        <!-- start reason dialog -->
        <v-dialog v-model="reasonDialog" persistent width="30%" scrollable>
            <v-card>
                <v-card-title style="background-color: #0275d8;color:white;">ORAC
                    <v-spacer /><v-btn  @click="reasonDialog = false" icon><v-icon color="white">mdi-close-circle-outline</v-icon></v-btn>
                </v-card-title>
                <br />
                <br />
                
                <v-card-text>
                    <div>
                        <h4><b style="font-size:20px;">{{ this.lackingMp }}</b> member/s of the workforce did not conduct this activity.Please specify the reason</h4>
                        <v-divider/>
                        <v-textarea
                            outlined
                            dense
                            v-model="reasonMet"
                            auto-grow
                            label="Reason"
                            style="white-space: pre-wrap;"
                        />
                    </div>
                    
                </v-card-text>
                <v-card-actions>
                    <v-btn block color="success" @click="updateReason()" :disabled="approved.Status != 0?true:false">UPDATE</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>


        <!-- over lay -->
        <v-overlay :value="loadingPage">
            <v-progress-circular
                indeterminate
                size="70"
            >
                
            </v-progress-circular>
            <br>
            <span>LOADING</span>
        </v-overlay>

        <!-- <RemarksDialog ref="RemarksDialog" :getTraining="generate" :getApprovedMet="getApprovedMet"/> -->
        <TrainingPrint ref="TrainingPrint"/>
        <Details ref="Details" />
        <SubmitDialog ref="SubmitDialog" :getTraining="generate" :getApprovedMet="getApprovedMet"/>
        <DeclinedDialog ref="DeclinedDialog" :getTraining="getTrainingData" :getApprovedMet="getApprovedMet" />
        <PrintDialog ref="PrintDialog" :printPdf="printPdf" :printExcel="extractExcel"/>
        <ExtractionExcel ref="ExtractionExcel" :excelData="dataDownload"/>
    </div>
</template>

<script>

import {mapState} from 'vuex'
import moment from 'moment'
// import RemarksDialog from '../Personnel/RemarksDialog.vue'
import SubmitDialog from '../Details/SubmitDialog.vue'
import TrainingPrint from './TrainingPrint.vue'
import axios from 'axios'
import Details from '../Details/RedConDetails.vue';
import Approved from '../Details/Approved'
import DeclinedDialog from '../Details/DeclinedDialog'
import DeclinedCard from '../Details/DeclinedCard'
import ExtractionExcel from './ExtractionExcel.vue'
import PrintDialog from '../Details/PrintDialog'
import StatusTimeline from '../Details/StatusTimeline.vue'
export default {
    name:'TrainingComponent',
    components:{
        // RemarksDialog,
        SubmitDialog,
        TrainingPrint,
        Details,
        DeclinedDialog,
        DeclinedCard,
        ExtractionExcel,
        PrintDialog,
        StatusTimeline
    },
    data:()=>({
        tableData:[],
        training_tab:'orac',
        generateMonthDialog: false,
        generateDate: null,
        id:null,
        team:null,
        section:null,
        quarter:null,
        quarterItems:[{text:'1st',value:'1'},{text:'2nd',value:'2'},{text:'3rd',value:'3'},{text:'4th',value:'4'},],
        year:moment().year(),
        met:null,
        trainingTask:null,
        required:null,
        actual:null,
        rules:{
            required: [
               (v) => !!v || 'Field is required'
            ],
            existing:[(v) => v !== '1' || "Value '1' is not accepted"],
        },
        snackbar:{
            show: false,
            message: null,
            color: null,
            icon: null,
        },
        timeout:1500,
        monthnow:moment().format('YYYY-MM'),
        remarksTable:[],
        editingAct: false,
        editing:false,
        editIndex: null,
        colPercentage:0,
        department:null,
        metDialog:false,
        metDialogTitle:null,
        dialogObj:{},
        dialogTable:[],
        metItems:[],
        metDetail:null,
        actualMp:0,
        reasonMet:null,
        metDate:null,
        menu:false,
        tableMp:[],
        lackingMp:null,
        reasonDialog:false,
        reasonData:null,
        sumActualMp:0,
        sumActualMet:0,
        quarterNumber:null,
        updateTrainingStatus:false, //this is use to specify what need data tobe called after update
        timelineData:{},
       
    }),

    methods:{
        mett(tab){
            this.training_tab = tab
        },
        orac(tab){
            this.updateTrainingStatus = false
            this.training_tab = tab
        },
        trr(tab){
            this.training_tab = tab
            this.$store.dispatch('diplayRemarks','training')
        },
        capitalizeAfterSpace(str) {
            // Split the string by spaces
            let words = str.split(' ');

            // Capitalize the first letter of each word
            let capitalizedWords = words.map(word => {
            if (word.length > 0) {
                return word.charAt(0).toUpperCase() + word.slice(1).toLowerCase();
            }
            return '';
            });
            // Join the words back together with spaces
            let result = capitalizedWords.join(' ');

            return result;
        },
        test(){

            axios({
                method:'post',
                url:`${this.$baseUrl}api/insertTraining`,
                data:{
                    Team : this.team,
                    Met : this.capitalizeAfterSpace(this.met),
                    // trainingTask : this.capitalizeAfterSpace(this.trainingTask),
                    Month : this.generateDate,
                    Required : this.required,
                    Actual : this.actual

                }
            }).then(res=>{
            if(res.status == 200){
                this.trainingTask = null
                this.required = null
                this.actual = null
                this.getTrainingData()
            }
                
            })
        },
        async callGetTimeline() {
            if (this.$refs.StatusTimeline) {
                await this.$refs.StatusTimeline.getTimeline();
            } else {
                console.error("StatusTimeline ref not available yet.");
            }
        },
        generate(){
           axios({
                method:'post',
                url:`${this.$baseUrl}api/insertTraining`,
                data:{
                    Team : this.team,
                    Month : this.generateDate,
                    Quarter : this.quarterJson
                }
           }).then(async res=>{
            if(res.data != 'noData'){
                await this.getTrainingData()
                await this.callGetTimeline()
                await  this.$refs.DeclinedCard.getReason()
               
            }else{
                alert('No set activities')
            }
                
           })
        },
        // getting training data in training table
      async  getTrainingData(){
       
            this.$store.state.teamData = this.team
            this.$store.state.monthData = this.generateDate
            this.$store.state.loadingPage = true
            axios({
                method:'post',
                url:`${this.$baseUrl}api/getTrainingData`,
                data:{
                    Section : null,
                    Team : this.team,
                    Month : this.generateDate,
                }
            }).then(res=>{
                this.tableData = res.data.reduce((total,item)=>{
                    let i = total.findIndex(r=> r.id == item.id)
                    if(i == -1){
                            if(this.quarterDate == 1){
                                item.Required = item.Required1
                                item.Actual = item.Actual1
                            }else if(this.quarterDate == 2){
                                item.Required = item.Required1 + item.Required2
                                item.Actual = item.Actual1 + item.Actual2

                            }else if(this.quarterDate == 3){
                                item.Required = item.Required1 + item.Required2 + item.Required3
                                item.Actual = item.Actual1 + item.Actual2 + item.Actual3
                            }else if(this.quarterDate == 4){
                                item.Required = item.Required1 + item.Required2 + item.Required3 + item.Required4
                                item.Actual = item.Actual1 + item.Actual2 + item.Actual3 + item.Actual4
                            }
                            item.colPercentage = item.Actual / item.Required * 100
                 
                        total.push(item)
                    }
                    return total
                },[])
                this.$store.state.loadingPage = false
                if(this.updateTrainingStatus == false){
                    this.getActualMp()
                    this.getReason()
                }
                this.updateTrainingStatus = false
             
                // this.$refs.StatusTimeline.getTimeline()
               
            })
        },

        // opening metDialog
        updateMet(item,val){
            // console.log(JSON.parse(item.ActualMet))
            
            if(item[val].includes("No training")){
                alert('No Training Scheduled')
            }else{
                this.quarterNumber = val
                this.getMet()
                this.metDialog = true
                this.metDialogTitle = item[val]
             
                  
                    this.dialogTable = JSON.parse(item.ActualMet)
                    // console.log(this.dialogTable,'this.dialogTable')
                    if(JSON.parse(item.ActualMet).length > 0){
                        this.dialogTable.forEach((rec, i) => {
                            this.dialogTable[i].id = item.id;
                            if (rec.quarter == val) {
                                 // Assuming item.id has a value
                                this.dialogTable[i].ActualMet = rec.ActualMet; // Assuming r.ActualMet has a value
                                this.dialogTable[i].date = rec.date; // Assuming r.date has a value
                                this.dialogTable[i].updatedBy = this.user.name; // Assuming r.date has a value
                                this.dialogTable[i].updatedDate = moment().format('YYYY-MM-DD HH:mm:ss'); // Assuming r.date has a value
                            }
                        });     
                    }
            }
        },
      
        // disable not month not included in quarter
        disableMonth(quarter){
     
                // Define an array of allowed months (0-based) for each quarter.
                const allowedMonths = {
                    'Quarter1': [0, 1, 2], // January, February, and March
                    'Quarter2': [3, 4, 5], // April, May, and June
                    'Quarter3': [6, 7, 8], // July, August, and September
                    'Quarter4': [9, 10, 11], // October, November, and December
                };

                return (date) => {
                    const val = moment(date, 'YYYY-MM-DD'); // Parse the date string
                    const month = val.month();

                    // Check if the month is in the allowed months for the selected quarter.
                    return allowedMonths[quarter].includes(month);
                };
            },
        //getting met items 
        getMet(){
            axios({
                method:'post',
                url:`${this.$baseUrl}api/getMetItems`,
                data:{
                    year : this.generateDate.substr(0,4),
                    team : this.team
                }
            }).then(res=>{
                this.metItems = res.data.filter(res => res.Met)
            })
        },
        
    //checking mp vs required
        checkMp(val){
            if(val > this.totalAssigned){
                alert(`Error: Actual is bigger than Required, the Required Mp for the ${this.formattedDate} are ${this.totalAssigned}`)
                
                this.editingAct = false
                return false
            }else{
                this.editingAct = false
                if(val < this.totalAssigned){
                    this.lackingMp = this.totalAssigned - val
                    this.reasonDialog = true
                }else{
                    this.updateReason()
                }   
            }
            
        },
        checkMonth(item){
            // console.log(item.substr(0,7),'tes')
            this.$store.state.monthData = item.substr(0,7)
        },
       
        openActualMp(){
            this.reasonDialog = true
        },
     
        // get reason
        getReason(){
            axios({
                method:'post',
                url:`${this.$baseUrl}api/getTrainingReason`,
                data : {
                    Team : this.team,
                    Month : this.generateDate
                }
            }).then(res=>{
                if(res.status == 200){
                   this.reasonMet = res.data
                }
            })
        },
        // updating reason dialog
        updateReason(){
            axios({
                method:'post',
                url:`${this.$baseUrl}api/updateTrainingReason`,
                data : {
                    actualMp : this.actualMp,
                    reason : this.reasonMet,
                    Team : this.team,
                    Month : this.generateDate
                }
            }).then(res=>{
                if(res.status == 200){
                    this.getTrainingData()
                    this.reasonDialog = false

                }
            })
        },
        // updating training table
       async  updateTraining(){
            if(this.restrictMonth){
                this.updateTrainingStatus = true
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/updateTraining`,
                    data:{
                        id : this.dialogTable[0].id,
                        actualMet : this.dialogTable,
                        reason : this.reasonMet,
                        Actual : `Actual${this.quarterDate}`,
                        ActualNumber : this.restrictMonth?1:0,
                        totalActualMet : 0,
                        Month : this.generateDate,
                        Department : this.department,
                        Team : this.team,
                        quarter : this.quarterNumber



                    }
                }).then(res=>{
                    if(res.status == 200){

                        this.getTrainingData()

                        this.metDialog = false
                        this.metDate = null
                        this.metDetail = null
                      
                    }
                })
            }else{
                alert('Error: Inclusive date is not same with your request Month ')
            }
           
            
        },
        // getting actuall mp in trainig_table
        getActualMp(){
            axios({
                method:'post',
                url:`${this.$baseUrl}api/getActualMPTraining`,
                data:{
                    Team : this.team,
                    Month : this.generateDate
                }
            }).then(res=>{
                this.tableMp = res.data
               
            })
        },
      
        // submit function
       async submitData(month,team){
        const confirmed = window.confirm('Are you sure you want to submit this report?');
            if(confirmed){
                await  this.$store.dispatch('diplayRemarks','training')
                await  this.$store.dispatch('getQuestion','Training')
                await  this.$refs.SubmitDialog.openDialog(month,team,'training')
            }
         
        },
        //approvedData
        approvedData(month,team,status){
            const confirmed = window.confirm('Are you sure you want to approved this report?');
            if(confirmed){
                let data = {
                    month : month,
                    team : team,
                    table : 'training',
                    name : this.$store.state.user.name,
                    url : this.$baseUrl,
                    status : status
                }
                return Promise.all([Approved(data),this.generate()]) ;
            }
        },
        //declinedData
        declined(month,team){
            let data = {
                month : month,
                title : `Reason for the Decline in the Reporting Month of ${this.formattedDate}`,
                team : team,
                table : 'training',
                name : this.$store.state.user.name,
                url : this.$baseUrl,
                today : moment().format('YYYY-MM-DD HH:mm:ss')
            }
            this.$refs.DeclinedDialog.openDialog(data)
        //    return Promise.all([Declined(data),this.getApprovedMet('declined'),this.generate()]) ;
        },

        // updating assessment remarks function
        updateRemarks(item){
            this.editing = false
            this.editIndex = null  
            const value = event.target.innerText
            let obj = {
                id : item.id,
                remarks : value
            }
            this.$store.dispatch('updateRemarks',obj)
        },
        // print function
        print() {
            // const confirmed = window.confirm('Are you sure you want to print this report?');
            // if(confirmed){
            //     let obj = {
            //         tableData : this.tableData,
            //         teamName :this.unitName,
            //         month : this.formattedDate,
            //         psgTable : this.psgTable,
            //         totalToe : this.totalToe,
            //         totalAssigned : this.totalAssigned,
            //         totalOrac : this.totalOrac,
            //         totalActualMp : this.totalActualMp,
            //         totalRequired : this.totalRequired,
            //         metPercentage : this.metPercentage.toFixed(2)+'%',
            //         oracPercentage : this.oracPercentage.toFixed(2)+'%',
            //         oracValue : this.oracValue.toFixed(2)+'%',
            //         metValue : this.metValue.toFixed(2)+'%',
            //         trRating : this.trRating.toFixed(2)+'%',
            //         remarksData : this.remarksData,
            //         reason : this.reasonMet
            //     }
            //     this.$refs.TrainingPrint.printTraining(obj)
            // }
             //****uncomment tihs if personnel need excel extraction***///
            if(this.remarksData.length > 0){
                this.$store.dispatch('diplayRemarks','training')
                this.$refs.PrintDialog.openDialog()
            }else{
                alert('Kindly ensure that you submit the report before proceeding to print.')
            }
            
        },
        printPdf(){
            this.$refs.TrainingPrint.printTraining(this.dataDownload)
        },
        extractExcel(){
                this.$refs.ExtractionExcel.exportExcel()
        },
        handledTimelineData(data){
            this.timelineData = {...data[0]}
        },
        redConResult(data){
            if(data){
                return this.$refs.Details.redCon(Number(data))
            }
        },
        redConStyle(data){
            if(data){
                return this.$refs.Details.redConStyle(data)
            }
        },

        editable(item){
            if(item.Month == this.generateDate){
                return true
            }else{
                return false
            }
        },
       getApprovedMet(status){
        axios({
            method : 'post',
            url:`${this.$baseUrl}api/getApprovedMet`,
            data:{
                data : this.dialogTable,
                Team : this.team,
                Month : this.generateDate,
                status : status
            }

        }).then(res=>{
            if(res.status == 200){
                console.log('ok')
            }
        })
       },
      


   
    },
    computed:{
        ...mapState([
            'departmentItems',
            'teamItems',
            'psgTable',
            'remarksData',
            'trainingTableUpdate',
            'user',
            'loadingPage'
        ]),
         // function to show month name
         formattedDate() {
                if (this.generateDate) {
                    const date = new Date(this.generateDate);
                    const month = date.toLocaleString('default', { month: 'long' });
                    const year = date.getFullYear();
                    return `${month} ${year}`;
                }
                return null;
        },
         // function for disable sumbit btn
         disableSubmit(){
            if(this.team){
                let office = this.user.main == 'D01'?this.user.office:this.user.unit
                if(office == this.team && this.approved.Status == 0){
                    return true
                }else{
                    return false
                }
                   
            }else{
                return false
            }
        },
        // disable commander approve->this function made due to restric offices
        disableApproved(){
            if(['T01','T02','T03'].includes(this.user.office)){
              
                if(this.approved.Status == 1){
                    return false
                }else{
                    return true
                }
            }else{
                // console.log('false')
                return true
            }
        },
        quarterDisable(){
            let data = []
            let year = this.generateDate?this.generateDate.substr(0,4):''
            if(this.quarterNumber == 'Quarter1'){
                data = [`${year}-01`,`${year}-02`,`${year}-03`]
            }else if(this.quarterNumber == 'Quarter2'){
                data = [`${year}-04`,`${year}-05`,`${year}-06`]
            }else if(this.quarterNumber == 'Quarter3'){
                data = [`${year}-07`,`${year}-08`,`${year}-09`]
            }else if(this.quarterNumber == 'Quarter4'){
                data = [`${year}-10`,`${year}-11`,`${year}-12`]
            }
            return data
        },
        restrictMonth(){
            let data
            this.dialogTable.filter(r=>{
                if(r.quarter == this.quarterNumber){
                    if(r.date && r.date.includes(this.generateDate)){
                        // console.log(r,'rrr')
                        data = true
                    }else{
                        // console.log(r,'ffff')
                        data =  false
                    }
                }
            })
            return data

        },
        quarterDate(){
            if(this.generateDate){
                let month = parseInt(this.generateDate.substr(5, 2), 10);
                let data = null;

                if (month >= 1 && month <= 3) {
                    data = '1';
                } else if (month >= 4 && month <= 6) {
                    data = '2';
                } else if (month >= 7 && month <= 9) {
                    data = '3';
                } else if (month >= 10 && month <= 12) {
                    data = '4';
                }
                return data
            }else{
                return false
            }
          
        },
        quarterJson(){
            let data = []
            let quarter = ['Quarter1','Quarter2','Quarter3','Quarter4']
            
            for (let i = 0; i < quarter.length; i++) {
                let obj = {
                    id : null,
                    ActualMet : null,
                    date :null,
                    quarter : null,
                    approved : 0,
                    updatedBy:null,
                    updatedDate:null
                } 
                const element = quarter[i];
                obj.quarter = element
                data.push(obj)
            }   
            return data;    
        },
        // to check if the unit is submit
        submitted(){
            if(this.tableMp.length > 0){
                return this.tableMp.find(res=> res.Submit == '1' && res.Month == this.generateDate)
            }else{
                return 0
            }
        },
         // to check if the unit is approved by approver
         approved(){
            if(this.tableMp.length > 0){
                return this.tableMp.find(res=> res.Status && res.Month == this.generateDate)
                // console.log(approved,'approved')
                // if(approved){
                //     return 1
                // }else{
                //     return 0
                // }
            }else{
                return 0
            }
        },
        // to check if the unit is submit
    
        reasonTable(){
            if (this.tableMp.length > 0) {
                return this.tableMp[0].Reason
            }else{
                return null
            }
        },
        trainingId(){
           return this.tableMp.find(res=> res.Month == this.generateDate && res.TrainingID)
        },
        //total actual MP base on month
        totalActualMp(){
            let data
            this.tableMp.filter(res=>{
                // console.log(res,'res')
                if(res.Month == this.generateDate){
                    
                    data = res.ActualMp
                    this.actualMp = data
                }else{
                    data = 0
                    this.actualMp = data
                }
            })
            return data
        },
        
         totalRequired(){
            if(this.tableData.length > 0){
                return this.tableData.reduce((total, item) => {
                    if(item.Required){
                        return total + Number(item.Required);
                    }
                    return total;
                    }, 0);
            }else{
                return 0
            }
        },

        totalOrac(){
            // let data
            // this.tableMp.filter(res=>{
            //     // console.log(res,'res')
            //     if(res.Month == this.generateDate && res.ActualMet){
            //         data = Number(res.ActualMet)
            //     }else{
            //         data = 0
            //     }
            // })
            // return data
            if(this.tableData.length > 0){
                return this.tableData.reduce((total, item) => {
                    if(item.Actual){
                        return total + Number(item.Actual);
                    }
                    return total;
                    }, 0);
            }else{
                return 0
            }
        },
        // function to get total psg for psg tab
        metPercentage(){
            if(this.tableData.length > 0){
               let result
                result = (this.totalOrac / this.totalRequired * 100)
                // console.log(result,'result')
               return isNaN(result) || result !== Infinity ?result:0
            }else{
                return 0
            }
        },

        // function to get total Actual for psg tab
        totalAssigned(){
            if(this.psgTable.length > 0){
                return this.psgTable.reduce((total, item) => {
                    if (item.TotalActual) {
                        return total + item.TotalActual;
                    }
                    return total;
                    }, 0);
            }else{
                return 0
            }
        },
        // 
        oracPercentage(){
            if(this.tableData.length > 0){
               let result
                result = (this.totalActualMp / this.totalAssigned * 100)
               return result
            }else{
                return 0
            }
        },
        oracValue(){
            if(this.tableData.length > 0){
               let result
                result = ((this.totalActualMp / this.totalAssigned) * 100) *( 0.40)
               return result
            }else{
                return 0
            }
        },
        metValue(){
            if(this.tableData.length > 0){
               let result
                result = ((this.totalOrac / this.totalRequired) * 100) * (0.60)
               return result
            }else{
                return 0
            }
        },
        trRating(){
            if(this.tableData.length > 0){
               let result
               let met = ((this.totalOrac / this.totalRequired) * 100) * (0.60)
               let orac = ((this.totalActualMp / this.totalAssigned) * 100) *( 0.40)
                result = ((Number(orac) + Number(met)))

                return isNaN(result) || result !== Infinity ?result:0
            }else{
                return 0
            }
        },
        
        //unit name
        unitName(){
            let data
            this.teamItems.filter(r=>{
                if(r.UnitTeamCode == this.team){
                    data = r.UnitTeamName
                }
            })
            return data
        },
        
        yearItems(){
            const tempData = []
            const currentYear = moment().year();
            const endYear = currentYear + 5;

            for (let year = 2020; year <= endYear; year++) {
                tempData.push(year);
            }

            return tempData;

        },
         // function to get total TOE for psg tab
         totalToe(){
            if(this.psgTable.length > 0){
                return this.psgTable.reduce((total, item) => {
                    if (item.TotalRequired) {
                        return total + item.TotalRequired;
                    }
                    return total;
                    }, 0);
            }else{
                return 0
            }
        },
        reasonDetails(){
            let data = {
                table : 'training',
                month : this.generateDate,
                unit : this.team
            }
            return data
        },
        dataDownload(){
            let obj = {
                tableData : this.tableData,
                teamName :this.unitName,
                month : this.formattedDate,
                psgTable : this.psgTable,
                totalToe : this.totalToe,
                totalAssigned : this.totalAssigned,
                totalOrac : this.totalOrac,
                totalActualMp : this.totalActualMp,
                totalRequired : this.totalRequired,
                metPercentage : this.metPercentage.toFixed(2)+'%',
                oracPercentage : this.oracPercentage.toFixed(2)+'%',
                oracValue : this.oracValue.toFixed(2)+'%',
                metValue : this.metValue.toFixed(2)+'%',
                trRating : this.trRating.toFixed(2)+'%',
                remarksData : this.remarksData,
                reason : this.reasonMet,
                status : this.approved.Status,
                sign : this.timelineData
            }
            return obj
        }
    },
    created(){
        if(!this.$store.state.user.rights){
            this.$store.state.departmentData =  this.$store.state.user.main
            this.$store.dispatch('getTeam')
        }
    },
    mounted(){
        this.$store.dispatch('getDepartment')
        this.department = this.user.main
        this.$store.state.departmentData = this.department
        this.user.rights?this.team = null:this.user.main == 'D01'?this.team = this.user.office:this.team = this.user.unit
    },
    watch:{
        tableData(val){
            if(val){
                this.$store.dispatch('getPsgTable')
            }
            
        },
        trainingTableUpdate(val){
            if(val){
                this.getTrainingData()
            }
        },
        department(val){
            if(val){
                this.$store.state.departmentData = val
                this.$store.dispatch('getTeam')
            }
        },
        // metPercentage(val){
        //     console.log(val,'val')
        // }
 
    }
}
</script>
