<template>
    <div>

    </div>
</template>

<script>
    import pdfMake from 'pdfmake/build/pdfmake';
    import pdfFonts from 'pdfmake/build/vfs_fonts';
    pdfMake.vfs = pdfFonts.pdfMake.vfs;
    export default {
        data(){
            return{
                imageDataUrl:''
            }
        },
        methods:{
            redCon(data){
                // console.log(val,'val')
                let val
                if(data <= 100 && data >= 85){
                    val = 'R1 - Fully Mission Capable'
                }
                else if(data <= 84 && data >= 75){
                    val = 'R2 - Mission Capable'
                }
                else if(data <= 74 && data >= 51){
                    val = 'R3 - Partially Mission Capable'
                }
                else{
                    val = 'R4 - Not Mission Capable'
                }
               
                return val;
            },
            redConStyle(val){
                let style
                if(val.includes('R1')){
                    style = 'redCon1'
                }
                else if(val.includes('R2')){
                    style = 'redCon2'
                }
                else if(val.includes('R3')){
     
                    style = 'redCon3'
                }
                else{
                    style = 'redCon4'
                }
                return style
            },
            async printEquipment(data){
                Promise.all([this.table(data),this.readiness(data)]).then(function(){
                    alert('Files are printed!')
                })
            },
           async table(data) {

                let tableBody = [];
                const tableHeader = [
                    'Description',
                    'Required',
                    'Actual'
                ];

                data.tableData.forEach(row => {
             
                        let rowMain = [
                            { text: row.Detail || '', alignment: 'left', fontSize: row.Detail && row.Detail.length > 20 ? 8 : 10 },
                            { text: row.Required || '', alignment: 'center', fontSize: 10},
                            { text: row.Actual || '', alignment: 'center', fontSize: 10 },
                        ];
                        tableBody.push(rowMain);  
                });

                const headerStyle = {
                    fillColor: 'lightgrey',
                    fontSize: 11,
                    bold: true,
                    alignment: 'center',
                    noWrap: true
                };

                const bodyStyle = {
                    fontSize: 8,
                    noWrap: true
                };
                const docDefinition = {
                    width: '100%',
                    content: [
                        { text: data.teamName, alignment: 'center', fontSize: 15, margin: [0, 0, 0, 2] },
                        { text: 'EQUIPMENT', alignment: 'center', fontSize: 13, margin: [0, 0, 0, 2] },
                        { text: data.month, alignment: 'center', fontSize: 10, margin: [0, 0, 0, 2] },
                        {
                            table: {
                            headerRows: 1,
                            widths: ['60%', '20%', '20%'],
                            body: [
                                tableHeader.map(headerCell => ({
                                text: headerCell,
                                style: 'tableHeader'
                                })),
                                ...tableBody
                            ],
                
                            }
                        }
                    ],
                    pageOrientation: 'portait',
                    styles: {
                    tableHeader: headerStyle,
                    tableBody: bodyStyle
                    },
                    footer(currentPage, pageCount) {
                        return { text: `Page ${currentPage.toString()} of ${pageCount.toString()}`, alignment: 'right', margin: [50, 0],fontSize: '6', };
                    },
                };

                pdfMake.createPdf(docDefinition).download('EQUIPMENT.pdf');
            },


            
            // readiness


            async readiness(data) {
                    // console.log(data)
                    const  roundUpValue = (number) => {
                        const roundedNumber = Math.round(number * 100) / 100; // Round to 2 decimal places
                        return roundedNumber.toFixed(2); // Format to 2 decimal places as a string
                    }
                    const itemPercentage = (required, item) => {
                        let data = item / required * 100;
                        if (data) {
                        return roundUpValue(data);
                        } else {
                        return 0;
                        }
                    };
                    let remarksBody = [];
                        
                        if (data.assesment) {
                            data.remarksData.forEach(row => {
                                if (row.Question) {
                                    let question = [
                                        { text: row.Question || '', alignment: 'left', fontSize: 10, fillColor: 'lightgrey', bold: true },
                                    ];
                                    remarksBody.push(question);
                                }
                                if (row.Remarks) {
                                    let remarks = [
                                        { text: row.Remarks || '', alignment: 'left', fontSize: 10 }
                                    ];
                                    remarksBody.push(remarks);
                                }
                            });
                        }
                    let docDefinition = {
                    content: [
                    {
                            margin: [120, 0, 0, 0],
                            columns: [
                                {
                                    image: this.imageDataUrl, // Replace with the path to your image
                                    width: 50, // Adjust the width as needed
                                    alignment: 'right', // Adjust the alignment as needed
                                    margin: [0, 0, 0, 0], // Adjust the margins as needed
                                },
                                {
                                width: 'auto', // Adjust the width as needed
                                    text: [
                                        { text: data.teamName, alignment: 'center', fontSize: 15, margin: [0, 0, 0, 2] },
                                        "\n",
                                        { text: 'EQUIPMENT READINESS REPORT', alignment: 'center', fontSize: 13, margin: [0, 0, 0, 2] },
                                        "\n",
                                        { text: data.month, alignment: 'center', fontSize: 10, margin: [0, 0, 0, 2] },
                                    ],
                                    
                                },
                            ],
                        },

                        { text: '\n' },

                        { text: '\n' },
                
                        {   
                            table: {
                                headerRows: 1,
                                widths: ['*','*','10%','10%','10%'],
                                body: [
                                    [
                                        { text: '', style: 'tableHeader' },
                                        { text: '', style: 'tableHeader' },
                                        { text: 'TE', style: 'tableHeader' },
                                        { text: 'ACTUAL', style: 'tableHeader' },
                                        { text: '%', style: 'tableHeader' },
                                    ],
                                    ...data.totalTable.flatMap((item, i) => {
                                            const rows = [];

                                            if (item.Category == item.Sub) {
                                            rows.push([
                                                { text: item.Category, colSpan: 3, style: 'groupDivision' },
                                                {},
                                                {},
                                                {},
                                                {},
                                            ]);
                                            }
                                            if(!item.title){
                                                rows.push([
                                                    {},
                                                    { text: item.info == 'primary'?item.Sub:'',  style: 'body'},
                                                    { text: item.totalRequired, style: 'body' },
                                                    { text: item.totalActual, style: 'body' },
                                                    { text: item.percent.toFixed(2)+'%', style: 'body' }
                                                ]);
                                            }
                                            if(item.break){
                                                rows.push([
                                                    { text: '', style: 'total' },
                                                    { text: 'TOTAL', style: 'total' },
                                                    { text: item.info == 'primary'?data.totalPrimaryRequired:data.totalSecondaryRequired, style: 'total' },
                                                    { text: item.info == 'primary'?data.totalPrimaryActual:data.totalSecondaryActual, style: 'total' },
                                                    { text: item.info == 'primary' ?data.averagePrimary+'%':data.averageSecondary+'%', style: 'total' },
                                                ]);
                                                rows.push([
                                                    { text: '', style: 'subTotal' },
                                                    { text: item.info == 'primary' ? 'MISSION ESSENTIAL EQUIPMENT (80%)' : 'SECONDARY EQUIPMENT', style: 'subTotal', colSpan: 3 },
                                                    {},
                                                    {},
                                                    { text: item.info == 'primary' ? data.mee.toFixed(2)+'%' : data.see.toFixed(2)+'%', style: 'subTotal' },
                                                ]);
                                            }
                                                

                                                if (!data.totalTable[i +1]) {
                                                    rows.push(
                                                        [
                                                            { text: 'MEssE + SEE', style: 'total',colSpan: 4 },
                                                            {},
                                                            {},
                                                            {},
                                                            { text: data.readinessRating.toFixed(2)+'%', style: 'total' },
                                                        ],
                                                        [
                                                            { text: this.redCon(data.readinessRating), style: this.redConStyle(this.redCon(data.readinessRating)),colSpan:5,alignment:'center' },
                                                            {},
                                                            {},
                                                            {},
                                                            {},
                                                        ],
                                                    );
                                                }
                                        
                                            

                                            return rows;
                                        }),
                                ],
                            },
                        },
                        {},
                        { text: 'Assessment:',alignment: 'left', fontSize: 15,bold:true,margin: [0, 20, 0, 0]},
                        {
                            table: {
                                
                                widths: ['*'],
                                body: remarksBody
                            }
                        },
                        {},
                        {
                            margin: [0, 70, 0, 0],
                            columns:[
                                
                                {
                                    text: [
                                        { text: 'PREPARED BY:',alignment: 'center', fontSize: 12},
                                        "\n",
                                        "\n",
                                        "\n",
                                        "\n",
                                        { text: data.sign.PreparedBy,alignment: 'center', fontSize: data.sign.PreparedBy.length < 23?12:10,bold:true,margin: [0, 0, 0, 0]},
                                        "\n",
                                    ],
                                
                                },
                                {
                                    text: [
                                        { text: 'NOTED BY:',alignment: 'center', fontSize: 12},
                                        "\n",
                                        "\n",
                                        "\n",
                                        "\n",
                                        { text:data.sign.NotedBy,alignment: 'center', fontSize: data.sign.NotedBy && data.sign.NotedBy.length < 23?12:10,bold:true,margin: [0, 0, 80, 0]},
                                        "\n",
                                    ],
                                },
                                {
                                    text: [
                                        { text: 'APPROVED BY:',alignment: 'center', fontSize: 12},
                                        "\n",
                                        "\n",
                                        "\n",
                                        "\n",
                                        { text: data.sign.ApprovedBy,alignment: 'center', fontSize: data.sign.ApprovedBy && data.sign.ApprovedBy.length < 23?12:10,bold:true,margin: [0, 0, 0, 0]},
                                        "\n",
                                    ]
                                }
                            ],
                        }     
                    ],
                    functions: { // Pass the functions to the docDefinition
                        roundUpValue: roundUpValue,
                        itemPercentage: itemPercentage
                    },
                    styles: {
                        tableHeader: {
                            fontSize: 10,
                            bold: true,
                            // fillColor: '#0275d8',
                            fillColor: 'lightgray',
                            alignment: 'center',
                        },
                        total: {
                            fontSize: 10,
                            bold: true,
                            alignment:'center',
                        },
                    
                        groupDivision: {
                            bold: true,
                            fontSize: 10,
                            alignment:'left',
                        },
                        body: {
                            fontSize: 8,
                            alignment:'center',
                        },
                        subTotal: {
                            bold: true,
                            fontSize: 10,
                            fillColor: 'lightgray',
                            alignment:'center',
                        },
                        redCon1: { color: "white",fillColor: 'green'}, // Define styles as needed
                        redCon2: { color: "white",fillColor: 'blue' },
                        redCon3: { fillColor: 'yellow' },
                        redCon4: { color: "white",fillColor: 'red' },
                    },
                };

                    // Generate PDF using pdfmake
                pdfMake.createPdf(docDefinition).download('ERR.pdf');
            },
        },
        computed:{
           
        },
        mounted() {
            // Import the image using the 'import' statement or 'require' if you're using webpack
           
            // Create a function to convert the image to a data URL
            let imageSrc = require(`../../../img/${this.$store.state.user.main}.png`)
            const getImageDataUrl = (imageSrc, callback) => {
            const img = new Image();
            img.crossOrigin = "Anonymous"; // Enable CORS for the image if it's hosted on a different domain
            img.onload = () => {
                const canvas = document.createElement("canvas");
                canvas.width = img.width;
                canvas.height = img.height;
                const ctx = canvas.getContext("2d");
                ctx.drawImage(img, 0, 0);
                const dataUrl = canvas.toDataURL("image/png"); // Replace 'image/png' with the appropriate MIME type if needed
                callback(dataUrl);
            };
            img.src = imageSrc;
            };

            // Call the function to get the data URL
            getImageDataUrl(imageSrc, (dataUrl) => {
            this.imageDataUrl = dataUrl;
            });
        },
    }
</script>

