<template>
    <div>
       
            <v-card 
                rounded="2"  
                color="red" 
                v-if="tableData.length > 0" 
                @click="dialog = true" 
                outlined 
                darken-4
            >
                <v-card-text style="text-align:center;">
                    <p style="font-size:30px;font-weight:bold;">DECLINED HISTORY</p>
                    <p style="font-size:30px;font-weight:bold;">({{tableData.length}})</p>
                    
                </v-card-text>
            </v-card>
   
        
        <v-dialog v-model="dialog" persistent width="700px">
            <v-card elevation="5" v-if="tableData.length > 0">
                <v-card-title style="background-color: #0275d8;color:white;">DECLINED HISTORY
                    <v-spacer /><v-btn  @click="dialog = false" icon><v-icon color="white">mdi-close-circle-outline</v-icon></v-btn>
                </v-card-title>
                <v-card-text>
                    <v-simple-table height="60vh" fixed-header>
                        <thead>
                            <tr>
                                <th>Reason For Declined</th>
                                <th>Date Declined</th>
                                <th>Declined By</th>
                            </tr>
                        </thead>
                        <tbody v-for="(item, i) in tableData" :key="i">
                            <tr>
                                <td>{{ item.Reason }}</td>
                                <td>{{ item.Date }}</td>
                                <td>{{ item.Name }}</td>
                            </tr>
                        </tbody>
                    </v-simple-table>
                </v-card-text>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
    import axios from 'axios'
    export default {
        props:[
            'details'
        ],
        data() {
            return {
                tableData:[],
                dialog:false
            }
        },
        methods:{
            getReason(){
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/declinedReasonCard`,
                    data:{
                        table : this.details.table,
                        month : this.details.month,
                        unit : this.details.unit
                    }
                }).then(res=>{
                    this.tableData = res.data
                    this.$emit('reasonEvent', res.data.length > 0?true:false); 
                })  
            }
            
        },
        mounted() {
         
        },
    }
</script>

<style scoped>
.v-data-table > .v-data-table__wrapper > table > thead > tr > th{
    text-align: center !important;
    background-color: transparent !important;
    height:50px; /* height para sa Header*/
    font-weight: bold !important;
    color:black !important;
}
.history{
    background-color: rgb(248, 52, 52);
    font-size: 15px;
    font-weight: bold;
    color: azure;
}
</style>