import axios from 'axios';
// import { update } from 'lodash';
import Vue from 'vue';
import Vuex from 'vuex';
import createPersistedState from 'vuex-persistedstate';
import moment from 'moment'

Vue.use(Vuex)
const persistedData = new createPersistedState({
    key:'store',
    storage: localStorage,
    reducer : state => ({
        user : state.user,
        hideLogin : state.hideLogin,
        signatories : state.signatories
        // loggedInUser : state.loggedInUser
    })
})

export default new Vuex.Store({
    state:()=>({
        user:[],
        signatories:[],
        hideLogin:false,
        departmentData:null,
        sectionData:null,
        teamData:null,
        monthData:null,
        departmentItems:[],
        teamItems:[],
        branchItems:[],
        positionItems:[],
        rankItems:[],
        afposItems:[],
        serviceItems:['PN','PN(M)','PN(GSC)','PN(MNSA)','Civ'],
        lastMonthData:[],
        psgTable:[],
        gradePoints:0,
        afposPoints:0,
        questionData:[],
        remarksData:[],
        overlay:false,
        loadingPage:false,
        overlayRemarks:false,
    


        // admin
        personnelAddTable:[],
        personnelRecap:[],
        divisionItems:[],
        unitItems:[],
        filterDivisionItems:[],
        filterRankItems:[],
        filterUnitItems:[],

        //training
        trainingTableUpdate:false,

        
    }),

    actions:{
        getLogin({commit},data){
            commit('getLogin',data)
        },
        getSignatories({commit},data){
            commit('getSignatories',data)
        },
        getDepartment({commit}){
            commit('getDepartment')
        },
        getTeam({commit}){
            commit('getTeam')
        },
        getLastMonth({commit}){
            commit('getLastMonth')
        },
        getBranch({commit},data){
            commit('getBranch',data)
        },
        getPosition({commit},data){
            commit('getPosition',data)
        },
        getRank({commit}){
            commit('getRank')
        },
        getAfpos({commit}){
            commit('getAfpos')
        },
        getPsgTable({commit}){
            commit('getPsgTable')
        },
        getPoints({commit}){
            commit('getPoints')
        },
        getQuestion({commit},data){
            commit('getQuestion',data)
        },
        diplayRemarks({commit},data){
            commit('diplayRemarks',data)
        },
        updateRemarks({commit},data){
            commit('updateRemarks',data)
        },

        // admin
        getDivision({commit}){
            commit('getDivision')
        },
       getFilterDivision({commit},data){
            commit('getFilterDivision',data)
        },
        getFilterRank({commit},data){
            commit('getFilterRank',data)
        },
        getUnit({commit}){
            commit('getUnit')
        },
        getFilterUnit({commit},data){
            commit('getFilterUnit',data)
        },
    
    },
    mutations:{
        getLogin(state,data){
            if(data == 'logout'){
                state.user = []
                state.signatories = []
            }else{
                state.user = data
                axios({
                    method:'post',
                    url:`${Vue.prototype.$baseUrl}api/insertLog`,
                    data:{
                        unit : data.main == 'D01'?data.office:data.unit,
                        id : data.id,
                        log : moment().format('YYYY-MM-DD HH:mm:ss')
                    } 
                })
                // .then(res=>{
                //     console.log(res)
                // })
            }
            
            
            
        },
        getSignatories(state,data){
            axios({
                method:'post',
                url:`${Vue.prototype.$baseUrl}api/getUserSignatory`,
                data:{
                    unit : data.main == 'D01'?data.office:data.unit,
                    main : data.main
                }
                
            }).then(res=>{
                state.signatories = res.data
            })
        },
        getTeam(state){
            state.loadingPage = true
            axios({
                method:'post',
                // url:`${Vue.prototype.$baseUrl}api/getTeamList',
                url:`${Vue.prototype.$baseUrl}api/getTeamList`,
                data:{
                    department : state.departmentData
                }
                
            }).then(res=>{
                const decryptedData = atob(res.data);
                let tempData = JSON.parse(decryptedData);
                // console.log(tempData)
                if(state.departmentData == 'D01'){
                    state.teamItems = tempData
                }else{
                    state.teamItems = tempData.reduce((total,item)=>{
                        let i = total.findIndex(res=> res.UnitTeamCode == item.UnitTeamCode)
                        if(i == -1){
                            total.push(item)
                        }
                        return total
                    },[])
                }
                
                state.loadingPage = false
            })
        },
        getDepartment(state){
            // state.loadingPage = true
            axios({
                method:'post',
                url:`${Vue.prototype.$baseUrl}api/getDepartmentList`,
                
            }).then(res=>{
                const decryptedData = atob(res.data);
                let tempData = JSON.parse(decryptedData);
                state.departmentItems = tempData
                state.loadingPage = false
            })
        },
        getLastMonth(state){
            state.overlay = true
            axios({
                method:'post',
                url:`${Vue.prototype.$baseUrl}api/getLastMonth`,
                data:{
                    Month : state.monthData,
                    Team : state.teamData,
                    User : state.user.name,
                    Time : moment().format('YYYY-MM-DD HH:mm:ss')
                }
            }).then(res=>{
                state.lastMonthData = []
                if(res.status == 200){
                    state.lastMonthData = res.data
                    // console.log(state.lastMonthData,'ceo')
                    state.overlay = false
                }
            })
            .catch(error => {
              if(error){
                state.overlay = false; // Make sure to handle the overlay even in case of an error
              }
             
          });
        },
        getBranch(state,data){
            axios({
                method:'post',
                url:`${Vue.prototype.$baseUrl}api/getBranchList`,
                data:{
                    data : data
                }
            }).then(res=>{
                const decryptedData = atob(res.data);
                let tempData = JSON.parse(decryptedData);
                state.branchItems = tempData.reduce((total,item)=>{
                    let i = total.findIndex(r=> r.BranchCode == item.BranchCode)
                    if(i == -1){
                        total.push(item)
                    }
                    return total
                },[])
            })
        },
        getPosition(state,data){
            axios({
                method:'post',
                url:`${Vue.prototype.$baseUrl}api/getPositionList`,
                data:{
                    data : data,
                    unit : state.teamData
                }
            }).then(res=>{
                state.positionItems = res.data.reduce((total,item)=>{
                    let i = total.findIndex(r=> r.PositionCode == item.PositionCode)
                    if(i == -1){
                        total.push(item)
                    }
                    return total
                },[])
            })
        },
        getRank(state){
            axios({
                method:'post',
                url:`${Vue.prototype.$baseUrl}api/getRankList`,
            }).then(res=>{
                state.rankItems = res.data
            })
        },
        getAfpos(state){
            axios({
                method:'post',
                url:`${Vue.prototype.$baseUrl}api/getAfposList`,
                
            }).then(res=>{
                state.afposItems = res.data
            })
        },
        getPsgTable(state){
            axios({
                method:'post',
                url:`${Vue.prototype.$baseUrl}api/getPsgTable`,
                data:{
                    Month : state.monthData,
                    Team : state.teamData
                }
            }).then(res=>{
                // console.log(res.data)
               
                state.psgTable = res.data.reduce((total,item)=>{
                    let index = total.findIndex(res=> res.DivisionCode == item.DivisionCode)
                    if(index == -1){
                        let obj = {...item}
                        obj.count = 1
                        obj.GroupDivision = item.DivisionName
                        obj.TotalRequired = item.countRequired
                        obj.TotalActual = item.countActual
                        total.push(obj)
                    }else{
                       
                        total[index].TotalRequired += item.countRequired
                        total[index].TotalActual += item.countActual
                        total[index].count += 1
                        item.totalCountRequired = total[index].TotalRequired
                        item.totalCountActual = total[index].TotalActual
                        total.push(item)
                    }
                    return total
                },[])
                // console.log(state.psgTable,'state.psgTable')
               
            })
        },
        getPoints(state){
            axios({
                method:'post',
                url:`${Vue.prototype.$baseUrl}api/getPoints`,
                data:{
                    Month : state.monthData,
                    Section : state.sectionData,
                    Team : state.teamData,
                }
            }).then(res=>{
                let data = res.data
                if(data){
                    state.gradePoints = data.GradePoint
                    state.afposPoints = data.AfposPoint
                }
                // console.log(state.gradePoints)
               
            
               
                
            })
        },
    
        getQuestion(state,data){
            axios({
                method:'post',
                url:`${Vue.prototype.$baseUrl}api/getQuestion`,
                data:{
                    Table : data
                }
            }).then(res=>{
                state.questionData = res.data
            })
        },
        diplayRemarks(state,data){
            // console.log('remarks')
            state.overlayRemarks = true
            axios({
                method:'post',
                url:`${Vue.prototype.$baseUrl}api/getRemarks`,
                data:{
                    Month : state.monthData,
                    Team : state.teamData,
                    Table : data
                }
            }).then(res=>{
                // console.log(res.data,'res')
                state.remarksData = res.data
                state.overlayRemarks = false
            })
        },
        updateRemarks(state,data){
            axios({
                method:'post',
                url:`${Vue.prototype.$baseUrl}api/updateRemarks`,
                data:{
                    id : data.id,
                    remarks : data.remarks,
                    Time : moment().format('YYYY-MM-DD HH:mm:ss'),
                    Name : state.user.name,
                }
            }).then(function(){
                
            })
        },



        ////admin tab
        getDivision(state){
            axios({
                method:'get',
                url:`${Vue.prototype.$baseUrl}api/getDivision`,
                
            }).then(res=>{
                // console.log(res.data)
                state.divisionItems = res.data
            })
        },
       getFilterDivision(state,data){
            axios({
                method:'post',
                url:`${Vue.prototype.$baseUrl}api/getFilterDivision`,
                data:{
                    data : data
                }
            }).then(res=>{
                // console.log(res.data)
                state.filterDivisionItems = res.data
            })
       },
        getFilterRank(state,data){
            axios({
                method:'post',
                url:`${Vue.prototype.$baseUrl}api/getFilterRank`,
                data:{
                    data : data
                }
            }).then(res=>{
                // console.log(res.data)
                state.filterRankItems = res.data
            })
        },
        getUnit(state){
            axios({
                method:'get',
                url:`${Vue.prototype.$baseUrl}api/getUnit`,
                
            }).then(res=>{
                state.unitItems = res.data
            })
        },
        getFilterUnit(state,data){
            axios({
                method:'post',
                url:`${Vue.prototype.$baseUrl}api/getFilterUnit`,
                data:{
                    data : data
                }
                
            }).then(res=>{
                state.filterUnitItems = res.data
            })
        },
   
    },
    getters:{},
    plugins: [persistedData]
});