<template>
    <div>
        <v-dialog v-model="reportDialog" persistent scrollable>
            <v-card>
                <v-card-title style="background-color: #0275d8; color: white;">
                    EQUIPMENT & MAINTENANCE READINESS
                    <v-spacer/><v-btn  @click="reportDialog = false" icon><v-icon color="white">mdi-close-circle-outline</v-icon></v-btn>
                </v-card-title>
                <br>
                    <v-card-text>
                        <h1><center>{{ teamName }}</center></h1>
                        <h2><center>{{ formattedDate }}</center></h2>
                        <v-divider/>
        <v-divider/>
        <v-col cols="12" md="12" lg="12" xl="12">
                <v-tabs v-model="equipment_tab" justify-center>
                    <v-tab href="#equip" @click="equip('equip')">EQUIPMENT & MAINTENANCE</v-tab>
                    <v-tab href="#read" @click="read('read')" v-if="tableData.length > 0">REPORT READINESS</v-tab>
                </v-tabs>
        </v-col>
        <v-tabs-items v-model="equipment_tab">
            <v-tab-item :value="'equip'">
                <v-container>
                    <v-card>
                        <v-simple-table class="elevation-1 custom-table" fixed-header height="80vh">
                            <thead>
                                <tr>
                                    <th>Description</th>
                                    <th width="20%">Actual</th>
                                    <th width="20%">Serviceable</th>
                                    <th width="5%" v-show="!submitted">Action</th>
                                </tr>
                            </thead>
                                <tbody v-for="(item,i) in tableData" :key="i">
                                <tr >
                                    <td colspan="3" :style="item.SubMain != 'MOBILITY' && item.SubMain != 'FIREPOWER'?'background-color: lightgray;vertical-align:middle;':''">  
                                        <b>{{ item.Main }}</b>
                                    </td>
                                </tr>
                                <template v-if="item.Details">
                                    <tr v-for="(data, e) in JSON.parse(item.Details)" :key="e">
                                        <td style="white-space: pre-wrap;">{{ data.Item }}</td>
                                        <td style="white-space: pre-wrap;">{{ data.Actual?data.Actual:'' }}</td>
                                        <td style="white-space: pre-wrap;">{{ data.Service?data.Service:'' }}</td>
                                    </tr>
                                </template>
                            </tbody>
                        </v-simple-table>
                    </v-card>
                </v-container>
                
               
            </v-tab-item>
            <v-tab-item :value="'read'">
                <v-container>
                    <v-row>
                        <!-- EQUIPMENT -->
                        <v-col cols="12" md="6" lg="6" xl="6">
                            <h2>EQUIPMENT READINESS RATING</h2>
                            <v-card>
                                <v-simple-table class="elevation-1 custom-table" fixed-header>
                                    <thead>
                                        <tr>
                                            <th>MEE</th>
                                            <th>SE</th>
                                            <th>ERR</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr>
                                            <td class="content">{{ meEquipment }}%</td>
                                            <td class="content">{{ seEquipment }}%</td>
                                            <td class="content">{{ readinessRatingE }}%</td>
                                        </tr>
                                    </tbody>
                                </v-simple-table>         
                            </v-card>
                            <br>
                            <v-card>
                                <v-simple-table class="elevation-1 custom-table" fixed-header>
                                    <thead>
                                        <tr>
                                            <th></th>
                                            <th></th>
                                            <th>TE</th>
                                            <th>ACTUAL</th>
                                            <th>%</th>
                                        </tr>
                                    </thead>
                                    <tbody v-for="(item,i) in readinessTable" :key="i">
                                        <tr v-if="!item.SubMain">
                                            <td>{{ item.Main }}</td>
                                        </tr>
                                        <tr v-if="item.SubMain">
                                            <td>{{ item.SubMain != 'MOBILITY' && item.SubMain != 'FIREPOWER'?item.Main:'' }}</td>
                                            <td>{{ item.SubMain == 'MOBILITY' || item.SubMain == 'FIREPOWER'?item.Main:'' }}</td>
                                            <td>{{ item.Required }}</td>
                                            <td>{{ calculateActual(item.Details) }}</td>
                                            <td>{{ itemPercentage(item.Required,calculateActual(item.Details)) }}%</td>
                                        </tr>
                                        <template v-if="!item.Priority && (!readinessTable[i + 1] || readinessTable[i + 1].Priority)">
                                            <tr>
                                            <td ></td>
                                            <td class="content">Total</td>
                                            <td class="content">{{ item.total }}</td>
                                            <td class="content">{{ item.Info == 'primary'?totalPrimaryActual:totalSecondaryActual }}</td>
                                            <td class="content">{{ item.Info == 'primary'?averagePrimary:averageSecondary }}%</td>
                                            </tr>
                                            <tr>
                                                <td ></td>
                                                <td class="content" colspan="3">{{ remarks(item) }}</td>
                                                <td class="content">{{ item.Info == 'primary'?meEquipment:seEquipment }}%</td>
                                            </tr>
                                        </template>
                                        
                                    </tbody>
                                </v-simple-table>
                            </v-card>
                        </v-col>
                        <!-- MAINTENANCE -->
                        <v-col cols="12" md="6" lg="6" xl="6">
                            <h2>MAINTENANCE READINESS RATING</h2>
                            <v-card>
                                <v-simple-table class="elevation-1 custom-table" fixed-header>
                                    <thead>
                                        <tr>
                                            <th>MEE</th>
                                            <th>SE</th>
                                            <th>MRR</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr>
                                            <td class="content">{{ meEquipmentM }}%</td>
                                            <td class="content">{{ seEquipmentM }}%</td>
                                            <td class="content">{{ readinessRatingM }}%</td>
                                        </tr>
                                    </tbody>
                                </v-simple-table>         
                            </v-card>
                            <br>
                            <v-card>
                                <v-simple-table class="elevation-1 custom-table" fixed-header>
                                    <thead>
                                        <tr>
                                            <th></th>
                                            <th></th>
                                            <th>ONHAND</th>
                                            <th>ACTUAL</th>
                                            <th>%</th>
                                        </tr>
                                    </thead>
                                    <tbody v-for="(item,i) in readinessTable" :key="i">
                                        <tr v-if="!item.SubMain">
                                            <td>{{ item.Main }}</td>
                                        </tr>
                                        <tr v-if="item.SubMain">
                                            <td>{{ item.SubMain != 'MOBILITY' && item.SubMain != 'FIREPOWER'?item.Main:'' }}</td>
                                            <td>{{ item.SubMain == 'MOBILITY' || item.SubMain == 'FIREPOWER'?item.Main:'' }}</td>
                                            <td>{{ calculateActual(item.Details) }}</td>
                                            <td>{{ calculateService(item.Details) }}</td>
                                            <td>{{ itemPercentage(calculateActual(item.Details),calculateService(item.Details)) }}%</td>
                                        </tr>
                                        <template v-if="!item.Priority && (!readinessTable[i + 1] || readinessTable[i + 1].Priority)">
                                            <tr>
                                            <td class="content"></td>
                                            <td class="content">Total</td>
                                            <td class="content">{{ item.Info == 'primary'?totalPrimaryActual:totalSecondaryActual }}</td>
                                            <td class="content">{{ item.Info == 'primary'?totalPrimaryService:totalSecondaryService }}</td>
                                            <td class="content">{{ item.Info == 'primary'?averagePrimaryM:averageSecondaryM }}%</td>
                                            </tr>
                                            <tr>
                                                <td class="content"></td>
                                                <td class="content" colspan="3">{{ remarks(item) }}</td>
                                                <td class="content">{{ item.Info == 'primary'?meEquipmentM:seEquipmentM }}%</td>
                                            </tr>
                                        </template>
                                    </tbody>
                                </v-simple-table>
                            </v-card>
                        
                        </v-col>
                    </v-row>
                    <v-divider />
                    <br>
                        
                        <!------------------assessment table------------------------- -->
                    <h3>Assessment:</h3>
                    <v-card elevation="2">
                        
                            <v-simple-table
                                class="custom-table"
                            >
                            
                            <tbody v-for="(val, e) in remarksData" :key="e">
                                <tr>
                                    <td style="font-weight: bold;text-align: left !important;background-color: lightgray;">{{ val.Question }}</td>
                                </tr>
                                <tr height="90%">
                                    <td style="white-space: pre-wrap;">{{ val.Remarks }}</td>
                                </tr>
                            </tbody>  
                            </v-simple-table>
                    </v-card>   
                </v-container>
            </v-tab-item>
        </v-tabs-items>
                    </v-card-text>
            </v-card>
        </v-dialog>
            
        

    </div>
  </template>
  
  <script>
  
  import {mapState} from 'vuex'
  import moment from 'moment'
  import axios from 'axios'
  export default {
    name:'EquipmentComponent',
    components:{

    },
    data:()=>({
        equipment_tab:'equip',
        tableData:[],
        reportDialog:false,
        generateDate: null,
        id:null,
        team:null,
        teamName:null,
        year:moment().year(),
        rules:{
            required: [
               (v) => !!v || 'Field is required'
            ],
            existing:[(v) => v !== '1' || "Value '1' is not accepted"],
        },
        snackbar:{
            show: false,
            message: null,
            color: null,
            icon: null,
        },
        timeout:1500,
        monthnow:moment().format('YYYY-MM'),
        totalActual:0,
        totalService:0,
        percentage:0,
    }),
    methods:{

        async openDialog(item){
                this.generateDate = item.Month
                this.team = item.UnitTeamCode
                this.teamName = item.UnitTeamName
                this.reportDialog = true
                this.generate()
                await this.$store.dispatch('diplayRemarks')
                // this.tableContainer = this.lastMonthData
            },
        generate(){
            axios({
                method:'post',
                url:`${this.$baseUrl}api/getEquipment`,
                data:{
                    Team : this.team,
                    Month : this.generateDate
                }
            }).then(r=>{
                if(r.status == 200){
                    this.tableData = r.data
                    this.$store.state.teamData = this.team
                    this.$store.state.monthData = this.generateDate
        
                }
            })
        },
        equip(tab){
            this.equipment_tab = tab
        },
        read(tab){
            this.equipment_tab = tab
            this.$store.dispatch('diplayRemarks','equipment')
        },
        checkRequired(required,actual){
            if(required < actual){
                alert(`Error: The total value of Actual is greater than the Required Value`)
                return true
            }else{
                return false
            }
        },
        calculateActual(item){

            return this.totalActual =  JSON.parse(item).reduce((total,val)=> Number(total) + Number(val.Actual) , 0)
        },
        calculateService(item){
            return this.totalService =  JSON.parse(item).reduce((total, val) => Number(total) + Number(val.Service), 0)
        }, 
        itemPercentage(required,item){
            let data
                data = item / required * 100
                if(data){
                    return this.percentage = this.roundUpValue(data)
                }else{
                    return this.percentage = 0
                }
            
        } ,
       // submit function
       submitData(month,team){
            this.$store.dispatch('diplayRemarks','equipment')
            this.$store.dispatch('getQuestion','Equipment')
            this.$refs.RemarksDialog.openDialog(month,team,'equipment')
        },


        remarks(item){
            let data 
                if(item.Info == 'primary'){
                    data = 'MISSION ESSENTIAL EQUIPMENT (80%)'
                }else{
                    data = 'SECONDARY EQUIPMENT'
                }
            return data
        },


        

        roundUpValue(number) {
            const roundedNumber = Math.round(number * 100) / 100; // Round to 2 decimal places
            return roundedNumber.toFixed(2); // Format to 2 decimal places as a string
        }
    },
    computed:{
        ...mapState([
            'teamItems',
            'psgTable',
            'remarksData',
        ]),
        readinessTable(){
            if(this.tableData.length > 0){
               return this.tableData.reduce((total,item)=>{
                    let index = total.findIndex(res => res.Info == item.Info)
                    if(index == -1){
                        let obj = {...item}
                        obj.totalRequired = Number(obj.Required);
                        obj.Priority = item.Info
                        total.push(obj)
                    }else{
                        total[index].totalRequired += Number(item.Required);
                        item.total = total[index].totalRequired
                        total.push(item)
                    }
                    return total
                },[])
            }else{
                return [];
            }
        },

        totalPrimaryActual() {
        let totalSum = 0;
            for (const item of this.readinessTable) {
                if(item.Info == 'primary'){
                    totalSum += this.calculateActual(item.Details);
                }
            }
            return totalSum;
        },
        totalSecondaryActual() {
        let totalSum = 0;
            for (const item of this.readinessTable) {
                if(item.Info == 'secondary'){
                    totalSum += this.calculateActual(item.Details);
                }
            }
            return totalSum;
        },

        totalPrimaryService() {
        let totalSum = 0;
            for (const item of this.readinessTable) {
                if(item.Info == 'primary'){
                    totalSum += this.calculateService(item.Details);
                }
            }
            return totalSum;
        },
        totalSecondaryService() {
        let totalSum = 0;
            for (const item of this.readinessTable) {
                if(item.Info == 'secondary'){
                    totalSum += this.calculateService(item.Details);
                }
            }
            return totalSum;
        },
        averagePrimary() {
            let totalPercentage = 0;
            let itemCount = 0;
            for (const item of this.readinessTable) {
                if (item.SubMain && item.Info == 'primary') {
                    let percentage = Number(this.itemPercentage(item.Required, this.calculateActual(item.Details)));
                    if(percentage){
                        // console.log(percentage)
                        totalPercentage += percentage
                        itemCount++;
                    }
                }
            }
            if (itemCount === 0) {
                return 0; // To avoid division by zero
            }
            return this.roundUpValue((totalPercentage / itemCount)); // Calculate average and round to 2 decimal places
        },
        averageSecondary() {
            let totalPercentage = 0;
            let itemCount = 0;
            for (const item of this.readinessTable) {
                if (item.SubMain && item.Info == 'secondary') {
                    let percentage = Number(this.itemPercentage(item.Required, this.calculateActual(item.Details)));
                    if(percentage){
                        // console.log(percentage)
                        totalPercentage += percentage
                        itemCount++;
                    }
                }
            }
            if (itemCount === 0) {
                return 0; // To avoid division by zero
            }
            return this.roundUpValue((totalPercentage / itemCount)); // Calculate average and round to 2 decimal places
        },


        averagePrimaryM() {
            let totalPercentage = 0;
            let itemCount = 0;
            for (const item of this.readinessTable) {
                if (item.SubMain && item.Info == 'primary') {
                    let percentage = Number(this.itemPercentage(this.calculateActual(item.Details), this.calculateService(item.Details)));
                    if(percentage){
                        // console.log(percentage)
                        totalPercentage += percentage
                        itemCount++;
                    }
                }
            }
            if (itemCount === 0) {
                return 0; // To avoid division by zero
            }
            return this.roundUpValue((totalPercentage / itemCount)); // Calculate average and round to 2 decimal places
        },
        averageSecondaryM() {
            let totalPercentage = 0;
            let itemCount = 0;
            for (const item of this.readinessTable) {
                if (item.SubMain && item.Info == 'secondary') {
                    let percentage = Number(this.itemPercentage(this.calculateActual(item.Details), this.calculateService(item.Details)));
                    if(percentage){
                        // console.log(percentage)
                        totalPercentage += percentage
                        itemCount++;
                    }
                }
            }
            if (itemCount === 0) {
                return 0; // To avoid division by zero
            }
            return  this.roundUpValue((totalPercentage / itemCount)); // Calculate average and round to 2 decimal places
        },

        meEquipment(){
            let data = this.averagePrimary * 0.8
            return this.roundUpValue(data)
        },
        seEquipment(){
            let data = this.averageSecondary * 0.2
            return this.roundUpValue(data)
        },
        meEquipmentM(){
            let data = this.averagePrimaryM * 0.8
            return this.roundUpValue(data)
        },
        seEquipmentM(){
            let data = this.averageSecondaryM * 0.2
            return this.roundUpValue(data)
        },

        readinessRatingE(){
            return Number(this.meEquipment) + Number(this.seEquipment)
        },
        readinessRatingM(){
            return Number(this.meEquipmentM) + Number(this.seEquipmentM)
        },
        
        
        

         // function to show month name
         formattedDate() {
                if (this.generateDate) {
                    const date = new Date(this.generateDate);
                    const month = date.toLocaleString('default', { month: 'long' });
                    const year = date.getFullYear();
                    return `${month} ${year}`;
                }
                return null;
        },
        // to check if the unit is submit
        submitted(){
            if(this.tableData.length > 0){
                return this.tableData.find(res=> res.Submit == '1')
            }else{
                return 0
            }
        },
        //unit name
        unitName(){
            let data
            this.teamItems.filter(r=>{
                if(r.UnitTeamCode == this.team){
                    data = r.UnitTeamName
                }
            })
            return data
        },
        
        yearItems(){
            const tempData = []
            const currentYear = moment().year();
            const endYear = currentYear + 5;
  
            for (let year = 2020; year <= endYear; year++) {
                tempData.push(year);
            }
  
            return tempData;
  
        }
    },
    mounted(){
        this.$store.dispatch('getTeam')
    },
    watch:{
        tableData(val){
            if(val){
                this.$store.dispatch('getPsgTable')
            }
            
        },
        trainingTableUpdate(val){
            if(val){
                this.getTrainingData()
            }
        },
        // readinessTable(val){
        //     console.log(val,'read')
        // }
    }
  }
  </script>
  <style>
  .v-slide-group__content {
    justify-content:center;
  }
  .v-data-table > .v-data-table__wrapper > table > thead > tr > th{
    font-size: 14px !important; 
    background-color: #0275d8 !important;
    text-align: center !important;
    height:50px; /* height para sa Header*/
    color:white !important;
    font-weight: bold !important;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td,
  .v-data-table > .v-data-table__wrapper > table > tfoot > tr > th {
    font-size: 14px ;
    text-align: center;
  }
  .v-data-table > .v-data-table__wrapper > table {
    text-align:center; /* text naka center*/
  }
  .text-center .v-text-field__slot input{
    text-align: center !important;
  }
  .text-center .v-select__slot input{
    text-align: center !important;
  }
  
  .custom-container {
    margin-bottom: -10;
  }
  
  .custom-container .v-input__control {
    padding-top: -10px; /* Adjust top padding */
    padding-bottom: -10px; /* Adjust bottom padding */
  }
  
  .custom-container .v-label {
    margin-bottom: 1px; /* Adjust bottom margin */
  }
  .background{
    background-color: yellow;
  }
  .editable{
    display: block;
    width: 100%;
    text-align: center;
  }
  .content{
    font-weight:bold;
    font-size:15px !important;
  }
  </style>