<template>
    <div>
        <h1><center>User Information</center></h1>
        <v-divider />
        <br/>
        <v-row>
            
            <v-col cols="12" md="3" lg="3" xl="3">
                <v-card elevation="3" class="justify-center" >
                    <v-card-title style="background-color: #0275d8;color:white;"><center>CONTROL PANEL</center></v-card-title>
                    <v-container>
                        <br>
                        <v-row class="d-flex justify-center" >
                            <v-radio-group v-model="userRadio" row v-show="!edit">
                                <v-radio label="Add" value="add"></v-radio>
                                <v-radio label="Search" value="search"></v-radio>
                              
                            </v-radio-group>
                        </v-row>
                        <v-form ref="add" @submit.prevent="add">

                            <v-autocomplete 
                                label="Category"
                                item-text="CategoryName"
                                item-value="CategoryCode"
                                dense
                                outlined
                                :items="categoryItems"
                                v-model="category"
                                @change="getDepartment(),searchUser()"
                                :rules="rules.required"
                                clearable
                            />
                            <v-autocomplete 
                                label="Unit"
                                item-text="UnitDepartmentName"
                                item-value="UnitDepartmentCode"
                                dense
                                outlined
                                :items="departmentItems"
                                v-model="department"
                                @change="getSection(),searchUser()"
                                :rules="rules.required"
                                clearable
                   
                            />
                            <v-autocomplete 
                                label="Sub-Unit"
                                item-text="UnitSectionName"
                                item-value="UnitSectionCode"
                                dense
                                outlined
                                :items="sectionItems"
                                v-model="section"
                                @change="getTeam(),searchUser()"
                                :rules="rules.required"
                                clearable
                            
                            />
                            <v-autocomplete 
                                label="Offices"
                                item-text="UnitTeamName"
                                item-value="UnitTeamCode"
                                dense
                                outlined
                                :items="teamItems"
                                v-model="team"
                                :rules="rules.required"
                                clearable
                                v-if="category == 'CA01'"
                                @change="searchUser()"
                            />
                            <v-text-field
                                label="Name"
                                v-model="name"
                                outlined
                                dense
                                :rules="rules.required"
                                clearable
                                @click="!name && userRadio == 'add'?dialogAddName = true:''"
                            />
                            <!-- <v-autocomplete
                                label="Branch of service"
                                v-model="service"
                                :items="serviceItems"
                                item-text="text"
                                item-value="value"
                                outlined
                                dense
                                :rules="rules.required"
                                clearable
                                v-show="userRadio == 'add'"
                            /> -->
                            <v-autocomplete
                                label="Unit Approver"
                                v-model="approver"
                                :items="approverItems"
                                item-text="text"
                                item-value="value"
                                outlined
                                dense
                                :rules="rules.required"
                                clearable
                                v-show="userRadio == 'add'"
                            />
                            <v-autocomplete
                                label="Office Admin"
                                v-model="restriction"
                                :items="restrictionItems"
                                item-text="text"
                                item-value="value"
                                outlined
                                dense
                                :rules="rules.required"
                                clearable
                                v-show="userRadio == 'add'"
                            />
                            <v-text-field
                                label="Email Address"
                                v-model="email"
                                outlined
                                dense
                                :rules="rules.required"
                                v-show="userRadio == 'add'"
                            />
                            <v-text-field
                                label="Username"
                                v-model="username"
                                outlined
                                dense
                                :rules="rules.required"
                                @blur="generatePassword()"
                                v-show="userRadio == 'add'"
                                maxlength="10"
                                hint="Maximum of 10 characters"
                            />
                            <v-text-field
                                label="Password"
                                v-model="password"
                                outlined
                                dense
                                :rules="rules.required"
                                v-show="userRadio == 'add'"
                                maxlength="6"
                                hint="Maximum of 6 characters"
                            />
                            <v-btn block color="success" type="submit" v-show="userRadio == 'add' && !edit">ADD</v-btn>
                            <v-btn block color="success" type="submit" v-show="edit">SAVE</v-btn>
                            <v-btn block color="primary"  v-show="userRadio == 'search'" @click="searchUser()">SEARCH</v-btn>
                        </v-form>
                    </v-container>
                </v-card>
            </v-col>
            <v-col cols="12" md="9" lg="9" xl="9" >
                <v-card>
                    
                    <v-simple-table fixed-header height="80vh" class="elevation-1 custom-table">
                        <thead>
                            <tr>
                                <th>No.</th>
                                <th>Unit</th>
                                <th>Sub-Unit</th>
                                <th>Office</th>
                                <th>Name</th>
                                <th>Designation</th>
                                <th>Username</th>
                                <th>Password</th>
                                <th>No</th>
                                <th>AccessRight</th>
                                <th>Logs</th>
                                <th>Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(item,i) in tableData" :key="i">
                                <td>{{ i + 1 }}</td>
                                <td>{{ item.UnitDepartmentAbr }}</td>
                                <td>{{ item.UnitSectionName.toUpperCase() }}</td>
                                <td>{{ item.UnitTeamName }}</td>
                                <td>{{ item.Name }}</td>
                                <td>{{ designation(item) }}</td>
                                <td>{{ item.Username }}</td>
                                <td>{{ item.Password }}</td>
                                <td>{{ item.Office }}</td>
                                <td style="text-align: center;">
                                        <v-switch
                                            :value="item.id"
                                            inset
                                            v-model="rights"
                                            @change="addRights(item)"
                                        >
                                        </v-switch>
                                
                                </td>
                                <td><v-btn icon><v-icon color="success" @click="viewLogs(JSON.parse(item.Log),item.Name)">mdi-eye</v-icon></v-btn></td>
                                <td><v-btn icon  @click="dialogActionOpen(item.id)"><v-icon color="warning">mdi-pencil-circle-outline</v-icon></v-btn></td>
                            </tr>
                        </tbody>
                        
                    </v-simple-table>
                    <v-overlay :value="overlay" absolute>
                        <v-progress-circular
                            indeterminate
                            size="64"
                        ></v-progress-circular>
                    </v-overlay>
                </v-card>
                
            </v-col>
            
        </v-row>
        <!-- dialog for logs -->
        <v-dialog v-model="logDialog" persistent width="600px">
            <v-card>
                <v-card-title style="background-color: #0275d8;color:white;">{{ dialogName }}
                    <v-spacer /><v-btn  @click="logDialog = false" icon><v-icon color="white">mdi-close-circle-outline</v-icon></v-btn>
                </v-card-title>
                <br />
                <v-card-text>
                    <v-simple-table>
                        <thead>
                            <tr>
                                <th>LOGIN</th>
                                <th>LOGOUT</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(val,i) in logTable" :key="i">
                                <td>{{ val.Login }}</td>
                                <td>{{ val.Logout }}</td>
                            </tr>
                        </tbody>
                    </v-simple-table>
                </v-card-text>
            </v-card>
        </v-dialog>
        <!-- dialog for adding name -->
        <v-dialog v-model="dialogAddName" width="600px" persistent> 
            <v-card>
                <v-card-title style="background-color: #0275d8;color:white;">Name Details
                    <v-spacer /><v-btn  @click="dialogAddName = false" icon><v-icon color="white">mdi-close-circle-outline</v-icon></v-btn>
                </v-card-title>
                <v-card-text>
                    <br>
                    
                            <v-form ref="addName" @submit.prevent="addName">
                                <div class="d-flex align-center">
                                    <v-row>
                                        <v-select 
                                            v-model="selectRank" 
                                            dense 
                                            outlined
                                            label="Rank"
                                            class="custom-select"
                                            :rules="rules.required"
                                            :items="rankItems"
                                            item-text="Rank"
                                            item-value="Rank"
                                        />
                                        <v-text-field 
                                            v-model="nameData" 
                                            label="Name"
                                            dense 
                                            outlined
                                            :rules="rules.required"

                                        />
                                        <v-select 
                                            v-model="selectService" 
                                            label="Service"
                                            dense 
                                            outlined
                                            class="custom-select"
                                            :rules="rules.required"
                                            :items="serviceItems"
                                        />
                                        <v-btn type="submit" color="success" >SAVE</v-btn>
                                    </v-row>
                                </div>
                            </v-form>
                        
                    
                </v-card-text>
            </v-card>
        </v-dialog>
        <!-- dialog for actions -->
        <v-dialog persistent v-model="dialogAction" width="250px">
            <v-card>
                <v-card-title style="background-color: #0275d8;color:white;">Actions
                    <v-spacer /><v-btn  @click="dialogAction = false" icon><v-icon color="white">mdi-close-circle-outline</v-icon></v-btn>
                </v-card-title>
                <v-card-text>
                    <br />
                    <v-row>
                        <v-btn block color="warning" @click="editUser(idValue)" dark>EDIT</v-btn>
                        <v-btn block color="error" @click="deleteUser(idValue)" dark class="mt-3">DELETE</v-btn>
                    </v-row>
                    
                </v-card-text>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
import axios from 'axios'
import {mapState} from 'vuex';
    export default {
        data() {
            return {
                userRadio:'add',
                category:null,
                categoryName:null,
                categoryItems:[],
                division:null,
                department:null,
                departmentItems:[],
                section:null,
                newSectionName:null,
                sectionItems:[],
                team:null,
                newTeamName:null,
                teamItems:[],
                service:null,
                approver:null,
                // approverItems:[
                //     {text:'Encoder/Drafter',value:1},
                //     {text:'OIC',value:2},
                //     {text:'EX-O/COS',value:3},
                //     {text:'DEPUTY',value:4},
                //     {text:'COMMANDER',value:5},
                // ],
                restriction:null,
                restrictionItems:[
                    {text:'NO RESTRICTION',value:'-'},
                    {text:'1-PERSONNEL',value:1},
                    {text:'8-TRAINING',value:8},
                    {text:'4-EQUIPT&FAC',value:4},
                    {text:'6-COMMUNICATION',value:6},
                    {text:'3-ALL',value:3},
                ],
                tableData:[],
                name:null,
                username:null,
                password:null,
                email:null,
                rules:{
                    required: [
                        v => !!v || 'Field is required'
                    ],
                },
                overlay:false,
                rights:[],
                logDialog:false,
                dialogName:null,
                dialogAddName:false,
                logTable:[],
                selectRank:null,
                selectService:null,
                nameData:null,
                dialogAction:false,
                idValue:null,
                edit:false
            }
        },
        methods:{
            decryptData(encryptedData) {
                const decryptedData = atob(encryptedData);
                return decryptedData;
            },
            getCategory(){
                this.tableData = []
                this.department = null
                this.section = null
                this.team = null
                axios({
                    method:'get',
                    url:`${this.$baseUrl}api/getCategory`,
                }).then(r=>{
                    this.categoryItems = r.data
                })
            },
            getDepartment(){
                this.tableData = []
                this.section = null
                this.team = null
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/getDepartment`,
                    data:{
                        data : this.category,
                        list : this.departmentRadio
                    }
                }).then(r=>{
                    this.departmentItems = r.data
                })
            },
            getSection(){
                this.tableData = []
                this.team = null
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/getSection`,
                    data:{
                        data : this.department,
                        list : this.sectionRadio
                    }
                }).then(r=>{
                    this.sectionItems = r.data
                })
            },
            getTeam(){
                this.tableData = []
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/getTeam`,
                    data:{
                        data : this.section,
                        list : this.radioTO
                    }
                }).then(r=>{
                    this.teamItems = r.data
                })
            },
            generatePassword() {
            const charset = "abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789";
                let password = "";
                    for (let i = 0; i < 7; i++) {
                        const randomIndex = Math.floor(Math.random() * charset.length);
                        password += charset[randomIndex];
                    }
                    if(this.username){
                        this.password = password
                    }else{
                        this.password = null
                    }
                    
                return this.password
            },
            add(){
                if(this.$refs.add.validate()){
                    if(!this.edit){
                        axios({
                        method:'post',
                        url:`${this.$baseUrl}api/insertUser`,
                        data:{
                            category :this.category,
                            department : this.department,
                            section : this.section,
                            team : this.category == 'CA01'?this.team:null,
                            name : this.name.toUpperCase(),
                            approver : this.approver,
                            username : this.username,
                            password : this.password,
                            email : this.email,
                            restriction : this.restriction,
                            updatedBy : this.$store.state.user.name
                        }
                        }).then(res=>{
                            if(res.status == 200){
                                // alert('inserted')
                                this.name = null
                                this.username = null
                                this.password = null
                                this.getUser()
                            }
                        })
                    }else{
                        
                        axios({
                        method:'post',
                        url:`${this.$baseUrl}api/updateUser`,
                        data:{
                            id : this.idValue,
                            category :this.category,
                            department : this.department,
                            section : this.section,
                            team : this.category == 'CA01'?this.team:null,
                            name : this.name.toUpperCase(),
                            approver : this.approver,
                            email : this.email,
                            restriction : this.restriction,
                            username : this.username,
                            password : this.password,
                            updatedBy : this.$store.state.user.name
                        }
                        }).then(res=>{
                            if(res.status == 200){
                                
                                this.$refs.add.reset()
                                this.edit = false
                                alert('The user was updated successfully!')
                                this.overlay = false
                                this.getUser()
                            }
                        })
                    }
                    
                }
            },
            searchUser(){
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/searchUser`,
                    data:{
                        category :this.category,
                        department : this.department,
                        section : this.section,
                        team : this.team,
                        name : this.name
                    }
                }).then(res=>{
                    if(res.status == 200){
                        this.tableData = []
                        const decryptedData = this.decryptData( res.data);
                        const tempData = JSON.parse(decryptedData);
                        tempData.filter(r=>{
                            if(r.Username != 'ceo'){
                                this.tableData.push(r) 
                            }
                        })
                    }
                })
            },
            getUser(){
                
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/getUser`,
                }).then(res=>{
                    if(res.status == 200){
                        this.tableData = []
                        const decryptedData = this.decryptData( res.data);
                        const tempData = JSON.parse(decryptedData);
                        tempData.filter(r=>{
                            if(r.Username != 'ceo'){
                                this.tableData.push(r) 
                            }
                            if(r.AccessRight){
                                this.rights.push(r.id)
                            }
                        })
                    }
                    // console.log(this.tableData)
                })
            },
            addName(){
                if(this.$refs.addName.validate()){
                    this.name = `${this.selectRank} ${this.nameData.toUpperCase()} ${this.selectService}`
                    this.dialogAddName = false
                }
            },
            addRights(item){
                let addRights
                if(item.AccessRight){
                    addRights = null
                }else{
                    addRights = 1
                }

                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/updateRights`,
                    data:{
                        id : item.id,
                        addRights : addRights
                    }
                }).then(async(r)=>{
                    if(r.status == 200){
                        this.getUser()
                    }

                })
            },
            designation(item){
                if(item.Approver == 1){
                    return 'Encoder'
                }else if(item.Approver == 2){
                    return 'OIC'
                }else if(item.Approver == 3){
                    return item.UnitDepartmentCode != 'D01'?'CHIEF OF STAFF':'EX-O'
                }else if(item.Approver == 4){
                    return 'DEPUTY'
                }else{
                    return item.UnitDepartmentCode != 'D01'?'COMMANDER':item.UnitTeamAbr
                }
            },
            viewLogs(item,name){
                this.dialogName = name
                this.logDialog = true
                const itemCopy = [...item];
                // Assuming 'Login' is a numerical property, convert it to a number before sorting
                this.logTable = itemCopy.sort((a, b) => new Date(b.Login) - new Date(a.Login));
                // this.logTable
            },
            dialogActionOpen(val){
                this.dialogAction = true
                this.idValue = val
            },
            deleteUser(item){
                const confirmDelete = confirm("Are you sure you want to delete?");
                if(confirmDelete){
                    axios({
                        method:'post',
                        url:`${this.$baseUrl}api/deleteUser`,
                        data:{
                            id : item
                        }
                    }).then(res=>{
                        if(res.status == 200){
                            this.getUser()
                        }
                    })
                }
            },
            editUser(item){
                const confirmEdit = confirm("Are you sure you want to edit?");
                if(confirmEdit){
                    this.edit = true
                    this.overlay = true
                    console.log(item)
                    axios({
                        method:'post',
                        url:`${this.$baseUrl}api/editUser`,
                        data:{
                            id : item
                        }
                    }).then(res=>{
                        if(res.status == 200){
                            const decryptedData = this.decryptData( res.data);
                            const val = JSON.parse(decryptedData);
                            this.idValue = val.id
                            this.name = val.Name
                            this.email = val.Email
                            this.username = val.Username
                            this.password = val.Password
                            this.dialogAction = false
                        
                        }
                    })
                }
            }

        },
        computed:{
            ...mapState([
                'rankItems',
                'serviceItems'
            ]),
            approverItems(){
                let arr
                if(['T01','T02','T03'].includes(this.team)){
                    arr = [
                        {text:'Encoder/Drafter',value:1},
                        {text:'OIC',value:5},
                    
                    ]
                }else{
                    arr = [
                        {text:'Encoder/Drafter',value:1},
                        {text:'OIC',value:2},
                        {text:'EX-O/COS',value:3},
                        {text:'DEPUTY',value:4},
                        {text:'COMMANDER',value:5},
                    ]
                }
                return arr
     
            }
                    
               
        },
        mounted(){
            this.getCategory()
            this.getUser()
        },
        watch:{
            category(val){
                if(!val){
                    this.getUser() 
                }
            },
            dialogAddName(val){
                if(val){
                    this.$store.dispatch('getRank')
                }
            }
        }
    }
</script>

<style scoped>
.d-flex {
    display: flex;
  }
  
.align-center {
    align-items: center;
}
.custom-select {
    width: 5px; /* Adjust the width as per your preference */
}
</style>