<template>
    <div>

    </div>
</template>

<script>
    import pdfMake from 'pdfmake/build/pdfmake';
    import pdfFonts from 'pdfmake/build/vfs_fonts';
    pdfMake.vfs = pdfFonts.pdfMake.vfs;
    export default {
        data(){
            return{
                imageDataUrl:''
            }
        },
        methods:{
            redCon(data){
                // console.log(val,'val')
                let val
                if(data <= 100 && data >= 85){
                    val = 'R1 - Fully Mission Capable'
                }
                else if(data <= 84 && data >= 75){
                    val = 'R2 - Mission Capable'
                }
                else if(data <= 74 && data >= 51){
                    val = 'R3 - Partially Mission Capable'
                }
                else{
                    val = 'R4 - Not Mission Capable'
                }
               
                return val;
            },
            redConStyle(val){
                let style
                if(val.includes('R1')){
                    style = 'redCon1'
                }
                else if(val.includes('R2')){
                    style = 'redCon2'
                }
                else if(val.includes('R3')){
     
                    style = 'redCon3'
                }
                else{
                    style = 'redCon4'
                }
                return style
            },
            async print(data){
                Promise.all([this.rating(data),this.read(data)]).then(function(){
                    alert('Files are printed!')
                })
            },
           async rating(data) {
            let tableBody = [];
            data.tableData.forEach((item,i) => {
                // console.log(item)
                const row = [
                    { 
                        text: item.Detail, colSpan: item.Title? 5 : 1 ,
                        fontSize:item.Title == '1'?10:item.Title == '2'?9:8,
                        bold:item.Title == '1'?true:false
                    },
                    !item.Title ? { text: item.Required || '',style:'body'} : '',
                    !item.Title ? { text: item.Actual || '',style:'body'} : '',
                    !item.Title ? { text:item.Rating ? item.Rating.toFixed(2) + '%' : '',style:'body'} : '',
                    !item.Title ? { text:item.overall ? item.overall.toFixed(2) + '%' : '',style:'body'} : '',
                    item.count > 0 ? { text: (item.facilityRating ? item.facilityRating.toFixed(2) + '%' : ''),
                                    alignment:'center',
                                    margin: [0, (item.count > 1 ? (item.count - 1) * 5 : 0)], // Adjust the margin based on the count
                                    lineHeight: (item.count > 1 ? 1.5 : 1),
                                    rowSpan: item.count } : '',
                    item.count > 0 ? { text: item.facilityRating?this.redCon(item.facilityRating):'',
                                    rowSpan: item.count,
                                    alignment:'center',
                                    margin: [0, (item.count > 1 ? (item.count - 1) * 5 : 0)], // Adjust the margin based on the count
                                    lineHeight: (item.count > 1 ? 1.5 : 1),
                                    style:this.redConStyle(this.redCon(item.facilityRating))
                                    } : ''
                ];
                tableBody.push(row);

                if (!item.count && (!data.tableData[i + 1] || data.tableData[i + 1].count)) {
                    const avgRow = [
                        { text: 'Average Rating', bold: true, fontSize: 10, colSpan: 3 },
                        {},
                        {},
                        { text: item.ratingQuanPercent ? item.ratingQuanPercent.toFixed(2) + '%' : '', bold: true, fontSize: 10 },
                        { text: item.ratingQualPercent ? item.ratingQualPercent.toFixed(2) + '%' : '', bold: true, fontSize: 10 },
                        {},
                        {}
                    ];
                    tableBody.push(avgRow);
                }
            });

            const docDefinition = {
                content: [
                    {
                        margin: [140, 0, 0, 0],
                        columns: [
                            {
                                image: this.imageDataUrl, // Replace with the path to your image
                                width: 50, // Adjust the width as needed
                                alignment: 'right', // Adjust the alignment as needed
                                margin: [0, 0, 0, 0], // Adjust the margins as needed
                            },
                            {
                            width: 'auto', // Adjust the width as needed
                                text: [
                                    { text: data.teamName, alignment: 'center', fontSize: 15, margin: [0, 0, 0, 2] },
                                    "\n",
                                    { text: 'PERSONNEL READINESS REPORT', alignment: 'center', fontSize: 13, margin: [0, 0, 0, 2] },
                                    "\n",
                                    { text: data.month, alignment: 'center', fontSize: 10, margin: [0, 0, 0, 2] },
                                ],
                                
                            },
                        ],
                    },
                    {
                        table: {
                            headerRows: 2,
                            widths: ['auto', 'auto', 'auto', 'auto', 'auto', 'auto', 'auto'], // Adjust column widths as needed
                            body: [
                                [
                                    { text: 'Facility', style: 'tableHeader',  },
                                    { text: 'Quantitative', style: 'tableHeader', colSpan: 3 },
                                    {},
                                    {},
                                    { text: 'Qualitative', style: 'tableHeader' },
                                    { text: 'Rating', style: 'tableHeader',  },
                                    { text: 'REDCON', style: 'tableHeader',  }
                                ],
                                [
                                    {},
                                    { text: 'Required', style: 'tableHeader' },
                                    { text: 'Actual', style: 'tableHeader' },
                                    { text: 'Rating', style: 'tableHeader' },
                                    { text: 'Rating', style: 'tableHeader' },
                                    {},
                                    {}
                                ],
                                ...tableBody
                              
                            ],
                            layout: {
                                fillColor: 'autoTableAlternate',
                                hLineWidth: function (i, node) {
                                return (i === 0 || i === 1 || i === node.table.body.length) ? 1 : 0;
                                },
                                vLineWidth: function () {
                                return 0;
                                },
                                hLineColor: function (i) {
                                return i === 1 ? '#000000' : '#00000000';
                                },
                                margin: [0, 0, 0, 10]
                            }
                        }
                    }
                ],
                
                pageOrientation: 'portrait',
                styles: {
                    tableHeader: {
                        bold: true,
                        alignment: 'center',
                        fontSize:10,
                    },
                    body:{
                        alignment: 'center',
                        fontSize:8,
                        margin: [0, 5],
                    },
                    redCon1: { color: "green" }, // Define styles as needed
                    redCon2: { color: "blue" },
                    redCon3: { color: "orange" },
                    redCon4: { color: "red" },
                },
                footer(currentPage, pageCount) {
                    return { text: `Page ${currentPage.toString()} of ${pageCount.toString()}`, alignment: 'right', margin: [50, 0],fontSize: '6', };
                },
            };

            // Generate the PDF
            pdfMake.createPdf(docDefinition).download('Facility Rating.pdf');
            },
        

        

            async read(data){
        
                    let remarksBody = [];
                    
                    if (data.assesment) {
                        data.remarksData.forEach(row => {
                            if (row.Question) {
                                let question = [
                                    { text: row.Question || '', alignment: 'left', fontSize: 10, fillColor: 'lightgrey', bold: true },
                                ];
                                remarksBody.push(question);
                            }
                            if (row.Remarks) {
                                let remarks = [
                                    { text: row.Remarks || '', alignment: 'left', fontSize: 10 }
                                ];
                                remarksBody.push(remarks);
                            }
                        });
                    }
                    let tableBody = [];
                        data.report.forEach(row => {
                           
                            let rowData = [
                                { text: row.Detail || '', alignment: 'center', fontSize: 10 },
                                { text: row.Required || '', alignment: 'center', fontSize: 10 },
                                { text: row.Actual || '', alignment: 'center', fontSize: 10 },
                                { text: row.Quantitative?row.Quantitative.toFixed(2)+'%':'' || '', alignment: 'center', fontSize: 10 },
                                { text: row.Qualitative?row.Qualitative.toFixed(2)+'%':'' || '', alignment: 'center', fontSize: 10 },
                                { text: row.total?row.total.toFixed(2)+'%':'' || '', alignment: 'center', fontSize: 10 },
                                { text: row.weight?row.weight.toFixed(2)+'%':'' || '', alignment: 'center', fontSize: 10 }
                            ];

                            tableBody.push(rowData);
                        });
                    let docDefinition = {
                    content: [
                        {
                                margin: [80, 0, 0, 0],
                                columns: [
                                    {
                                        image: this.imageDataUrl, // Replace with the path to your image
                                        width: 50, // Adjust the width as needed
                                        alignment: 'right', // Adjust the alignment as needed
                                        margin: [0, 0, 0, 0], // Adjust the margins as needed
                                    },
                                    {
                                    width: 'auto', // Adjust the width as needed
                                        text: [
                                            { text: data.teamName, alignment: 'center', fontSize: 15, margin: [0, 0, 0, 2] },
                                            "\n",
                                            { text: 'FACILITY READINESS REPORT', alignment: 'center', fontSize: 13, margin: [0, 0, 0, 2] },
                                            "\n",
                                            { text: data.month, alignment: 'center', fontSize: 10, margin: [0, 0, 0, 2] },
                                        ],
                                        
                                    },
                                ],
                        },
                        {
                            table: {
                            headerRows: 1,
                            widths: ['20%', '10%', '10%', '10%', '10%', '10%', '30%'],
                            body: [
                                    [
                                        { text: "Category \n (a)", style: 'tableHeader' },
                                        { text: "Required \n Area \n (b)", style: 'tableHeader' },
                                        { text: "Actual \n Area \n (c)", style: 'tableHeader' },
                                        { text: "Quantitative \n (c/d x 100) \n (d)", style: 'tableHeader' },
                                        { text: "Qualitative \n (e)", style: 'tableHeader' },
                                        { text: "Total \n (d + e) \n /2", style: 'tableHeader' },
                                        { text: "Percentage \n (Weighted \n distribution)", style: 'tableHeader' },
                                    ],
                                   ...tableBody,
                                    [ 
                                        { text: 'TOTAL' ,alignment:'right',bold:true,colSpan:6},
                                        { },
                                        { },
                                        { },
                                        { },
                                        { },
                                        { text:data.readiness.toFixed(2)+'%',bold:true,alignment: 'center',},
                                    ],
                                    [ 
                                        { text: 'REDCON STATUS' ,alignment:'right',bold:true,colSpan:6},
                                        { },
                                        { },
                                        { },
                                        { },
                                        { },
                                        { text: this.redCon(data.readiness),style: this.redConStyle(this.redCon(data.readiness)),fontSize: 10,alignment: 'center'},
                                    ],
                                    
                                ],
                              
                            }
                        },
                        {},
                        { text: 'Assessment:',alignment: 'left', fontSize: 15,bold:true,margin: [0, 20, 0, 0]},
                        {
                            table: {
                             
                                widths: ['*'],
                                body: remarksBody
                            }
                        },
                        {},
                        {
                            margin: [0, 70, 0, 0],
                            columns:[
                                
                                {
                                    // margin: [90, 0, 0, 0],
                                    text: [
                                        { text: 'PREPARED BY:',alignment: 'center', fontSize: 12},
                                        "\n",
                                        "\n",
                                        "\n",
                                        "\n",
                                        { text: data.sign.PreparedBy,alignment: 'center', fontSize: data.sign.PreparedBy.length < 23?12:10,bold:true,margin: [0, 0, 0, 0]},
                                        "\n",
                                        // { text: 'UNIT',alignment: 'center', fontSize: 12,bold:true,margin: [0, 0, 0, 0]},
                                    ],
                                
                                },
                                {
                                    // margin: [0, 0, 90, 0],
                                    text: [
                                        { text: 'NOTED BY:',alignment: 'center', fontSize: 12},
                                        "\n",
                                        "\n",
                                        "\n",
                                        "\n",
                                        { text:data.sign.NotedBy,alignment: 'center', fontSize: data.sign.NotedBy && data.sign.NotedBy.length < 23?12:10,bold:true,margin: [0, 0, 80, 0]},
                                        "\n",
                                        // { text: 'UNIT',alignment: 'center', fontSize: 12,bold:true,margin: [90, 0, 0, 0]},
                                    ],
                                },
                                {
                                    // margin: [0, 0, 0, 90],
                                    text: [
                                        { text: 'APPROVED BY:',alignment: 'center', fontSize: 12},
                                        "\n",
                                        "\n",
                                        "\n",
                                        "\n",
                                        { text: data.sign.ApprovedBy,alignment: 'center', fontSize: data.sign.ApprovedBy && data.sign.ApprovedBy.length < 23?12:10,bold:true,margin: [0, 0, 0, 0]},
                                        "\n",
                                        // { text: 'UNIT',alignment: 'center', fontSize: 12,bold:true,margin: [0, 0, 0, 0]},
                                    ]
                                }
                            ],
                        }
                    ],
                    styles: {
                        tableHeader: {
                            fontSize: 10,
                            bold: true,
                            // fillColor: '#0275d8',
                            fillColor: 'lightgray',
                            alignment: 'center',
                        },
                        percent:{
                            alignment: 'center',
                            fontSize: 10,
                            bold: true,
                        },
                        percentTitle:{
                            alignment: 'center',
                            bold: true,
                            fontSize: 10,
                            fillColor: 'lightgrey'
                        },
                        redCon1: { color: "white",fillColor: 'green'}, // Define styles as needed
                        redCon2: { color: "white",fillColor: 'blue' },
                        redCon3: { fillColor: 'yellow' },
                        redCon4: { color: "white",fillColor: 'red' },
                    },
                }
                if (remarksBody.length === 0) {
                    docDefinition.content.splice(-1); // Remove the empty table from the content
                }
                pdfMake.createPdf(docDefinition).download('FRR.pdf');
            }
        },
        computed:{
           
        },
        mounted() {
            // Import the image using the 'import' statement or 'require' if you're using webpack
           

            // Create a function to convert the image to a data URL
            let imageSrc = require(`../../../img/${this.$store.state.user.main}.png`)
            const getImageDataUrl = (imageSrc, callback) => {
            const img = new Image();
            img.crossOrigin = "Anonymous"; // Enable CORS for the image if it's hosted on a different domain
            img.onload = () => {
                const canvas = document.createElement("canvas");
                canvas.width = img.width;
                canvas.height = img.height;
                const ctx = canvas.getContext("2d");
                ctx.drawImage(img, 0, 0);
                const dataUrl = canvas.toDataURL("image/png"); // Replace 'image/png' with the appropriate MIME type if needed
                callback(dataUrl);
            };
            img.src = imageSrc;
            };

            // Call the function to get the data URL
            getImageDataUrl(imageSrc, (dataUrl) => {
            this.imageDataUrl = dataUrl;
            });
        },
    }
</script>

