<template>
    <div>
        <h1><center>Branch/Rank</center></h1>
        <br>
        <v-divider/>
        <br>
        <v-row>
            <v-col class="shrink" cols="12" md="3" lg="3" xl="3">
                <!-- expand -->
                <v-btn
                  color="success"
                  @click="toggleExpand()"
                  block
                  :disabled="disableView"
                >
                  ADD DATA TABLE
                </v-btn>
                    <!-- expand branch-->
                    <v-expand-transition>
                        <v-btn
                            v-show="expand && !expandBranch" 
                            color="primary"
                            @click="toggleBranch()"
                            block
                        >
                            VIEW BRANCH/POSITION
                        </v-btn>
                    </v-expand-transition>
                    <!-- expand rank-->
                    <v-expand-transition>
                        <v-btn
                            v-show="expand && !expandRank"
                            color="primary"
                            @click="toggleRank()"
                            block
                        >
                            VIEW RANK
                        </v-btn>
                    </v-expand-transition>
                    <!-- expand unit-->
                    <v-expand-transition>
                        <v-btn
                            v-show="expand && !expandUnit"
                            color="primary"
                            @click="toggleUnit()"
                            block
                        >
                            ADD UNIT
                        </v-btn>
                    </v-expand-transition>
                    <!-- expand required-->
                    <!-- <v-expand-transition>
                        <v-btn
                            v-show="expand && !expandRequired"
                            color="primary"
                            @click="toggleRequired()"
                            block
                        >
                            VIEW REQUIRED
                        </v-btn>
                    </v-expand-transition> -->
                <!-- expand vcard branch-->
                <v-expand-transition>
                  <v-card v-show="expandBranch" elevation="5">
                    <v-card-title class="justify-center">VIEW BRANCH/POSITION</v-card-title>
                        <v-container>
                            <div class="d-flex justify-center">
                                <v-radio-group v-model="branchRadio" row>
                                    <v-radio
                                        label="UPDATE"
                                        value="update"
                                    ></v-radio>
                                    <v-radio
                                        label="ADD"
                                        value="add"
                                    ></v-radio>
                                </v-radio-group>
                            </div>
                            <v-autocomplete
                                clearable
                                outlined
                                dense
                                label="Division"
                                :items="divisionItems"
                                v-model="divisionData"
                                item-text="DivisionName"
                                item-value="DivisionCode"
                                @change="changeDivision()"
                            />
                            <v-autocomplete
                                
                                clearable
                                outlined
                                dense
                                label="Branch"
                                :items="filterDivisionItems"
                                v-model="branchData"
                                item-text="BranchName"
                                item-value="BranchCode"
                                @change="changeBranch()"
                            />
                            <v-autocomplete
                                v-show="branchRadio == 'update'"
                                clearable
                                outlined
                                dense
                                label="Position"
                                :items="positionItems"
                                v-model="positionData"
                                item-text="PositionName"
                                item-value="PositionCode"
                            />
                            <v-text-field 
                                v-show="branchRadio != 'update' && !branchData && divisionData"
                                label="Add New Branch"
                                dense
                                outlined
                                v-model="addBranch"
                            />
                            <v-text-field 
                                v-show="branchRadio != 'update' && branchData || addBranch"
                                label="Position"
                                dense
                                outlined
                                v-model="addPosition"
                            />
                            <v-btn color="primary" block @click="branchBtn(branchRadio)" 
                                :disabled="branchRadio == 'add' && !addPosition?true:branchRadio == 'update' && !branchData?true:false"
                                >
                                {{ branchRadio == 'update'?'VIEW':'ADD' }}
                            </v-btn>
                            <v-btn block @click="toggleCloseView()">CLOSE</v-btn>
                        </v-container>
                    </v-card>
                </v-expand-transition>
                <!-- expand vcard rank-->
                <v-expand-transition>
                  <v-card v-show="expandRank" elevation="5">
                    <v-card-title class="justify-center">VIEW RANK</v-card-title>
                        <v-container>
                            <div class="d-flex justify-center">
                                <v-radio-group v-model="rankRadio" row>
                                    <v-radio
                                        label="UPDATE"
                                        value="update"
                                    ></v-radio>
                                    <v-radio
                                        label="ADD"
                                        value="add"
                                    ></v-radio>
                                </v-radio-group>
                            </div>
                            <v-row>
                                <v-col cosl="12" md="12" lg="12" xl="12">
                                    <v-autocomplete
                                        clearable
                                        outlined
                                        dense
                                        label="Division"
                                        :items="divisionItems"
                                        v-model="divisionData"
                                        item-text="DivisionName"
                                        item-value="DivisionCode"
                                        @change="changeDivision()"
                                    />
                                </v-col>
                            
                                <v-col cosl="12" md="6" lg="6" xl="6" v-show="!addGrade">
                                    <v-autocomplete
                                    clearable
                                    outlined
                                    dense
                                    label="Grade"
                                    v-model="gradeData"
                                    :items="filterRankItemsMerge"
                                    item-text="Grade"
                                    item-value="Grade"
                                    @change="changeGrade()"
                                />
                                </v-col>
                                <v-col cosl="12" md="6" lg="6" xl="6"  v-show="rankRadio == 'update'">
                                    <v-autocomplete
                                    clearable
                                    outlined
                                    dense
                                    label="Rank"
                                    v-model="rankData"
                                    :items="rankItems"
                                    item-text="Rank"
                                    item-value="Rank"
                                    />
                                </v-col>
                                <v-col cosl="12" md="6" lg="6" xl="6"  v-show="rankRadio != 'update' && !gradeData">
                                    <v-text-field
                                        label="Add new Grade"
                                        dense
                                        outlined
                                        v-model="addGrade"
                                       
                                    />
                                </v-col>
                                <v-col cosl="12" md="6" lg="6" xl="6" v-show="rankRadio != 'update' && addGrade || rankRadio != 'update' && gradeData">
                                    <v-text-field
                                        label="Add new Rank"
                                        dense
                                        outlined
                                        v-model="addRank"
                                    />
                                </v-col>
                            </v-row>
                            <v-btn color="primary" block @click="rankBtn(rankRadio)" 
                                :disabled="rankRadio == 'add' && !addRank?true:rankRadio == 'update' && !divisionData?true:false"
                                >
                                {{ rankRadio == 'update'?'VIEW':'ADD' }}
                            </v-btn>
                            <v-btn block @click="toggleCloseView()">CLOSE</v-btn>
                        </v-container>
                    </v-card>
                </v-expand-transition>
                <!-- expand vcard unit-->
                <v-expand-transition>
                  <v-card v-show="expandUnit" elevation="5">
                    <v-card-title class="justify-center">VIEW UNIT</v-card-title>
                        <v-container>
                            <v-radio-group v-model="categoryRadio" row>
                                <h3>Category : </h3>
                                <v-radio
                                    label="Existing"
                                    value="exist"
                                ></v-radio>
                                <v-radio
                                    label="New"
                                    value="new"
                                ></v-radio>
                            </v-radio-group>
                            <v-form ref="unithBtn" @submit.prevent="unithBtn">
                                <v-autocomplete 
                                    label="Category"
                                    item-text="CategoryName"
                                    item-value="CategoryCode"
                                    dense
                                    outlined
                                    :items="categoryItems"
                                    v-model="category"
                                    @change="getDepartment()"
                                    :rules="rules.required"
                                    v-if="categoryRadio == 'exist'"
                                />
                                <v-autocomplete 
                                    label="Department"
                                    item-text="UnitDepartmentName"
                                    item-value="UnitDepartmentCode"
                                    dense
                                    outlined
                                    :items="departmentItems"
                                    v-model="department"
                                    @change="getSection()"
                                    :rules="rules.required"
                                    
                                />
                                <v-autocomplete 
                                    label="Section"
                                    item-text="UnitSectionName"
                                    item-value="UnitSectionCode"
                                    dense
                                    outlined
                                    :items="sectionItems"
                                    v-model="section"
                                    @change="getTeam()"
                                    :rules="rules.required"
                                />
                                <v-autocomplete 
                                    label="Team"
                                    item-text="UnitTeamName"
                                    item-value="UnitTeamCode"
                                    dense
                                    outlined
                                    :items="teamItems"
                                    v-model="team"
                                    :rules="rules.required"
                                    @change="getEmpty()"
                                />
                            

                        
                           
                            
                            <v-btn color="primary" block  type="submit">
                                {{ unitRadio == 'update'?'VIEW':'ADD' }}
                            </v-btn>
                            <v-btn block @click="toggleCloseView()">CLOSE</v-btn>
                        </v-form>
                        </v-container>
                    </v-card>
                </v-expand-transition>
              </v-col>
              <!-- branch postion table -->
              <v-col cols="12" md="9" lg="9" xl="9">
                    <v-simple-table
                        dense
                        class="elevation-1"
                        fixed-header
                        height="80vh"
                        v-show="dpbTableView"
                    >
                        <thead>
                            <tr v-if="expandBranch">
                                <th>Division</th>
                                <th>Branch Name</th>
                                <th>Position Name</th>
                                <th>Delete</th>
                            </tr>
                            <tr v-else-if="expandRank">
                                <th>Division</th>
                                <th>Grade</th>
                                <th>Rank</th>
                                <th>Delete</th>
                            </tr>
                        </thead>
                        <tbody v-if="expandBranch">
                            <tr v-for="(item,i) in dpbTableMerge" :key="i">
                                <td v-if="item.count > 0" :rowspan="item.count > 0?item.count : 1" class="tableBorder">{{ item.DivisionName }}</td>
                                <td v-if="item.count > 0" :rowspan="item.count > 0?item.count : 1"
                                     @dblclick="editing = true; editIndex = i" class="editColor tableBorder">
                                    <template v-if="editing && editIndex === i">
                                        <div contenteditable  @blur="updateValue(item, 'branch')" style="white-space: pre-wrap;">{{ item.BranchName }}</div>
                                    </template>
                                    <template v-else>
                                        <div style="white-space: pre-wrap;">{{ item.BranchName }}</div>
                                      </template>
                                </td>
                                <td @dblclick="editing = true; editIndex = i" class="editColor tableBorder">
                                    <template v-if="editing && editIndex === i">
                                        <div contenteditable @blur="updateValue(item,'position')" style="white-space: pre-wrap;">{{ item.PositionName }}</div>
                                    </template>
                                    <template v-else>
                                        <div style="white-space: pre-wrap;">{{ item.PositionName }}</div>
                                      </template>
                                </td>
                                <td class="tableBorder"><v-btn color="error" icon @click="deleteValue(item)"><v-icon >mdi-trash-can-outline</v-icon></v-btn></td>
                            </tr>
                        </tbody>

                        <tbody v-else-if="expandRank">
                            <tr v-for="(item,i) in rankTableMerge" :key="i">
                                <td v-if="item.count > 0" :rowspan="item.count > 0?item.count : 1" class="tableBorder">{{ item.DivisionName }}</td>
                                <td @dblclick="editing = true; editIndex = i" class="editColor tableBorder">
                                    <template v-if="editing && editIndex === i">
                                        <div contenteditable  @blur="updateValue(item,'grade')" style="white-space: pre-wrap;">{{ item.Grade }}</div>
                                    </template>
                                    <template v-else>
                                        <div style="white-space: pre-wrap;">{{ item.Grade }}</div>
                                      </template>
                                </td>
                                <td @dblclick="editing = true; editIndex = i" class="editColor tableBorder">
                                    <template v-if="editing && editIndex === i">
                                        <div contenteditable @blur="updateValue(item,'rank')" style="white-space: pre-wrap;">{{ item.Rank }}</div>
                                    </template>
                                    <template v-else>
                                        <div style="white-space: pre-wrap;">{{ item.Rank }}</div>
                                      </template>
                                </td>
                                <td class="tableBorder"><v-btn color="error" icon @click="deleteValue(item)"><v-icon >mdi-trash-can-outline</v-icon></v-btn></td>
                            </tr>
                        </tbody>
                    </v-simple-table>
              </v-col>
        </v-row>

    </div>
</template>

<script>
    import axios from 'axios';
    // import moment from 'moment'
import {mapState} from 'vuex'
    export default {
        name:'AdminComponent',
        data:()=>({
            disableView:false,
            expand:false,
            expandBranch:false,
            expandRank:false,
            expandUnit:false,
            expandRequired:false,
            divisionData:null,

            

            editing: false,
            editIndex: null,

            branchRadio:'update',
            addBranch:null,
            addPosition:null,
            branchData:null,
            positionData:null,
            positionItems:[],
            dpbTable:[],
            dpbTableView:false,

            rankRadio:'update',
            addGrade:null,
            addRank:null,
            rankItems:[],
            rankData:null,
            gradeData:null,
            rankTable: [],

            categoryRadio:'exist',
            departmentRadio:'exist',
            sectionRadio:'exist',
            category:null,
            categoryItems:[],
            division:null,
            department:null,
            departmentItems:[],
            section:null,
            sectionItems:[],
            team:null,
            teamItems:[],


            rules:{
            required: [
                v => !!v || 'Field is required'
            ],
        },


        }),
        methods:{
            toggleExpand() {
                this.expand = !this.expand;
            },
            toggleBranch() {
                this.expandBranch = !this.expandBranch;
                this.expand = !this.expand;
                this.disableView =true
            
            },
            toggleUnit() {
                this.expandUnit = !this.expandUnit;
                this.expand = !this.expand;
                this.disableView =true
             
            },
            toggleRank() {
                this.expandRank = !this.expandRank;
                this.expand = !this.expand;
                this.disableView =true
               
            },
            toggleRequired(){
                this.expandRequired = !this.expandRequired;
                this.expand = !this.expand;
                this.disableView =true
            },
            toggleCloseView(){
                this.expand =true
                this.expandUnit = false
                this.expandBranch = false;
                this.expandRank = false;
                this.expandRequired = false
                this.disableView =false
                this.dpbTableView = false
            },

            changeDivision(){
                if(this.expandBranch){
                    this.positionItems = []
                    this.branchData = null
                    this.addBranch = null
                    this.addPosition = null
                    this.$store.dispatch('getFilterDivision',this.divisionData)
                }
                if(this.expandRank){
                    this.rankItems = []
                    this.gradeData = null
                    this.addGrade = null
                    this.addRank = null
                    this.$store.dispatch('getFilterRank',this.divisionData)
                }
                if(this.expandUnit){
                    this.$store.state.unitItems = []
                    this.$store.state.filterUnitItems = []
                    this.unitData = null
                    this.subUnitData = null
                    // this.addRank = null
                }
                
            },
            changeBranch(){
                this.positionItems = []
                this.addBranch = null
                this.$store.dispatch('getFilterDivision',this.divisionData)
                this.filterDivisionItems.filter(r=>{
                    if(r.BranchCode == this.branchData){
                        this.positionItems.push(r)
                    }
                })
            },

            changeGrade(){
                this.rankItems = []
                this.addBranch = null
                this.$store.dispatch('getFilterDivision',this.divisionData)
                this.filterRankItems.filter(r=>{
                    if(r.Grade == this.gradeData){
                        this.rankItems.push(r)
                    }
                })
            },
            changeUnit(){
                // console.log(this.unitData)
                this.subUnitItems = []
                this.addUnit = null
                this.addSubUnit = null
                this.$store.dispatch('getFilterUnit',this.unitData)
            },

          async branchBtn(val){
                if(val != 'update'){
                    //get last object of array
                    let lastId = this.filterDivisionItems.pop()
                    //for new branchCode
                    let lastBranch = lastId.BranchCode.match(/\d+/);
                    let branchId = parseInt(lastBranch[0], 10) + 1;
                    let branchCode = lastId.BranchCode.replace(/\d+/, branchId.toString().padStart(lastBranch[0].length, '0'))
                    //for new positionCode
                    let lastPosition = lastId.PositionCode.match(/\d+/);
                    let positionId = parseInt(lastPosition[0], 10) + 1;
                    let positionCode = lastId.PositionCode.replace(/\d+/, positionId.toString().padStart(lastPosition[0].length, '0'))

                    axios({
                        method:'post',
                        url:`${this.$baseUrl}api/addBranchPosition`,
                        data:{
                            divisionCode : this.divisionData,
                            branchCode : this.branchData?this.branchData:branchCode,
                            branchName : !this.branchData?this.addBranch:null,
                            positionCode : positionCode,
                            positionName : this.addPosition
                        }
                    }).then(function(){
                        
                        this.$store.dispatch('getFilterDivision',this.divisionData)
                        this.addBranch = null
                        this.addPosition = null
                        alert('add')
                    })

                }else{
           
                    this.dpbTable = []
                    this.filterDivisionItems.filter(res=>{
                        if(this.positionData){
                            if(res.PositionCode == this.positionData){
                            this.dpbTable.push(res)
                        }
                        }else{
                            if(res.BranchCode == this.branchData){
                                this.dpbTable.push(res)
                            }
                        }
                     
                    })
     
                    this.dpbTableView = true
                }
            },
            rankBtn(val){
                if(val != 'update'){
                    axios({
                        method:'post',
                        url:`${this.$baseUrl}api/addRank`,
                        data:{
                            divisionCode : this.divisionData,
                            gradeCode : this.gradeData?this.gradeData:this.addGrade.toUpperCase(),
                            rankCode : this.addRank.toUpperCase()
                        }
                    }).then(function(){
                    
                        this.$store.dispatch('getFilterRank',this.divisionData)
                        this.addGrade = null
                        this.addRank = null
                        alert('add')
                    })

                }else{           
                    this.rankTable = []
                    this.filterRankItems.filter(res=>{
                        if(this.rankData){
                            if(res.Rank == this.rankData){
                            this.rankTable.push(res)
                        }
                        }else if(this.gradeData){
                            if(res.Grade == this.gradeData){
                                this.rankTable.push(res)
                            }
                        }else{
                            this.rankTable.push(res)
                        }
                        
                     
                    })
                    this.dpbTableView = true
                }
            },
            getCategory(){
                // this.tableData = []
                this.department = null
                this.section = null
                this.team = null
                axios({
                    method:'get',
                    url:`${this.$baseUrl}api/getCategory`,
                }).then(r=>{
                    this.categoryItems = r.data
                })
            },
            getDepartment(){
                this.tableData = []
                this.section = null
                this.team = null
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/getDepartment`,
                    data:{
                        data : this.category
                    }
                }).then(r=>{
                    this.departmentItems = r.data
                })
            },
            getSection(){
                // this.tableData = []
                this.team = null
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/getSection`,
                    data:{
                        data : this.department
                    }
                }).then(r=>{
                    this.sectionItems = r.data
                })
            },
            getTeam(){
                // this.tableData = []
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/getTeam`,
                    data:{
                        data : this.section
                    }
                }).then(r=>{
                    this.teamItems = r.data
                })
            },
            unithBtn(){
     
                if(this.$refs.unithBtn.validate()){
               
                    //get last object of array
                    // let lastId = this.filterDivisionItems.pop()
                    // //for new branchCode
                    // let lastBranch = lastId.BranchCode.match(/\d+/);
                    // let branchId = parseInt(lastBranch[0], 10) + 1;
                    // let branchCode = lastId.BranchCode.replace(/\d+/, branchId.toString().padStart(lastBranch[0].length, '0'))
                    // //for new positionCode
                    // let lastPosition = lastId.PositionCode.match(/\d+/);
                    // let positionId = parseInt(lastPosition[0], 10) + 1;
                    // let positionCode = lastId.PositionCode.replace(/\d+/, positionId.toString().padStart(lastPosition[0].length, '0'))
                }
            },

            
            updateValue(item,val){
                // console.log(val)
                this.editing = false
                this.editIndex = null  
                const updatedValue = event.target.innerText
                if(val == 'position' || val == 'branch'){
                    // console.log('testtset')
                    axios({
                        method:'post',
                        url:`${this.$baseUrl}api/updateBranchPosition`,
                        data:{
                            updatedValue : updatedValue,
                            code : val == 'branch'?item.BranchCode:item.PositionCode,
                            table : val
                        }
                    }).then(function(){
                        alert('Updated')
                    })
                }else if(val == 'grade' ||val == 'rank'){
                    // console.log('rank','tea')
                    axios({
                        method:'post',
                        url:`${this.$baseUrl}api/updateGradeRank`,
                        data:{
                            updatedValue : updatedValue,
                            code : val == 'rank'?item.Rank:item.Grade,
                            table : val
                        }
                    }).then(function(){
                        alert('Updated')
                        this.$store.dispatch('getFilterRank',this.divisionData)
                    })
                }
            },
           async deleteValue(item){
              let deleteApi
              if(this.expandBranch){
                deleteApi = 'deleteBranchPosition'
              }else if(this.expandRank){
                deleteApi = 'deleteRank'
              }else if(this.expandUnit){
                deleteApi = 'deleteUnit'
              }
               await axios({
                    method:'post',
                    url:`${this.$baseUrl}api/${deleteApi}`,
                    data:{
                        data : item
                    } 
                }).then(async function(){
                    this.expandBranch?await  this.$store.dispatch('getFilterDivision',item.DivisionCode):
                    this.expandRank?await  this.$store.dispatch('getFilterRank',item.DivisionCode):''
                    
                    // console.log(this.filterDivisionItems,'sok')
                    alert('Deleted')
                    setTimeout(() => {
                        this.expandBranch?this.branchBtn('update'):this.expandRank?this.rankBtn('update'):''
                    }, 1000);
                  
                })
            }
        },
        computed:{
            ...mapState([
                'divisionItems',
                'unitItems',
                'filterDivisionItems',
                'filterRankItems',
                'filterUnitItems',
                'lastMonthData'
            ]),
            dpbTableMerge(){
                return this.dpbTable.reduce((total,item)=>{
                    let index = total.findIndex(r=> r.DivisionCode == item.DivisionCode)

                    if(index == -1){
                        let obj = {...item}
                        obj.count = 1
                        total.push(obj)
                    }else{
                        total[index].count += 1
                        total.push(item)
                    }
                    return total
                },[])
            },
            rankTableMerge(){
                return this.rankTable.reduce((total,item)=>{
                    let index = total.findIndex(r=> r.DivisionCode == item.DivisionCode)

                    if(index == -1){
                        let obj = {...item}
                        obj.count = 1
                        total.push(obj)
                    }else{
                        total[index].count += 1
                        total.push(item)
                    }
                    return total
                },[])
            },

            filterRankItemsMerge(){
                return this.filterRankItems.reduce((total,item)=>{
                    let index = total.findIndex(r=> r.Grade == item.Grade)
                    if(index == -1){
                        total.push(item)
                    }
                    return total
                },[])
            },
            formattedDate() {
                if (this.selectedDate) {
                    const date = new Date(this.selectedDate);
                    const month = date.toLocaleString('default', { month: 'long' });
                    const year = date.getFullYear();
                    return `${month} ${year}`;
                }
                return null;
            }
        },
        watch:{
            expand(val){
                if(val){
                    this.$store.dispatch('getDivision')
                }
            },
            branchRadio(val){
                if(val == 'update'){
                    this.branchData = null
                    this.addBranch = null
                    this.addPosition = null
                }
            },
            expandBranch(val){
                if(val){
                    this.$store.dispatch('getFilterDivision',this.divisionData)
                }
            },
            expandRank(val){
                if(val){
                    this.$store.dispatch('getFilterRank',this.divisionData)
                }
            },
            expandUnit(val){
                if(val){
                    this.$store.dispatch('getUnit')
                }
            }
        },
        mounted(){
            this.getCategory()
        }
    }
