<template>
    <div>
        <h1><center>Personnel Readiness</center></h1>
        <h2><center>{{ formattedDate }}</center></h2>
        <!-- <h2><center>{{ approved.Status }}</center></h2> -->

        <v-divider/>
        <v-col cols="12" md="12" lg="12" xl="12" v-show="approved.Status && ['1','2'].includes(user.approver)|| !['1','2'].includes(user.approver)">
                <v-tabs v-model="personel_tab" justify-center>
                    <v-tab href="#pqr" @click="pqr('pqr')" v-if="tableData">PERSONNEL QUALIFICATION RATING</v-tab>
                    <v-tab href="#psg" @click="psg('psg')" v-if="tableData.length > 0">PERSONNEL STRENGTH BY GRADE</v-tab>
                    <v-tab href="#prs" @click="prs('prs')" v-if="tableData.length > 0">PERSONNEL READINESS RATING</v-tab>
                </v-tabs>
        </v-col>
        <v-tabs-items v-model="personel_tab">
            <v-tab-item :value="'pqr'">
                <v-row>
                    <v-col cols="12" md="3" lg="3" xl="3">
                        <v-row>
                            <v-col cols="12" md="12" lg="12" xl="12">
                                <v-card elevation="3" class="justify-center">
                                    <v-card-title class="d-flex justify-center" style="background-color: #0275d8; color: white;">
                                        CONTROL
                                    </v-card-title>
                                    <v-container>
                                        <div>
                                            <br>
                                            <v-col cols="12"  v-if="user.rights == '1' || ['1','3'].includes(user.res) && user.main == 'D01' ?true:false">
                                                <v-autocomplete 
                                                    label="Unit"
                                                    dense
                                                    outlined
                                                    style="margin-top:-30px;"
                                                    v-model="department"
                                                    :items="departmentItems"
                                                    item-text="UnitDepartmentName"
                                                    item-value="UnitDepartmentCode"
                                                
                                                />
                                            </v-col>
                                            <v-col cols="12"  v-if="user.rights == '1'  || ['1','3'].includes(user.res)?true:false">
                                                <v-autocomplete 
                                                    label="Sub-Unit"
                                                    dense
                                                    outlined
                                                    style="margin-bottom:-40px;margin-top:-30px;"
                                                    v-model="team"
                                                    :items="teamItems"
                                                    item-text="UnitTeamName"
                                                    item-value="UnitTeamCode"
                                                
                                                />
                                            </v-col>
                                            <v-col>
                                                <v-dialog ref="dialog" v-model="generateMonthDialog" :return-value.sync="generateDate" persistent width="290px">
                                                    <template v-slot:activator="{ on, attrs }">
                                                        <v-text-field v-model="formattedDate" label="Select Month" prepend-inner-icon="mdi-calendar" readonly v-bind="attrs" v-on="on" dense outlined class="text-center" ></v-text-field>
                                                    </template>
                                                    <v-date-picker v-model="generateDate" type="month" scrollable>
                                                        <v-spacer></v-spacer>
                                                        <v-btn text color="primary" @click="generateMonthDialog = false">Cancel</v-btn>
                                                        <v-btn text color="primary" @click="[$refs.dialog.save(generateDate),tableData = []]">OK</v-btn>
                                                    </v-date-picker>
                                                </v-dialog>
                                            <!-- <v-btn color="warning" medium block @click="generate()" :disabled="lastMonthData.length > 0?false:true">GENERATE</v-btn> -->
                                                <v-btn color="warning" medium block @click="generate()" style class="mb-2" :disabled="!generateDate || generateDate > monthnow?true:false">GENERATE</v-btn>
                                                <v-btn color="primary" medium block class="mb-2" :disabled="tableData.length == 0 || !approved.Status || !approved.Status && user.approver != '-' ?true:false" @click="print()">PRINT</v-btn>
                                                <v-btn v-show="this.user.approver == 2" color="error" medium block class="mb-2" :disabled="tableData.length == 0 || approved.Status != 1 && user.approver == 2 ?true:false" @click="declined(generateDate,team,user.approver)">DECLINED</v-btn>
                                                <v-btn v-show="this.user.approver == 3" color="error" medium block class="mb-2" :disabled="tableData.length == 0 || approved.Status != 2 && user.approver == 3 ?true:false" @click="declined(generateDate,team,user.approver)">DECLINED</v-btn>
                                                <v-btn v-show="this.user.approver == 4" color="error" medium block class="mb-2" :disabled="tableData.length == 0 || approved.Status != 3 && user.approver == 4 ?true:false" @click="declined(generateDate,team,user.approver)">DECLINED</v-btn>
                                                <v-btn v-show="this.user.approver == 5" color="error" medium block class="mb-2" :disabled="tableData.length == 0 || approved.Status != 4 && user.approver == 5 && disableApproved?true:false" @click="declined(generateDate,team,user.approver)">DECLINED</v-btn>
                                                <v-btn v-show="this.user.approver == 1" color="success" medium block @click="submitData(generateDate,team)" :disabled="tableData.length == 0 || !disableSubmit?true:false">SUBMIT</v-btn>
                                                <v-btn v-show="this.user.approver == 2" color="success" medium block @click="approvedData(generateDate,team,'2')" :disabled="tableData.length == 0  || approved.Status != 1 ?true:false">CONCURRENCE</v-btn> <!-- OIC-->
                                                <v-btn v-show="this.user.approver == 3" color="success" medium block @click="approvedData(generateDate,team,'3')" :disabled="tableData.length == 0  || approved.Status != 2 ?true:false">CONCURRENCE</v-btn> <!-- EX-O-->
                                                <v-btn v-show="this.user.approver == 4" color="success" medium block @click="approvedData(generateDate,team,'4')" :disabled="tableData.length == 0  || approved.Status != 3 ?true:false">CONCURRENCE</v-btn> <!-- DEPUTY-->
                                                <v-btn v-show="this.user.approver == 5" color="success" medium block @click="approvedData(generateDate,team,'5')" :disabled="tableData.length == 0  || approved.Status != 4 && disableApproved?true:false">APPROVED</v-btn> <!-- COMMANDER-->
                                                <div v-if="tableData.length > 0">
                                                    <br />
                                                    <hr />
                                                    <center>
                                                        <StatusTimeline 
                                                            ref="StatusTimeline" 
                                                            :month="generateDate" 
                                                            :department="department" 
                                                            :team="team" 
                                                            :table="'personnel'"
                                                            @fromTimeline="handledTimelineData"
                                                        />
                                                    </center>
                                                    
                                                </div>
                                            </v-col>
        
                                        </div>
        
                                    </v-container>
                                </v-card>
                            </v-col>
                        </v-row>
                         <!-- reason card history -->
                        <v-row>
                            <v-col cols="12" xl="12" md="12" lg="12">
                                <DeclinedCard ref="DeclinedCard" :details="reasonDetails"/>
                            </v-col>
                        </v-row>
                        
                    </v-col>


                    <v-col cols="12" md="9" lg="9" xl="9">
                        <v-card elevation="3">
                            <v-simple-table
                                fixed-header
                                class="elevation-1 custom-table"
                                height="150vh"
                            >
                                <thead>
                                    <tr>
                                        <th>DESCRIPTION/POSITION</th>
                                        <th width="25%">NAME</th>
                                        <th>RANK REQUIRED</th>
                                        <th>ACTUAL</th>
                                        <th>POINTS</th>
                                        <th>AFPOS</th>
                                        <th>ACTUAL</th>
                                        <th>POINTS</th>
                                        <th>REQUIRED</th>
                                        <th v-show="approved.Status == '0' && user.approver == 1">UPDATE</th>
                                    </tr>
                                </thead>
                                <tbody v-for="(item, i) in tableData" :key="i" v-show="approved && user.approver == 1 || user.approver != 1">
                                    <tr v-if="item.OfficeName">
                                        <td style="font-weight:bold;font-size:15px;background-color:lightgrey !important;" colspan="9">{{ item.OfficeName }}</td>
                                    </tr>
                                    <tr v-if="item.GroupBranch && !item.OfficeName">
                                        <td style="font-weight:bold;font-size:15px;" colspan="2">{{ item.GroupBranch }}</td>
                                    </tr>
                                    <tr v-if="item.PositionName != 'office_unit'">
                                        <td style="text-align:left !important">{{ item.PositionName }}</td>
                                        <td style="text-align:left !important">{{ item.RankName }} {{ item.Name }} {{ item.ServiceCode }}</td>
                                        <td>{{ item.requiredGradeName }}</td>
                                        <td>{{ item.RankCode }}</td>
                                        <td>{{ item.GradePoint }}</td>
                                        <td>{{ item.requiredAfposName }}</td>
                                        <td>{{ item.AfposName }}</td>
                                        <td>{{ item.AfposPoint }}</td>
                                        <td>{{ item.requiredMp }}</td>
                                        <td v-show="approved.Status == '0' && user.approver == 1">
                                            <v-tooltip bottom  transition="slide-x-reverse-transition">
                                                <template v-slot:activator="{on: tooltip}">
                                                    <v-btn icon @click="updateItem(item)" v-on="{...tooltip}"><v-icon color="warning">mdi-table-edit</v-icon></v-btn>
                                                </template>
                                                <span>Click to open updating details</span>
                                            </v-tooltip>
                                            
                                        </td>
                                        <!-- <td><v-btn icon @click="deleteItem(item)"><v-icon color="error">mdi-trash-can-outline</v-icon></v-btn></td> -->
                                    </tr>
                                </tbody>
                            </v-simple-table>

                            <v-overlay :value="overlay">
                                <v-progress-circular
                                    indeterminate
                                    size="64"
                                ></v-progress-circular>
                            </v-overlay>
                        </v-card>
                    </v-col>
                </v-row>

                
            </v-tab-item>

            <!------------------------------ psg tab----------------------------------- -->
            <v-tab-item :value="'psg'">
                <v-container>
                    <v-row>
                        <v-col cols="12" md="3" lg="3" xl="3">
                            <v-card>
                                <v-simple-table
                                    class="elevation-1 custom-table"
                                    fixed-header
                                  
                                >
                                    <thead>
                                        <tr>
                                            <th colspan="2">TOTAL</th>
                                        </tr>
                                    </thead>
                                        <tbody>
                                            <tr>
                                                <td>TO</td>
                                                <td  class="total">{{ totalToe }}</td>
                                            </tr>
                                            <tr>
                                                <td>ACTUAL</td>
                                                <td  class="total">{{ totalActual }}</td>
                                            </tr>
                                            <tr>
                                                <td colspan="2" style="background-color:#0275d8;color:white;"><b>PSG RATING</b></td>
                                            </tr>
                                            <tr>
                                                <td colspan="2"  class="total">{{ psgRating.toFixed(2) }}%</td>
                                            </tr>
                                        </tbody>
                                    
                                </v-simple-table>
                            </v-card>
                        </v-col>
                        <v-col cols="12" md="9" lg="9" xl="9">
                            <v-card max-width="100%">
                                <v-simple-table
                                    fixed-header
                                    class="elevation-1 custom-table"
                                    height="70vh"
                        
                                >
                                    <thead>
                                        <tr>
                                        <th>GRADE</th> 
                                        <th>TO</th> 
                                        <th>ACTUAL</th> 
                                        </tr>
                                    </thead>
                                    <tbody v-for="(item,i) in psgTable" :key="i">
                                        <tr v-if="item.GroupDivision">
                                            <td style="font-weight:bold;font-size:15px;">{{ item.GroupDivision }}</td>
                                        </tr>
                                        <tr v-if="item.GradeName != '-'">
                                            <td >{{ item.GradeName }}</td>
                                            <td >{{ item.countRequired }}</td>
                                            <td >{{ item.countActual?item.countActual:'' }}</td>
                                        </tr>
                                        <template v-if="!item.GroupDivision && (!psgTable[i + 1] || psgTable[i + 1].GroupDivision)">
                                            <tr style="background-color:lightgray;" v-if="item.GradeName != '-'">
                                                <td style="font-weight:bold;font-size:15px;">Sub-Total</td>
                                                <td style="font-weight:bold;font-size:15px;">{{ item.totalCountRequired }}</td>
                                                <td style="font-weight:bold;font-size:15px;">{{ item.totalCountActual }}</td>
                                            </tr>
                                        </template>
                                    </tbody>
                                </v-simple-table>
                            </v-card>
                        </v-col>
                    </v-row>
                </v-container>
            </v-tab-item>
            <!-------------------------------------------prr tab--------------------------------------->
            <v-tab-item :value="'prs'">
                <v-container>
                    <v-card elevation="2">
                            <v-simple-table
                                class="custom-table"
                            >
                            <thead>
                                <tr>
                                    <th>RATING</th>
                                    <th>PERCENTAGE</th>
                                    <th>WEIGHT<br>PERCENTAGE</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td>PSGR (50%)</td>
                                    <td>{{ psgRating?psgRating.toFixed(2):0 }}%</td>
                                    <td>{{ psgRating05?psgRating05.toFixed(2):0 }}%</td>
                                </tr>
                                <tr>
                                    <td>Personnel Qualification Rating (50%)</td>
                                    <td>{{ pqrRating05?pqrRating05.toFixed(2):0 }}%</td>
                                    <td>{{ pqrRatingTotal?pqrRatingTotal.toFixed(2):0}}%</td>
                                </tr>
                                <tr>
                                    <td>PRR (40%)</td>
                                    <td>{{ gradeRating?gradeRating.toFixed(2):0 }}%</td>
                                    <td>{{ gradeRating04?gradeRating04.toFixed(2):0 }}%</td>
                                </tr>
                                <tr>
                                    <td>PSR (60%)</td>
                                    <td>{{ afposRating?afposRating.toFixed(2):0 }}%</td>
                                    <td>{{ afposRating06?afposRating06.toFixed(2):0}}%</td>
                                </tr>
                                <tr>
                                    <td>TOTAL (PSGR + PQR)</td>
                                    <td></td>
                                    <td><b>{{ totalRating?totalRating.toFixed(2):0 }}%</b></td>
                                </tr>
                                <tr>
                                    <td colspan="2"></td>
                                    <td :class="totalRating?redConStyle(redConResult(totalRating)):''">{{ totalRating?redConResult(totalRating):'' }}</td>
                                </tr>
                            </tbody>  
                            </v-simple-table>
                        
                    </v-card>
                </v-container>
                <v-divider />
                <br>
                <v-container>
                <!------------------assessment table------------------------- -->
                <h3>Assessment:</h3>
                
                    <v-card elevation="2">
                        
                            <v-simple-table
                                class="custom-table"
                            >
                            
                            <tbody v-for="(val, e) in remarksData" :key="e">
                                <tr>
                                    <td style="font-weight: bold;text-align: left !important;background-color: lightgray;">{{ val.Question }}</td>
                                </tr>
                                <tr height="90%">
                                    <td @dblclick="editing = true; editIndex = e" style="text-align: left !important;white-space: pre-wrap;">
                                        <template v-if="editing && editIndex === e">
                                            <div contenteditable  @blur="updateRemarks(val)" style="white-space: pre-wrap;">{{ val.Remarks }}</div>
                                        </template>
                                        <template v-else>
                                            <div style="white-space: pre-wrap;">{{ val.Remarks }}</div>
                                        </template>
                                </td>
                                </tr>
                            </tbody>  
                            </v-simple-table>
                    
                    </v-card>
            </v-container>
            </v-tab-item>
        </v-tabs-items>
    <!-- </div> -->
        <!-- dialog for updating -->
        <v-dialog persistent width="500px" v-model="updateDialog">
            <v-card>
                <v-card-title style="background-color: #0275d8;color:white;">UPDATE
                    <v-spacer /><v-btn  @click="updateDialog = false" icon><v-icon color="white">mdi-close-circle-outline</v-icon></v-btn>
                </v-card-title>
                <v-card-text>
                    <v-form ref="updatePersonnel" @submit.prevent="updatePersonnel">
                            <v-row>
                                <v-col cols="12">
                                    <v-text-field 
                                        label="Branch"
                                        dense
                                        outlined
                                        style="margin-top:10px;margin-bottom:-30px;"
                                        v-model="branch"
                                        readonly
                                        :rules="rules"
                                     
                                    />
                         
                                </v-col>
                                <v-col cols="12">
                                    <v-text-field 
                                        label="Position"
                                        outlined
                                        dense
                                        v-model="position"
                                        readonly
                                        style="margin-bottom:-30px;"
                                        :rules="rules"
                                      
                                    />
                                </v-col>
                                <v-col cols="12">
                                    <v-text-field 
                                        label="Name"
                                        placeholder="Firstname MI Lastname"
                                        outlined
                                        dense
                                        v-model="name"
                                        clearable
                                        style="margin-bottom:-30px;"
                                        :rules="rules"
                                    />
                                </v-col>
                                <v-col cols="6">
                                    <v-autocomplete 
                                        label="Rank"
                                        outlined
                                        dense
                                        style="margin-bottom:-30px;"
                                        v-model="rankName"
                                        :items="rankItems"
                                        item-text="Rank"
                                        item-value="Rank"
                                        clearable
                                        :rules="rules"
                                    
                                    />
                                </v-col>
                                <v-col cols="6">
                                    <v-autocomplete 
                                        label="Branch of Service"
                                        outlined
                                        dense
                                        style="margin-bottom:-30px;"
                                        v-model="service"
                                        :items="serviceItems"
                                        clearable
                                        :rules="rules"
                                    />
                                </v-col>
                                <v-col cols="6">
                                    <v-text-field 
                                        label="Serial Number"
                                        outlined
                                        dense
                                        style="margin-bottom:-30px;"
                                        v-model="serialNo"
                                        @blur="checkSerial()"
                                        :rules="rules"
                                    />
                                </v-col>
                                <v-col cols="6">
                                    <v-autocomplete 
                                        label="FOS"
                                        outlined
                                        dense
                                        v-model="afpos"
                                        :items="afposItems"
                                        item-text="AfposName"
                                        item-value="AfposCode"
                                        clearable
                                        :rules="rules"
                                    />
                                </v-col>
                               
                                <v-col cols="12">
                                    <v-btn color="success" medium block type="submit">UPDATE</v-btn>
                                    <v-btn  medium block @click="resetUpdate()">CLEAR</v-btn>
                                </v-col>
                   
                            
                        </v-row>
                    </v-form>
                </v-card-text>
            </v-card>
        </v-dialog>
         <!-- -----------------------------------------snackbar--------------------------------------------------------------- -->
         <v-snackbar
            v-model="snackbar.show" 
            :color="snackbar.color"
            :timeout="timeout"
            top
            elevation="5"
            min-width="0"
      
            >
         {{snackbar.message}}
         <v-icon>{{snackbar.icon}}</v-icon> 
       </v-snackbar>
       <v-overlay :value="loadingPage">
            <v-progress-circular
                indeterminate
                size="70"
            >
                
            </v-progress-circular>
            <br>
            <span>LOADING</span>
        </v-overlay>
        
        <SubmitDialog ref="SubmitDialog" :getPersonnel="generate"/>
        <PersonnelPrint ref="PersonnelPrint"/>
        <Details ref="Details"/>
        <DeclinedDialog ref="DeclinedDialog" :generatePersonnel="generate"  @data-to-parent="handleDeclinedData"/>
        <PrintDialog ref="PrintDialog" :printPdf="printPdf" :printExcel="extractExcel"/>
        <ExtractionExcel ref="ExtractionExcel" :excelData="dataDownload"/>
    </div>
</template>

<script>

import PersonnelPrint from './PersonnelPrint.vue'
import { mapState,mapActions  } from 'vuex';
import pdfMake from 'pdfmake/build/pdfmake';
import pdfFonts from 'pdfmake/build/vfs_fonts';
pdfMake.vfs = pdfFonts.pdfMake.vfs;
import moment from 'moment'
import axios from 'axios';
// import redCon from '../Details/RedCon.js'
import Details from '../Details/RedConDetails.vue';
import Approved from '../Details/Approved'
// import Declined from '../Details/Declined'
import DeclinedCard from '../Details/DeclinedCard'
import DeclinedDialog from '../Details/DeclinedDialog.vue';
import ExtractionExcel from './ExtractionExcel.vue'
import PrintDialog from '../Details/PrintDialog'
import SubmitDialog from '../Details/SubmitDialog.vue'
import StatusTimeline from '../Details/StatusTimeline.vue'
export default {
    name:'PersonnelComponent',
    components:{
        SubmitDialog,
        PersonnelPrint,
        Details,
        DeclinedDialog,
        DeclinedCard,
        ExtractionExcel,
        PrintDialog,
        StatusTimeline
    },
    data:()=>({
        tableData:[],
        personalRadio:'generate',
        personel_tab:'pqr',
        generateMonthDialog: false,
        generateDate: null,
        id:null,
        team:null,
        department:null,
        branch:null,
        position:null,
        rank:null,
        rankName:null,
        service:null,
        serialNo:null,
        tempSerial:null,
        afpos:null,
        name:null,
        rules:[
            (value) => !!value || "Required"
        ],
        serialLoading:false,
        serialExist:false,
        updateDialog:false,
        snackbar:{
            show: false,
            message: null,
            color: null,
            icon: null,
        },
        timeout:1500,
        monthnow:moment().format('YYYY-MM'),
        remarksTable:[],
        editing: false,
        editIndex: null,
        tableContainer:[],
        declinedData:false,
        timelineData:{},
 

    }),
    methods:{
        ...mapActions([
            'diplayRemarks'  
        ]),
        // use this function for decryption
        decryptData(encryptedData) {
            const decryptedData = atob(encryptedData);
            return decryptedData;
        },
        // tab name function
        pqr(tab){
            this.personel_tab = tab
        },
        psg(tab){
            this.personel_tab = tab
            this.$store.dispatch('getPsgTable')
                
            
        },
        prs(tab){
            this.personel_tab = tab
            // this.$store.dispatch('diplayRemarks','personnel')
            this.diplayRemarks('personnel')
            this.$store.dispatch('getPsgTable')
            this.$store.dispatch('getPoints')
        },
        ///////////////////////////PQR FUNCTION/////////////////////////////
        capitalizeAfterSpace(str) {
            // Split the string by spaces
            let words = str.split(' ');

            // Capitalize the first letter of each word
            let capitalizedWords = words.map(word => {
            if (word.length > 0) {
                return word.charAt(0).toUpperCase() + word.slice(1).toLowerCase();
            }
            return '';
            });

            // Join the words back together with spaces
            let result = capitalizedWords.join(' ');

            return result;
        },
        // checking if serial no are used
        checkSerial(){
            this.serialLoading = true
            // this.serialExist = false;
            axios({
                method:'post',
                url:`${this.$baseUrl}api/checkSerial`,
                data:{
                    serialNo : this.serialNo,
                    month : this.generateDate,
                    unit : this.team
                }
            }).then(r=>{
                let serial = r.data.find(res => res == this.serialNo)
                if(serial != undefined){
                    if(this.tempSerial == this.serialNo){
                        this.serialExist = false;
                    }else{
                        this.serialExist = true;
                    }
                }else{
                    this.serialExist = false;
                }
                this.serialLoading = false
            })
        },
        requiredRule() {
        return !!this.serialNo || 'Field is required';
        },
        customRule() {
            // console.log(this.serialExist,'35')
        return this.serialExist !== true || "Serial number are existed";
        },
        // generate button
       async generate(){
        //  this.overlay = true
            try {
                await this.$store.dispatch('getLastMonth');
                await this.$store.dispatch('diplayRemarks', 'personnel');
                this.tableContainer = this.lastMonthData
                await this.$refs.DeclinedCard.getReason()
                await this.$refs.StatusTimeline.getTimeline()
                // console.log(this.lastMonthData,'ceo')
            } catch (error) {
                console.error('Error in generate:', error);
            }
       
        },
        // function to show all data in table
        getTable(val){
            // console.log(val,'val')
            if(val == 'noData'){
                this.tableData = [{}]
            }else{
                try{
                    const encryptedData = val;
                    const decryptedData = this.decryptData(encryptedData);
                    let tempData = JSON.parse(decryptedData);
                    
                        this.tableData = tempData.reduce((total,item)=>{
                            let index = total.findIndex(res=> res.BranchCode == item.BranchCode)
                            if(index == -1){
                                let obj = {...item}
                                obj.OfficeName = item.PositionName == 'office_unit'?item.BranchName:null
                                obj.GroupBranch = item.BranchName
                                obj.TotalMP = Number(item.requiredMp)
                                total.push(obj)
                            }else{
                                if (!isNaN(item.requiredMp)) {
                                    total[index].TotalMP += Number(item.requiredMp);
                                }
                                total[index].count += 1
                                item.total = total[index].TotalMP
                                total.push(item)
                            }
                            return total
                        },[])
                  
                    } catch(error){
                        console.error('Error fetching and decrypting grade data:', error);
                    }
            }
            
            
        },

        //////////////// function for updating table details///////////////
        updateItem(item){
            this.updateDialog = true
            this.id = item.id
            this.branch = item.BranchName
            this.position = item.PositionName
            this.rank= item.RankCode
            this.rankName = item.RankName
            this.service = item.ServiceCode
            this.serialNo = item.SerialNo?item.SerialNo:null
            this.tempSerial = item.SerialNo?item.SerialNo:null
            this.afpos = item.AfposCode
            this.name = item.Name
            
        },
        updatePersonnel(){
            if (this.$refs.updatePersonnel.validate()) {
                this.$store.state.overlay = true
                if(this.serialExist){
                    this.snackbar={
                            message: `Serial number already exists!!`,
                            color: 'error',
                            icon: 'mdi-alert-circle-outline',
                            show: true
                        }
                    this.$store.state.overlay = false
                }else{
                    axios({
                        method:'post',
                        url:`${this.$baseUrl}api/updatePersonnel`,
                        data:{
                            id : this.id,
                            RankCode : this.rank,
                            RankName : this.rankName,
                            ServiceCode : this.service,
                            SerialNo : this.serialNo,
                            AfposCode : this.afpos,
                            Name : this.capitalName,
                            Time : moment().format('YYYY-MM-DD HH:mm:ss'),
                            User : this.user.name
                        }
                    }).then(async res=>{
                        if (res.status === 200) {
                            // this.$store.dispatch('getLastMonth');
                            await this.$store.dispatch('getLastMonth');
                            // await this.generate()
                            this.snackbar = {
                                message: `Record was updated!`,
                                color: 'success',
                                icon: 'mdi-check-circle-outline',
                                show: true
                            };
                            
                            this.updateDialog = false;
                        }
                    })
                }
            }
        },
        resetUpdate(){
            axios({
                        method:'post',
                        url:`${this.$baseUrl}api/updatePersonnel`,
                        data:{
                            id : this.id,
                            RankCode : null,
                            RankName : null,
                            ServiceCode : null,
                            SerialNo : null,
                            AfposCode : null,
                            Name : null,
                        }
                    }).then(async res=>{
                        if (res.status === 200) {
                            // this.$store.dispatch('getLastMonth');
                            await this.generate()
                            this.snackbar = {
                                message: `Record was updated!`,
                                color: 'success',
                                icon: 'mdi-check-circle-outline',
                                show: true
                            };
                            
                            this.updateDialog = false;
                        }
                    })
        },
        /////////////////end of function updating details

        /////////////////////////////END OF PQR FUNCTION////////////////////////////////////////
        // submit function
        submitData(month,team){
            const confirmed = window.confirm('Are you sure you want to submit this report?');
            if(confirmed){
                this.$store.dispatch('diplayRemarks','personnel')
                this.$store.dispatch('getQuestion','Personnel')
                this.$refs.SubmitDialog.openDialog(month,team,'personnel')
                this.$refs.StatusTimeline.getTimeline()
                
            }
           
        },
        //approvedData
        approvedData(month,team,status){
            const confirmed = window.confirm('Are you sure you want to approved this report?');
            if(confirmed){
                let data = {
                    month : month,
                    team : team,
                    table : 'personnel',
                    name : this.$store.state.user.name,
                    url : this.$baseUrl,
                    status : status
                }
            return Promise.all([Approved(data),this.generate()]) ;
            }
        },
        //declinedData
        async declined(month,team){
            this.declinedData = false 
            let data = {
                month : month,
                title : `Reason for the Decline in the Reporting Month of ${this.formattedDate}`,
                team : team,
                table : 'personnel',
                name : this.$store.state.user.name,
                url : this.$baseUrl,
                today : moment().format('YYYY-MM-DD HH:mm:ss')
            }
            this.$refs.DeclinedDialog.openDialog(data)
            // return Promise.all([this.$refs.DeclinedDialog.openDialog(data),Declined(data),this.generate()]) ;
          
        },
        handleDeclinedData(data){
            this.declinedData = data 
        },
        handledTimelineData(data){
            this.timelineData = {...data[0]}
        },
        // print function
        print() {
            // const confirmed = window.confirm('Are you sure you want to print this report?');
            // if(confirmed){
            //     let obj = {
            //         tableData : this.tableData,
            //         teamName :this.department == 'D01'?this.user.officeName:this.user.unitName,
            //         month : this.formattedDate,
            //         psgTable : this.psgTable,
            //         totalToe : this.totalToe,
            //         totalActual : this.totalActual,
            //         psgRating : this.psgRating.toFixed(2)+'%',
            //         psgRating05 : this.psgRating05.toFixed(2)+'%',
            //         pqrRating05 : this.pqrRating05.toFixed(2)+'%',
            //         pqrRatingTotal : this.pqrRatingTotal.toFixed(2)+'%',
            //         gradeRating : this.gradeRating.toFixed(2)+'%',
            //         gradeRating04 : this.gradeRating04.toFixed(2)+'%',
            //         afposRating : this.afposRating.toFixed(2)+'%',
            //         afposRating06 : this.afposRating06.toFixed(2)+'%',
            //         totalRating : this.totalRating.toFixed(2)+'%',
            //         remarksData : this.remarksData,
            //         logo : this.department
            //     }
                
            //     this.$refs.PersonnelPrint.printPersonnel(obj)
            // }

            //****uncomment tihs if personnel need excel extraction***///
            if(this.remarksData.length > 0){
                this.$store.dispatch('diplayRemarks','personnel')
                this.$refs.PrintDialog.openDialog()
            }else{
                alert('Kindly ensure that you submit the report before proceeding to print.')
            }
            

        },
        printPdf(){
            this.$refs.PersonnelPrint.printPersonnel(this.dataDownload)
        },
        extractExcel(){
                this.$refs.ExtractionExcel.exportExcel()
        },
        redConResult(data){
            // console.log(Number(data),'ceo')
            if(this.tableData.length > 0){
                return this.$refs.Details.redCon(Number(data))
            }
        },
        redConStyle(data){
            if(data){
                return this.$refs.Details.redConStyle(data)
            }
        },
        // updating assessment remarks function
        updateRemarks(item){
            this.editing = false
            this.editIndex = null  
            const value = event.target.innerText
            let obj = {
                id : item.id,
                remarks : value
            }
            this.$store.dispatch('updateRemarks',obj)
        }
        /////////////////////////////PSG FUNCTION /////////////////////////////////////////////

    },
    computed:{
        ...mapState([
            'departmentItems',
            'teamItems',
            'lastMonthData',
            'branchItems',
            'positionItems',
            'rankItems',
            'serviceItems',
            'afposItems',
            'psgTable',
            'gradePoints',
            'afposPoints',
            'overlay',
            'loadingPage',
            'remarksData',
            'user'

        ]),
        //function for capitalized name 
        capitalName(){
            let data = '';
            if (this.name) {
                if (this.rank.includes('O')) {
                    data = this.name.toUpperCase();
                }else{
                    data = this.capitalizeAfterSpace(this.name)
                }
            }
            return data;
        },
        // function to show month name
        formattedDate() {
                if (this.generateDate) {
                    const date = new Date(this.generateDate);
                    const month = date.toLocaleString('default', { month: 'long' });
                    const year = date.getFullYear();
                    return `${month} ${year}`;
                }
                return null;
        },
        // function to display teamName
        teamName(){
            if(this.team){
                let data
                    this.teamItems.filter(r=>{
                        if(r.UnitTeamCode == this.team){
                            data = r.UnitTeamName
                        }
                    })
                return data
            }else{
                return ''
            }
        },
        // function for disable sumbit btn
        disableSubmit(){
            if(this.team){
                let office = this.user.main == 'D01'?this.user.office:this.user.unit
                if(office == this.team && this.approved.Status == 0){
                    return true
                }else{
                    return false
                }
                   
            }else{
                return false
            }
        },
        // disable commander approve->this function made due to restric offices
        disableApproved(){
            if(['T01','T02','T03'].includes(this.user.office)){
              
                if(this.approved.Status == 1){
                    return false
                }else{
                    return true
                }
            }else{
                // console.log('false')
                return true
            }
        },
        // function to get total TOE for psg tab
        totalToe(){
            if(this.psgTable.length > 0){
                return this.psgTable.reduce((total, item) => {
                    if (item.TotalRequired) {
                        return total + item.TotalRequired;
                    }
                    return total;
                    }, 0);
            }else{
                return 0
            }
        },
        // function to get total Actual for psg tab
        totalActual(){
            if(this.psgTable.length > 0){
                return this.psgTable.reduce((total, item) => {
                    if (item.TotalActual) {
                        return total + item.TotalActual;
                    }
                    return total;
                    }, 0);
            }else{
                return 0
            }
        },
        // function to get total psg for psg tab
        psgRating(){
            if(this.psgTable.length > 0){
               let result
                result = this.totalActual / this.totalToe * 100
               return result
            }else{
                return 0
            }
        },
        gradeRating(){
            let result
            result = this.gradePoints / this.totalActual * 100
            return result
        },
        gradeRating04(){
            let result
            result = this.gradeRating * 0.4
            return result
        },
        afposRating(){
            let result
            result = this.afposPoints / this.totalActual * 100
            return result
        },
        afposRating06(){
            let result
            result = this.afposRating * 0.6
            return result
        },


        psgRating05(){
            let data
                data = this.psgRating?this.psgRating * 0.5:0
            return data
        },
        pqrRating05(){
            let data
                data = this.pqrRatingTotal * 0.5
            return data
        },
        pqrRatingTotal(){
            let data
                data = (this.gradeRating * 0.4)+(this.afposRating * 0.6)
            return data
        },
        totalRating(){
            let data
                data = Number(this.psgRating05) + Number(this.pqrRating05)
            return data
        },
        // to check if the unit is submit
        submitted(){
            if(this.tableData.length > 0){
                return this.tableData.find(res=> res.Submit == '1')
            }else{
                return 0
            }
        },
        // to check if the unit is approved by approver
        approved(){
            if(this.tableData.length > 0){
                return this.tableData.find(res=> res.Status)
            }else{
                return 0
            }
        },
        reasonDetails(){
            let data = {
                table : 'personnel',
                month : this.generateDate,
                unit : this.team
            }
            return data
        },
        dataDownload(){
            let obj = {
                tableData : this.tableData,
                teamName :this.department == 'D01'?this.user.officeName:this.user.unitName,
                month : this.formattedDate,
                psgTable : this.psgTable,
                totalToe : this.totalToe,
                totalActual : this.totalActual,
                psgRating : this.psgRating.toFixed(2)+'%',
                psgRating05 : this.psgRating05.toFixed(2)+'%',
                pqrRating05 : this.pqrRating05.toFixed(2)+'%',
                pqrRatingTotal : this.pqrRatingTotal.toFixed(2)+'%',
                gradeRating : this.gradeRating.toFixed(2)+'%',
                gradeRating04 : this.gradeRating04.toFixed(2)+'%',
                afposRating : this.afposRating.toFixed(2)+'%',
                afposRating06 : this.afposRating06.toFixed(2)+'%',
                totalRating : this.totalRating.toFixed(2)+'%',
                remarksData : this.remarksData,
                logo : this.department,
                assesment : this.$store.state.remarksData && this.$store.state.remarksData.length > 0?true:false,
                status : this.approved.Status,
                sign : this.timelineData
            }
            return obj
        }

        
        
    },
    watch:{
        lastMonthData(val){
           if (val) {
                
               this.getTable(val);
              
           }

       },
        generateDate(val){
            if(val){
                this.$store.state.monthData = val
            }
        },
        department(val){
            if(val){
                this.$store.state.departmentData = val
                this.$store.dispatch('getTeam')
            }
        },
        team(val){
            if(val){
                // console.log(val)
                this.$store.state.teamData = val
                this.$store.dispatch('getBranch',val)
            }
        },
        
        tableContainer(val){
            if(val){
                this.getTable(val)
            }
        },
        branch(val){
            if(val){
                this.$store.dispatch('getPosition',val)
            }
        },
        position(val){
            if(val){
                // console.log(val)
                this.$store.dispatch('getRank')
            }
        },
        rankName(val){
            if(val){
                this.$store.dispatch('getAfpos')
                this.rankItems.filter(r=>{
                    if(r.Rank == this.rankName){
                        this.rank = r.Grade
                    }
                })
            }
        },
       
        // tableData(val){
        //     // console.log(val,this.lastMonthData,'table')
        //     if(val){
        //         this.$store.dispatch('getPsgTable')
        //         this.$store.dispatch('getPoints')
        //     }
        // },
        declinedData(val){
            if(val){
                this.generate()
            }
        }
        // totalActual(val){
        //     console.log(val,'val')
        // }
        // remarksData(val){
        //     if(val){
        //         this.questions.filter((r,e)=>{
        //             for (let i = 0; i < val.length; i++) {
        //                 const text = val[i];
        //                 if(e == i){
        //                     let obj ={
        //                         text : r,
        //                         value : val[i]
        //                     }
        //                     this.remarksTable.push(obj)
        //                 }
        //             }
        //         })
                
        //     }
        // }

        
    },
    mounted(){
        // this.$store.state.overlay = false
        
        this.$store.dispatch('getDepartment')
        this.department = this.user.main
        this.$store.state.departmentData = this.department
        this.user.rights?this.team = null:this.user.main == 'D01'?this.team = this.user.office:this.team = this.user.unit
       
    },
    created(){
     
    }

}
</script>
