<template>
    <div>
        <h1><center>Equipment  Readiness</center></h1>
        <h2><center>{{ formattedDate }}</center></h2>
        <v-divider/>
        <v-divider/>
        <v-col cols="12" md="12" lg="12" xl="12">
                <v-tabs v-model="equipment_tab" justify-center>
                    <v-tab href="#equip" @click="equip('equip')">EQUIPMENT</v-tab>
                    <v-tab href="#read" @click="read('read')" v-if="tableData.length > 0">REPORT READINESS</v-tab>
                </v-tabs>
        </v-col>
        <v-tabs-items v-model="equipment_tab">
            <v-tab-item :value="'equip'">
                <v-row>
                    <v-col cols="12" md="3" lg="3" xl="3">
                        <v-row>
                            <v-col cols="12" md="12" lg="12" xl="12">
                                <v-card elevation="3" class="justify-center">
                                    <v-card-title class="d-flex justify-center" style="background-color: #0275d8; color: white;">
                                        CONTROL
                                    </v-card-title>
                                    <v-container>
                                            <br>
                                           
                                            <v-row>
                                                <v-col cols="12"  v-if="user.rights == '1'  || ['4','3'].includes(user.res) && user.main == 'D01'?true:false">
                                                    <v-autocomplete 
                                                        label="Unit"
                                                        dense
                                                        outlined
                                                        style="margin-top:-30px;"
                                                        v-model="department"
                                                        :items="departmentItems"
                                                        item-text="UnitDepartmentName"
                                                        item-value="UnitDepartmentCode"
                                                       
                                                    />
                                                </v-col>
                                                <v-col cols="12" md="12" lg="12" xl="12" v-if="user.rights == '1'  || ['4','3'].includes(user.res)?true:false">
                                                    <v-autocomplete 
                                                        label="Sub-Unit"
                                                        dense
                                                        outlined
                                                        style="margin-bottom:-40px;margin-top:-30px;"
                                                        v-model="team"
                                                        :items="teamItems"
                                                        item-text="UnitTeamName"
                                                        item-value="UnitTeamCode"
                                                    />
                                                </v-col>
                                                <v-col cols="12" md="12" lg="12" xl="12">
                                                    <v-dialog ref="dialog" v-model="generateMonthDialog" :return-value.sync="generateDate" persistent width="290px">
                                                        <template v-slot:activator="{ on, attrs }">
                                                            <v-text-field v-model="formattedDate" label="Select Month" prepend-inner-icon="mdi-calendar" readonly v-bind="attrs" v-on="on" dense outlined class="text-center" ></v-text-field>
                                                        </template>
                                                        <v-date-picker v-model="generateDate" type="month" scrollable>
                                                            <v-spacer></v-spacer>
                                                            <v-btn text color="primary" @click="generateMonthDialog = false">Cancel</v-btn>
                                                            <v-btn text color="primary" @click="[$refs.dialog.save(generateDate),tableData = []]">OK</v-btn>
                                                        </v-date-picker>
                                                    </v-dialog>
                                                </v-col>
                                                <v-col cols="12" md="12" lg="12" xl="12">
                                                    <v-btn color="warning" medium block @click="generate()"  class="mb-2" :disabled=" !generateDate || generateDate > monthnow?true:false">GENERATE</v-btn>
                                                    <v-btn color="primary" medium block class="mb-2" :disabled="tableData.length == 0 || !approved.Status || !approved.Status && ['1','2'].includes(user.approver)?true:false" @click="print()">PRINT</v-btn>
                                                    <v-btn v-show="this.user.approver == 2" color="error" medium block class="mb-2" :disabled="tableData.length == 0 || approved.Status != 1 && user.approver == 2 ?true:false" @click="declined(generateDate,team,user.approver)">DECLINED</v-btn>
                                                    <v-btn v-show="this.user.approver == 3" color="error" medium block class="mb-2" :disabled="tableData.length == 0 || approved.Status != 2 && user.approver == 3 ?true:false" @click="declined(generateDate,team,user.approver)">DECLINED</v-btn>
                                                    <v-btn v-show="this.user.approver == 4" color="error" medium block class="mb-2" :disabled="tableData.length == 0 || approved.Status != 3 && user.approver == 4 ?true:false" @click="declined(generateDate,team,user.approver)">DECLINED</v-btn>
                                                    <v-btn v-show="this.user.approver == 5" color="error" medium block class="mb-2" :disabled="tableData.length == 0 || approved.Status != 4 && user.approver == 5 && disableApproved?true:false" @click="declined(generateDate,team,user.approver)">DECLINED</v-btn>

                                                    <v-btn v-show="this.user.approver == 1" color="success" medium block @click="submitData(generateDate,team)" :disabled="tableData.length == 0 || !disableSubmit?true:false">SUBMIT</v-btn>
                                                    <v-btn v-show="this.user.approver == 2" color="success" medium block @click="approvedData(generateDate,team,'2')" :disabled="tableData.length == 0  || approved.Status != 1 ?true:false">CONCURRENCE</v-btn> <!-- OIC-->
                                                    <v-btn v-show="this.user.approver == 3" color="success" medium block @click="approvedData(generateDate,team,'3')" :disabled="tableData.length == 0  || approved.Status != 2 ?true:false">CONCURRENCE</v-btn> <!-- EX-O-->
                                                    <v-btn v-show="this.user.approver == 4" color="success" medium block @click="approvedData(generateDate,team,'4')" :disabled="tableData.length == 0  || approved.Status != 3 ?true:false">CONCURRENCE</v-btn> <!-- DEPUTY-->
                                                    <v-btn v-show="this.user.approver == 5" color="success" medium block @click="approvedData(generateDate,team,'5')" :disabled="tableData.length == 0  || approved.Status != 4 && disableApproved?true:false">APPROVED</v-btn> <!-- COMMANDER-->
                                                        <div v-if="tableData.length > 0">
                                                            <br />
                                                            <hr />
                                                            <center>
                                                                <StatusTimeline 
                                                                    ref="StatusTimeline" 
                                                                    :month="generateDate" 
                                                                    :department="department" 
                                                                    :team="team" 
                                                                    :table="'equipment'"
                                                                    @fromTimeline="handledTimelineData"
                                                                />
                                                            </center>
                                                        </div>
                                                </v-col>
                                            </v-row>
                                    </v-container>
                                </v-card>
                            </v-col>
                        </v-row>
                        <!-- reason card history -->
                        <v-row>
                            <v-col cols="12" xl="12" md="12" lg="12">
                                <DeclinedCard ref="DeclinedCard" :details="reasonDetails"/>
                            </v-col>
                        </v-row>
                        
                    </v-col>
        
                    <v-col cols="12" md="9" lg="9" xl="9">
                        <v-card>
                            <v-simple-table class="elevation-1 custom-table" fixed-header height="90vh">
                                <thead>
                                    <tr>
                                        <th>Description</th>
                                        <th width="20%">Required</th>
                                        <th width="20%">Actual</th>
                                    </tr>
                                </thead>
                                 <tbody v-for="(item,i) in tableData" :key="i"  v-show="approved && user.approver == 1 || user.approver != 1">
                                    <tr >
                                        <td 
                                            :style="item.Title?'font-weight:bold;text-align:left !important;':'text-align:left !important;'"
                                            :colspan="!item.Required?'3':'1'"
                                        >
                                            {{ item.Detail }}
                                        </td>
                                        <td v-if="item.Required">{{ item.Required }}</td>
                                        <v-tooltip bottom  transition="slide-x-reverse-transition">
                                            <template v-slot:activator="{on: tooltip}">
                                                <td 
                                                    v-if="item.Required "
                                                    @dblclick="editActual = true; editIndex = i" 
                                                    :class="item.Status == '0' && item.Required?'background':''" 
                                                    v-on="{...tooltip}"
                                                >
                                                    <template v-if="editActual && editIndex === i && item.Status == '0'">
                                                        <input type="number" v-model="item.Actual" class="editable" @blur="updateDetail(item)"/>
                                                    </template>
                                                    <template v-else>
                                                        
                                                                <div style="white-space: pre-wrap;" >{{ item.Actual?item.Actual:'' }}</div>
                                                        
                                                    </template>
                                                </td>
                                            </template>
                                            <span>Double click to edit</span>
                                        </v-tooltip>
                                
                                    </tr>
                                </tbody>
                            </v-simple-table>
                        </v-card>
                    </v-col>
                </v-row>
            </v-tab-item>
            <v-tab-item :value="'read'">
               <v-container>
                    <v-row>
                        <!-- EQUIPMENT -->
                        <h2>EQUIPMENT READINESS RATING</h2>
                        <v-col cols="12">
                            <v-card>
                                <v-simple-table class="elevation-1 custom-table" fixed-header>
                                    <thead>
                                        <tr>
                                            <th></th>
                                            <th></th>
                                            <th>TE</th>
                                            <th>ACTUAL</th>
                                            <th>%</th>
                                        </tr>
                                    </thead>
                                    <tbody v-for="(item,i) in totalTable" :key="i">
                                        <tr v-if="item.Category == item.Sub">
                                            <td>{{ item.Category }}</td>
                                        </tr>
                                        <tr v-if="!item.title">
                                            <td></td>
                                            <td>{{ item.info == 'primary'?item.Sub:''  }}</td>
                                            <td>{{ item.totalRequired }}</td>
                                            <td>{{ item.totalActual }}</td>
                                            <td>{{ item.percent.toFixed(2)+'%' }}</td>
                                        </tr>
                

                                        <template v-if="item.break">
                                            <tr>
                                              <td class="content"></td>
                                              <td class="content">Total</td>
                                              <td class="content">{{ item.info == 'primary'?totalPrimaryRequired:totalSecondaryRequired }}</td>
                                              <td class="content">{{ item.info == 'primary'?totalPrimaryActual:totalSecondaryActual }}</td>
                                              <td class="content">{{ item.info == 'primary' ?averagePrimary:averageSecondary }}%</td>
                                            </tr>
                                            <tr>
                                              <td class="content"></td>
                                              <td class="content" colspan="3">{{ remarks(item) }}</td>
                                              <td class="content">{{ item.info == 'primary'?mee.toFixed(2)+'%':see.toFixed(2)+'%' }}</td>
                                            </tr>
                                          </template>
                                          <template v-if="!totalTable[i +1]">
                                            <tr>
                                                <td colspan="4" class="content">MEE + SEE</td>
                                                <td class="readiness">{{ readinessRating.toFixed(2)+'%' }}</td>
                                            </tr>
                                            <tr>
                                                <td colspan="5" :class="redConStyle(redConResult(readinessRating))">{{ redConResult(readinessRating) }}</td>
                                            </tr>
                                          </template>
                                    </tbody>
                                </v-simple-table>
                            </v-card>
                            </v-col>
                    </v-row>
                    <v-divider />
                    <br>
                        
                    <!------------------assessment table------------------------- -->
                    <h3>Assessment:</h3>
                        <v-card elevation="2">
                            <v-simple-table class="custom-table">
                                <tbody v-for="(val, e) in remarksData" :key="e">
                                    <tr>
                                        <td style="font-weight: bold;text-align: left !important;background-color: lightgray;">{{ val.Question }}</td>
                                    </tr>
                                    <tr height="90%">
                                        <td @dblclick="editing = true; editIndex = e" style="text-align: left !important;white-space: pre-wrap;">
                                            <template v-if="editing && editIndex === e">
                                                <div contenteditable  @blur="updateRemarks(val)" style="white-space: pre-wrap;">{{ val.Remarks }}</div>
                                            </template>
                                            <template v-else>
                                                <div style="white-space: pre-wrap;">{{ val.Remarks }}</div>
                                            </template>
                                    </td>
                                    </tr>
                                </tbody>  
                            </v-simple-table>
                        </v-card>
                </v-container>
            </v-tab-item>
        </v-tabs-items>
        <v-overlay :value="loadingPage">
            <v-progress-circular
                indeterminate
                size="70"
            >
                
            </v-progress-circular>
            <br>
            <span>LOADING</span>
        </v-overlay>
                
        <EquipmentPrint ref="EquipmentPrint"/>
        <!-- <RemarksDialog ref="RemarksDialog" :getEquipment="generate"/> -->
        <SubmitDialog ref="SubmitDialog" :getEquipment="generate"/>
        <PrintDialog ref="PrintDialog" :printPdf="printPdf" :printExcel="extractExcel"/>
        <ExtractionExcel ref="ExtractionExcel" :excelData="dataDownload"/>
        <Details ref="Details"/>
        <DeclinedDialog ref="DeclinedDialog" @data-to-parent="handleDeclinedData" />
        
    </div>
  </template>
  
  <script>
  
    import {mapState} from 'vuex'
    import moment from 'moment'
    import EquipmentPrint from './EquipmentPrint.vue'
    import ExtractionExcel from './ExtractionExcel.vue'
    import axios from 'axios'
    // import RemarksDialog from '../Personnel/RemarksDialog.vue'
    import SubmitDialog from '../Details/SubmitDialog.vue'
    import Details from '../Details/RedConDetails.vue';
    import Approved from '../Details/Approved'
    import DeclinedDialog from '../Details/DeclinedDialog'
    import DeclinedCard from '../Details/DeclinedCard'
    import PrintDialog from '../Details/PrintDialog'
    import StatusTimeline from '../Details/StatusTimeline.vue'
  export default {
    name:'EquipmentComponent',
    components:{
        EquipmentPrint,
        ExtractionExcel,
        SubmitDialog,
        Details,
        DeclinedDialog,
        DeclinedCard,
        PrintDialog,
        StatusTimeline
    },
    data:()=>({
        equipment_tab:'equip',
        tableData:[],
        department:null,
        editing:false,
        editActual:false,
        editIndex:null,
        generateMonthDialog: false,
        generateDate: null,
        id:null,
        team:null,
        year:moment().year(),
        rules:{
            required: [
               (v) => !!v || 'Field is required'
            ],
            existing:[(v) => v !== '1' || "Value '1' is not accepted"],
        },
        snackbar:{
            show: false,
            message: null,
            color: null,
            icon: null,
        },
        timeout:1500,
        monthnow:moment().format('YYYY-MM'),
        declinedData:false,
        updateDetailStatus:false,
        timelineData:{},
    }),
    methods:{
        async callGetTimeline() {
            if (this.$refs.StatusTimeline) {
                await this.$refs.StatusTimeline.getTimeline();
            } else {
                console.error("StatusTimeline ref not available yet.");
            }
        },
        generate(){
            this.$store.state.loadingPage = true
            axios({
                method:'post',
                url:`${this.$baseUrl}api/getEquipment`,
                data:{
                    Team : this.team,
                    Month : this.generateDate
                }
            }).then(async r=>{
                if(r.status == 200){
                    this.tableData = r.data
                    this.$store.state.teamData = this.team
                    this.$store.state.monthData = this.generateDate
                    if(this.updateDetailStatus == false){
                        await this.$store.dispatch('diplayRemarks','equipment')
                        await this.$refs.DeclinedCard.getReason()
                        await this.callGetTimeline()
                    }
                    this.$store.state.loadingPage = false
                    this.updateDetailStatus = false
       
                }
            })
        },
        equip(tab){
            this.equipment_tab = tab
        },
        read(tab){
            this.equipment_tab = tab
            this.$store.dispatch('diplayRemarks','equipment')
        },
        checkRequired(item){
            // console.log(required,actual)
            if(Number(item.Required) < Number(item.Actual)){
                alert(`Error: The total value of Actual is greater than the Required Value`)
                this.generate()
                return true
            }else{
                return false
            }
        },
      
        itemPercentage(required,item){
            let data
                data = item / required * 100
                if(data){
                    return this.percentage = this.roundUpValue(data)
                }else{
                    return this.percentage = 0
                }
            
        } ,

        updateDetail(item){
            // console.log(item,'ceo')
            // let checkValue = this.checkRequired(item)
                // if(!checkValue){
                    this.updateDetailStatus = true
                    axios({
                    method:'post',
                    url:`${this.$baseUrl}api/updateDetail_equipment`,
                    data:{
                        id : item.id,
                        actual : item.Actual,
                        Name : this.user.name,
                        Time : moment().format('YYYY-MM-DD HH:mm:ss')
                    }
                }).then(res=>{
                    if(res.status == 200){
                        // console.log(res.data,'ceo')

                        this.generate()
                    }else{
                        alert('Error:Please reinput actual')
                    }
                })
            // }
            
        },
       // submit function
       submitData(month,team){
            const confirmed = window.confirm('Are you sure you want to submit this report?');
            if(confirmed){
                this.$store.dispatch('diplayRemarks','equipment')
                this.$store.dispatch('getQuestion','Equipment')
                this.$refs.SubmitDialog.openDialog(month,team,'equipment')
            }
    
        },
        //approvedData
        approvedData(month,team,status){
            const confirmed = window.confirm('Are you sure you want to approved this report?');
            if(confirmed){
                let data = {
                    month : month,
                    team : team,
                    table : 'equipment',
                    name : this.$store.state.user.name,
                    url : this.$baseUrl,
                    status : status
                }
                return Promise.all([Approved(data),this.generate()]) ;
            }
        },
        //declinedData
        declined(month,team){
            let data = {
                month : month,
                title : `Reason for the Decline in the Reporting Month of ${this.formattedDate}`,
                team : team,
                table : 'equipment',
                name : this.$store.state.user.name,
                url : this.$baseUrl,
                today : moment().format('YYYY-MM-DD HH:mm:ss')
            }
            this.$refs.DeclinedDialog.openDialog(data)
        },
        handleDeclinedData(data){
            this.declinedData = data 
        },
        handledTimelineData(data){
            this.timelineData = {...data[0]}
        },

        // print function

        print() {
            
            if(this.remarksData.length > 0){
                this.$store.dispatch('diplayRemarks','equipment')
                this.$refs.PrintDialog.openDialog()
            }else{
                alert('Kindly ensure that you submit the report before proceeding to print.')
            }
            // const confirmed = window.confirm('Are you sure you want to print this report?');
            // if(confirmed){
            //     let obj = {
            //         tableData : this.tableData,
            //         totalTable : this.totalTable,
            //         teamName :this.unitName,
            //         month : this.formattedDate,
            //         totalPrimaryRequired : this.totalPrimaryRequired,
            //         totalSecondaryRequired : this.totalSecondaryRequired,
            //         totalPrimaryActual : this.totalPrimaryActual,
            //         totalSecondaryActual : this.totalSecondaryActual,
            //         averagePrimary : this.averagePrimary,
            //         averageSecondary : this.averageSecondary,
            //         mee : this.mee,
            //         see : this.see,
            //         readinessRating : this.readinessRating,
            //         remarksData : this.remarksData,
            //         assesment : this.$store.state.remarksData.length > 0?true:false
                
            //     }
            //     this.$refs.EquipmentPrint.printEquipment(obj)
            // }
        },
        printPdf(){
                this.$refs.EquipmentPrint.printEquipment(this.dataDownload)
        },
        extractExcel(){
                this.$refs.ExtractionExcel.exportExcel()
        },
        remarks(item){
            let data 
                if(item.info == 'primary'){
                    data = 'MISSION ESSENTIAL EQUIPMENT (80%)'
                }else{
                    data = 'SECONDARY EQUIPMENT'
                }
            return data
        },
        // updating assessment remarks function
        updateRemarks(item){
            this.editing = false
            this.editIndex = null  
            const value = event.target.innerText
            let obj = {
                id : item.id,
                remarks : value
            }
            this.$store.dispatch('updateRemarks',obj)
        },
        redConResult(data){
            if(data){
                return this.$refs.Details.redCon(Number(data))
            }
        },
        redConStyle(data){
            if(data){
                return this.$refs.Details.redConStyle(data)
            }
        },

        

        roundUpValue(number) {
            const roundedNumber = Math.round(number * 100) / 100; // Round to 2 decimal places
            return roundedNumber.toFixed(2); // Format to 2 decimal places as a string
        }
    },
    computed:{
        ...mapState([
            'departmentItems',
            'teamItems',
            'psgTable',
            'remarksData',
            'user',
            'loadingPage'
        ]),
         
        totalTable(){
            const key = ['MOBILITY','Ground','Sea','Air','FIREPOWER','Individual','Crew Served Weapon','COMMUNICATIONS','MEDICAL','DENTAL','QUARTERMASTER'];

            return key.map(val => {
            const matchedObject = this.tableData.find(current => current.Sub === val || current.Category === val);
               
            if (matchedObject) {
                const category = matchedObject.Category || val;
                const sub = matchedObject.Sub || val;

                const totalRequired = this.tableData.reduce((acc, current) => {
                return acc + (current.Sub === val || current.Category === val ? Number(current.Required) : 0);
                }, 0);

                const totalActual = this.tableData.reduce((acc, current) => {
                return acc + (current.Sub === val || current.Category === val ? Number(current.Actual) : 0);
                }, 0);

                // const percent = totalRequired !== 0 ? (totalActual / totalRequired) * 100 : 0;
                const percent = totalActual > totalRequired ?100: totalRequired !== 0 ? (totalActual / totalRequired) * 100 : 0;

                return {
                    Category: category,
                    Sub: sub,
                    totalRequired: totalRequired,
                    totalActual: totalActual,
                    percent: percent,
                    title : val == 'MOBILITY' ||  val == 'FIREPOWER'?1:0,
                    info : category == 'MOBILITY' ||  category == 'FIREPOWER' ||  category == 'COMMUNICATIONS'?'primary':'secondary',
                    break : val == 'COMMUNICATIONS' || val == 'QUARTERMASTER'?1:0
                };
            } else {
                return {
                    Category: val === 'Ground' || val === 'Sea' || val === 'Air' || val === 'Individual' ? 'MOBILITY' : val === 'Crew Served Weapon' ? 'FIREPOWER' : val,
                    Sub: val,
                    totalRequired: 0,
                    totalActual: 0,
                    percent: 0,
                    title : val == 'MOBILITY' ||  val == 'FIREPOWER'?1:0,
                    info : val === 'Ground' || val === 'Sea' || val === 'Air' || val === 'Individual' || val === 'Crew Served Weapon' ||  val == 'COMMUNICATIONS'?'primary':'secondary',
                    break : val == 'COMMUNICATIONS' || val == 'QUARTERMASTER'?1:0
                };
            }
            });
        },

        totalPrimaryRequired() {

            return this.tableData.reduce((acc, current) => {
                return acc + (current.Info === 'primary'? Number(current.Required) : 0);
                }, 0);

            },
        totalSecondaryRequired() {
            return this.tableData.reduce((acc, current) => {
                return acc + (current.Info === 'secondary'? Number(current.Required) : 0);
                }, 0);
        },

        totalPrimaryActual() {

            return this.tableData.reduce((acc, current) => {
                return acc + (current.Info === 'primary'? Number(current.Actual) : 0);
                }, 0);

        },
        totalSecondaryActual() {
            return this.tableData.reduce((acc, current) => {
                return acc + (current.Info === 'secondary'? Number(current.Actual) : 0);
                }, 0);
        },

        averagePrimary() {
            let totalPercentage = 0;
            let itemCount = 0;
            for (const item of this.totalTable) {
                if (item.percent && item.info == 'primary' && item.Sub != 'MOBILITY'  && item.Sub != 'FIREPOWER') {
                    let percentage = item.percent;
                    if(percentage){
                        // console.log(percentage)
                        totalPercentage += percentage
                        itemCount++;
                    }
                }
            }
            if (itemCount === 0) {
                return 0; // To avoid division by zero
            }
            return this.roundUpValue((totalPercentage / itemCount)); // Calculate average and round to 2 decimal places
        },
        averageSecondary() {
            let totalPercentage = 0;
            let itemCount = 0;
            for (const item of this.totalTable) {
                if (item.percent && item.info == 'secondary') {
                    let percentage = item.percent;
                    if(percentage){
                        // console.log(percentage)
                        totalPercentage += percentage
                        itemCount++;
                    }
                }
            }
            if (itemCount === 0) {
                return 0; // To avoid division by zero
            }
            return this.roundUpValue((totalPercentage / itemCount)); // Calculate average and round to 2 decimal places
        },
        mee(){
            return this.averagePrimary * 0.8
        },
        see(){
            return this.averageSecondary * 0.2
        },
        readinessRating(){
            return this.mee + this.see
        },


        

         // function to show month name
         formattedDate() {
                if (this.generateDate) {
                    const date = new Date(this.generateDate);
                    const month = date.toLocaleString('default', { month: 'long' });
                    const year = date.getFullYear();
                    return `${month} ${year}`;
                }
                return null;
        },
        // to check if the unit is submit
        submitted(){
            if(this.tableData.length > 0){
                return this.tableData.find(res=> res.Submit == '1')
            }else{
                return 0
            }
        },
        // to check if the unit is approved by approver
        approved(){
            if(this.tableData.length > 0){
                return this.tableData.find(res=> res.Status)
            }else{
                return 0
            }
        },
        // function for disable sumbit btn
        disableSubmit(){
            if(this.team){
                let office = this.user.main == 'D01'?this.user.office:this.user.unit
                if(office == this.team && this.approved.Status == 0){
                    return true
                }else{
                    return false
                }
                   
            }else{
                return false
            }
        },
        // disable commander approve->this function made due to restric offices
        disableApproved(){
            if(['T01','T02','T03'].includes(this.user.office)){
              
                if(this.approved.Status == 1){
                    return false
                }else{
                    return true
                }
            }else{
                // console.log('false')
                return true
            }
        },
        //unit name
        unitName(){
            let data
            this.teamItems.filter(r=>{
                if(r.UnitTeamCode == this.team){
                    data = r.UnitTeamName
                }
            })
            return data
        },
        
        yearItems(){
            const tempData = []
            const currentYear = moment().year();
            const endYear = currentYear + 5;
  
            for (let year = 2020; year <= endYear; year++) {
                tempData.push(year);
            }
  
            return tempData;
  
        },
        reasonDetails(){
            let data = {
                table : 'equipment',
                month : this.generateDate,
                unit : this.team
            }
            return data
        },
        dataDownload(){
            let obj = {
                    tableData : this.tableData,
                    totalTable : this.totalTable,
                    teamName :this.unitName,
                    month : this.formattedDate,
                    totalPrimaryRequired : this.totalPrimaryRequired,
                    totalSecondaryRequired : this.totalSecondaryRequired,
                    totalPrimaryActual : this.totalPrimaryActual,
                    totalSecondaryActual : this.totalSecondaryActual,
                    averagePrimary : this.averagePrimary,
                    averageSecondary : this.averageSecondary,
                    mee : this.mee,
                    see : this.see,
                    readinessRating : this.readinessRating,
                    remarksData : this.remarksData,
                    assesment : this.$store.state.remarksData && this.$store.state.remarksData.length > 0?true:false,
                    status : this.approved.Status,
                    sign : this.timelineData
                
                }
            return obj
        }
    },
    created(){
        if(!this.$store.state.user.rights){
            this.$store.state.departmentData =  this.$store.state.user.main
            this.$store.dispatch('getTeam')
        }
    },
    mounted(){
        this.$store.dispatch('getDepartment')
        this.department = this.user.main
        this.$store.state.departmentData = this.department
        this.user.rights?this.team = null:this.user.main == 'D01'?this.team = this.user.office:this.team = this.user.unit
    },
    watch:{

        department(val){
            if(val){
                this.$store.state.departmentData = val
                this.$store.dispatch('getTeam')
            }
        },
        declinedData(val){
            if(val){
                this.generate()
            }
        }
        // totalTable(val){
        //     console.log(val,'ceo')
        // }
     
    }
  }
  </script>
