<template>
    <div>
        <v-dialog v-model="dialog" persistent width="70%" scrollable>
            <v-card>
                <v-card-title style="background-color: #0275d8;color:white;">{{ dialogTitle }}
                    <v-spacer /><v-btn  @click="dialog = false" icon><v-icon color="white">mdi-close-circle-outline</v-icon></v-btn>
                </v-card-title>
                <br />
                <br />
                
                <v-card-text>
                    <div>

                        <br />
                        <v-divider/>
                        <v-textarea
                            outlined
                            dense
                            v-model="declineReason"
                            auto-grow
                            label="Reason for Decline"
                            style="white-space: pre-wrap;"
                        />
                    </div>
                    
                </v-card-text>
                <v-card-actions>
                    <v-btn block color="success" @click="reasonBtn()">SAVE</v-btn>
                </v-card-actions>
                <v-overlay :value="overlay">
                    <v-progress-circular
                    indeterminate
                    size="70"
                    />
                    <br />
                     <span>SAVING</span>
                </v-overlay>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
import Declined from '../Details/Declined'
import axios from 'axios'
    export default {
        props:[
            'getEquipment',
            'getTraining',
            'getApprovedMet',
            'getPersonnel',
            'getMaintenance',
            'getAll'
        ],
        data() {
            return {
                dialog:false,
                declineReason:null,
                dialogTitle:null,
                item : {},
                overlay:false

            }
        },
        methods:{
            openDialog(data){
                this.item = {...data}
                this.dialogTitle = data.title
                console.log(this.item.month)
                this.dialog = true
            },
            reasonBtn(){
                const confirmed = window.confirm('Are you sure you want to decline this report?');
                if(confirmed){
                    this.overlay = true
                    axios({
                        method:'post',
                        url:`${this.$baseUrl}api/declinedReason`,
                        data:{
                            time : this.item.today,
                            month : this.item.month,
                            unit : this.item.team,
                            table : this.item.table,
                            name : this.item.name,
                            reason : this.declineReason
                        }
                    }).then(res=>{
                        if(res.status == 200){
                            alert('Report was declined')
                            this.overlay = false
                            this.dialog = false
                            this.declineReason = null
                            console.log(this.item.table,'personnel yesfor')
                            if(this.item.table == 'training'){
                                this.getApprovedMet('declined')
                                this.getTraining()
                                Declined(this.item)
                            }
                            else{
                                this.$emit('data-to-parent',true);
                                Declined(this.item)
                            }

                            // Declined(this.item)
                            
                           
                        }
                    })
                }
            }

        }
    }
</script>

