<template>
    <div>
        <h1><center>Facility  Readiness</center></h1>
        <h2><center>{{ formattedDate }}</center></h2>
        <v-divider/>
        <v-divider/>
        <v-col cols="12" md="12" lg="12" xl="12">
                <v-tabs v-model="facility_tab" justify-center>
                    <v-tab href="#fillup" @click="fillup('fillup')">FACILITY FILL-UP</v-tab>
                    <v-tab href="#raiting" @click="raiting('raiting')">FACILITY RATING</v-tab>
                    <v-tab href="#read" @click="read('read')" v-if="tableData.length > 0">REPORT READINESS</v-tab>
                </v-tabs>
        </v-col>
        <v-tabs-items v-model="facility_tab">
            <v-tab-item :value="'fillup'">
                <v-row>
                    <v-col cols="12" md="3" lg="3" xl="3">
                        <v-row>
                            <v-col cols="12" md="12" lg="12" xl="12">
                                <v-card elevation="3" class="justify-center">
                                    <v-card-title class="d-flex justify-center" style="background-color: #0275d8; color: white;">
                                        CONTROL
                                    </v-card-title>
                                    <v-container>
                                            <br>
                                           
                                            <v-row>
                                                <v-col cols="12"  v-if="user.rights == '1'  || ['4','3'].includes(user.res) && user.main == 'D01'?true:false">
                                                    <v-autocomplete 
                                                        label="Unit"
                                                        dense
                                                        outlined
                                                        style="margin-top:-30px;"
                                                        v-model="department"
                                                        :items="departmentItems"
                                                        item-text="UnitDepartmentName"
                                                        item-value="UnitDepartmentCode"
                                                       
                                                    />
                                                </v-col>
                                                <v-col cols="12" md="12" lg="12" xl="12" v-if="user.rights == '1'  || ['4','3'].includes(user.res)?true:false">
                                                    <v-autocomplete 
                                                        label="Sub-Unit"
                                                        dense
                                                        outlined
                                                        style="margin-bottom:-40px;margin-top:-30px;"
                                                        v-model="team"
                                                        :items="teamItems"
                                                        item-text="UnitTeamName"
                                                        item-value="UnitTeamCode"
                                                    />
                                                </v-col>
                                                <v-col cols="12" md="12" lg="12" xl="12">
                                                    <v-dialog ref="dialog" v-model="generateMonthDialog" :return-value.sync="generateDate" persistent width="290px">
                                                        <template v-slot:activator="{ on, attrs }">
                                                            <v-text-field v-model="formattedDate" label="Select Month" prepend-inner-icon="mdi-calendar" readonly v-bind="attrs" v-on="on" dense outlined class="text-center" ></v-text-field>
                                                        </template>
                                                        <v-date-picker v-model="generateDate" type="month" scrollable>
                                                            <v-spacer></v-spacer>
                                                            <v-btn text color="primary" @click="generateMonthDialog = false">Cancel</v-btn>
                                                            <v-btn text color="primary" @click="[$refs.dialog.save(generateDate),tableData = []]">OK</v-btn>
                                                        </v-date-picker>
                                                    </v-dialog>
                                                </v-col>
                                                <v-col cols="12" md="12" lg="12" xl="12">
                                                    <v-btn color="warning" medium block @click="generate()"  class="mb-2" :disabled=" !generateDate  || generateDate > monthnow?true:false">GENERATE</v-btn>
                                                    <v-btn color="primary" medium block class="mb-2" :disabled="tableData.length == 0 || !approved.Status && ['1','2'].includes(user.approver)?true:false" @click="print()">PRINT</v-btn>
                                                    <v-btn v-show="this.user.approver == 2" color="error" medium block class="mb-2" :disabled="tableData.length == 0 || approved.Status != 1 && user.approver == 2 ?true:false" @click="declined(generateDate,team,user.approver)">DECLINED</v-btn>
                                                    <v-btn v-show="this.user.approver == 3" color="error" medium block class="mb-2" :disabled="tableData.length == 0 || approved.Status != 2 && user.approver == 3 ?true:false" @click="declined(generateDate,team,user.approver)">DECLINED</v-btn>
                                                    <v-btn v-show="this.user.approver == 4" color="error" medium block class="mb-2" :disabled="tableData.length == 0 || approved.Status != 3 && user.approver == 4 ?true:false" @click="declined(generateDate,team,user.approver)">DECLINED</v-btn>
                                                    <v-btn v-show="this.user.approver == 5" color="error" medium block class="mb-2" :disabled="tableData.length == 0 || approved.Status != 4 && user.approver == 5 && disableApproved ?true:false" @click="declined(generateDate,team,user.approver)">DECLINED</v-btn>
                                                    <v-btn v-show="this.user.approver == 1" color="success" medium block @click="submitData(generateDate,team)" :disabled="tableData.length == 0 || !disableSubmit?true:false">SUBMIT</v-btn>
                                                    <v-btn v-show="this.user.approver == 2" color="success" medium block @click="approvedData(generateDate,team,'2')" :disabled="tableData.length == 0  || approved.Status != 1 ?true:false">CONCURRENCE</v-btn> <!-- OIC-->
                                                    <v-btn v-show="this.user.approver == 3" color="success" medium block @click="approvedData(generateDate,team,'3')" :disabled="tableData.length == 0  || approved.Status != 2 ?true:false">CONCURRENCE</v-btn> <!-- EX-O-->
                                                    <v-btn v-show="this.user.approver == 4" color="success" medium block @click="approvedData(generateDate,team,'4')" :disabled="tableData.length == 0  || approved.Status != 3 ?true:false">CONCURRENCE</v-btn> <!-- DEPUTY-->
                                                    <v-btn v-show="this.user.approver == 5" color="success" medium block @click="approvedData(generateDate,team,'5')" :disabled="tableData.length == 0  || approved.Status != 4 && disableApproved ?true:false">APPROVED</v-btn> <!-- COMMANDER-->
                                                        <div v-if="tableData.length > 0">
                                                            <br />
                                                            <hr />
                                                            <center>
                                                                <StatusTimeline ref="StatusTimeline" 
                                                                :month="generateDate" 
                                                                :department="department" 
                                                                :team="team" 
                                                                :table="'facility'"
                                                                @fromTimeline="handledTimelineData"
                                                                />
                                                            </center>
                                                        </div>
                                                </v-col>
                                            </v-row>
                                    </v-container>
                                </v-card>
                            </v-col>
                        </v-row>
                         <!-- reason card history -->
                         <v-row>
                            <v-col cols="12" xl="12" md="12" lg="12">
                                <DeclinedCard ref="DeclinedCard" :details="reasonDetails"/>
                            </v-col>
                        </v-row>
                    </v-col>
        
                    <v-col cols="12" md="9" lg="9" xl="9">
                        <v-card>
                            <v-simple-table class="elevation-1 custom-table" fixed-header height="90vh">
                                <thead>
                                    <tr>
                                        <th width="20%">Description</th>
                                        <th>Required</th>
                                        <!-- <th>Required<br/>sq.m.</th> -->
                                        <th>Actual</th>
                                        <!-- <th>Actual<br/>sq.m.</th> -->
                                        <th>Physical<br>Apperance</th>
                                        <th>Safety</th>
                                        <th>Functionality<br>Sanitation</th>
                                        <th>Building Plans<br>Documents</th>
                                        <th>Update</th>
                                    </tr>
                                </thead>
                                 <tbody v-for="(item,i) in facilityTable" :key="i" v-show="approved.Status != 0 && user.approver != 1 || user.approver == 1">
                                    <tr >
                                        <td 
                                            :style="item.Title?'font-weight:bold;text-align:left !important;':'text-align:left !important;'"
                                            :colspan="item.Title?'10':'1'"
                                        >
                                            {{ item.Title == 1?item.Detail.toUpperCase():item.Detail }}
                                        </td>
                                        <td v-if="!item.Title">{{ item.Required }}</td>
                                        <v-tooltip bottom  transition="slide-x-reverse-transition">
                                            <template v-slot:activator="{on: tooltip}">
                                                <td v-if="!item.Title" @dblclick="editActual = true; editIndex = i"  :style="approved.Status == 0 && item.Required?'background-color:lightyellow;':''" v-on="{...tooltip}">
                                                    <template v-if="editActual && editIndex === i && item.Status == '0' && item.Required ">
                                                        <input type="number" v-model="item.Actual"  @blur="updateRating(item)" style="text-align:center !important;"/>
                                                    </template>
                                                    <template v-else>
                                                        <div style="white-space: pre-wrap;text-align:center !important;">{{ item.Actual?item.Actual:'' }}</div>
                                                    </template>
                                                </td>
                                            </template>
                                            <span>Double click to edit</span>
                                        </v-tooltip>
                                        <td v-if="!item.Title">{{ item.PA?item.PA+'%':'' }}</td>
                                        <td v-if="!item.Title">{{ item.SA?item.SA+'%':'' }}</td>
                                        <td v-if="!item.Title">{{ item.HS?item.HS+'%':'' }}</td>
                                        <td v-if="!item.Title">{{ item.BP?item.BP+'%':'' }}</td>
                                        <td v-if="!item.Title && item.Required">
                                            <v-tooltip bottom  transition="slide-x-reverse-transition">
                                                <template v-slot:activator="{on: tooltip}">
                                                    <v-btn icon @click="openDialog(item)" v-on="{...tooltip}">
                                                        <v-icon color="warning" v-if="!approved">mdi-table-edit</v-icon>
                                                        <v-icon color="success" v-else>mdi-eye</v-icon>
                                                    </v-btn>
                                                </template>
                                                <span>Click to open updating details</span>
                                            </v-tooltip>

                                        </td>
                                    </tr>
                                </tbody>
                            </v-simple-table>
                        </v-card>
                    </v-col>
                </v-row>
            </v-tab-item>
            <v-tab-item :value="'raiting'">
               <v-container>
                <center><h2>FACILITY  RATING</h2></center>
                    <v-row>
                        <!--  -->
                        
                        <v-col cols="12">
                            <v-card>
                                <v-simple-table class="elevation-1 custom-table" fixed-header height="90vh">
                                    <thead>
                                        <tr>
                                            <th width="20%" rowspan="2">Facility</th>
                                            <th colspan="3">Quantitative</th>
                                            <th>Qualitative</th>
                                            <th rowspan="2">Rating</th>
                                            <th rowspan="2">REDCON</th>
                                        </tr>
                                        <tr>
                                            <th>Required</th>
                                            <th>Actual</th>
                                            <th>Rating</th>
                                            <th>Rating</th>
                                        </tr>
                                    </thead>
                                     <tbody >
                                        <template v-for="(item,i) in facilityTable">
                                            <tr :key="'main'+ i">
                                              <td 
                                                :style="item.Detail == item.Info?'font-weight:bold;text-align:left !important;':'text-align:left !important;'"
                                                :colspan="item.Title == '1'?'5':'1'"
                                              >
                                                {{ item.Detail }}
                                              </td>
                                              <template v-if="item.Title != '1'">
                                                <td >{{ item.Required }}</td>
                                                <td >{{ item.Actual }}</td>
                                                <td >{{ item.Rating?item.Rating.toFixed(2)+'%':'' }}</td>
                                                <td >{{ item.overall?item.overall.toFixed(2)+'%':'' }}</td>
                                              </template>
                                              <td v-if="item.count > 0" :rowspan="item.count > 0 ? item.count : 1">
                                                {{ item.facilityRating?item.facilityRating.toFixed(2)+'%':'' }} 
                                              </td>
                                              <td v-if="item.count > 0" 
                                                :rowspan="item.count > 0 ? item.count : 1" 
                                                :class="redConStyle(redConResult(item.facilityRating))"
                                                style="font-size:25px !important;"
                                                >
                                                {{ item.facilityRating?redConResult(item.facilityRating).substr(0,2):'' }}
                                              </td>
                                            </tr>
                                            <template >
                                              <tr v-if="!item.count && (!facilityTable[i + 1] || facilityTable[i + 1].count)" style="background-color:lightgray;" :key="'sub'+ i">
                                                <td style="font-weight:bold;font-size:15px;" colspan="3">Average Rating</td>
                                                <td style="font-weight:bold;font-size:15px;">{{ item.ratingQuanPercent?item.ratingQuanPercent.toFixed(2)+'%':'' }}</td>
                                                <td style="font-weight:bold;font-size:15px;">{{ item.ratingQualPercent?item.ratingQualPercent.toFixed(2)+'%':'' }}</td>
                                                <td style="font-weight:bold;font-size:15px;"></td>
                                                <td style="font-weight:bold;font-size:15px;" colspan="2"></td>
                                                <!-- <td style="font-weight:bold;font-size:15px;"></td> -->
                                              </tr>
                                            </template>
                                          </template>
                                    </tbody>
                                </v-simple-table>
                            </v-card>
                        </v-col>
                    </v-row>
                    <v-divider />
                    <br>
                        
                    
                </v-container>
            </v-tab-item>

            <v-tab-item :value="'read'">
               <v-container>
                <center><h2>FACILITY OVERALL READINESS RATING</h2></center>
                    <v-row>
                        <!--  -->
                        
                        <v-col cols="12">
                            <v-card>
                                <v-simple-table class="elevation-1 custom-table">
                                    <thead>
                                        <tr>
                                            <th>Category<br>(a)</th>
                                            <th>Required<br>Area<br>(b)</th>
                                            <th>Actual<br>Area<br>(c)</th>
                                            <th>Quantitative<br>(c/d x 100)<br>(d)</th>
                                            <th>Qualitative<br>(e)</th>
                                            <th>Total<br>(d + e)<br>/2</th>
                                            <th>Percentage<br>(Weighted<br>distribution)</th>
                                        
                                        </tr>
                                    </thead>
                                     <tbody >
                                        <tr v-for="(item,i) in facilityReport" :key="i">
                                            <td>{{ item.Detail }}</td>
                                            <td>{{ item.Required }}</td>
                                            <td>{{ item.Actual }}</td>
                                            <td>{{ item.Quantitative?item.Quantitative.toFixed(2)+'%':0 }}</td>
                                            <td>{{ item.Qualitative?item.Qualitative.toFixed(2)+'%':0 }}</td>
                                            <td>{{ item.total?item.total.toFixed(2)+'%':0 }}</td>
                                            <td>{{ item.weight?item.weight.toFixed(2)+'%':0 }}</td>
                                            <!-- <td class="readiness">{{ facilityReport.quantitative?facilityReport.quantitative.toFixed(2)+'%':0 }}</td>
                                            <td class="readiness">{{ facilityReport.qualitative?facilityReport.qualitative.toFixed(2)+'%':0 }}</td>
                                            <td class="readiness">{{ facilityReport.readinnesRating?facilityReport.readinnesRating.toFixed(2)+'%':0 }}</td> -->
                                        </tr>
                                       
                                              <tr>
                                                <td colspan="6" style="font-weight:bold;text-align:right !important;">TOTAL</td>
                                                <td colspan="2" style="font-weight:bold;">{{ readiness.toFixed(2)+'%' }}</td>
                                            </tr>
                                            <tr>
                                                <td colspan="6" style="font-weight:bold;text-align:right !important;">REDCON STATUS</td>
                                                <td :class="redConStyle(redConResult(readiness))">{{ redConResult(readiness) }}</td>
                                            </tr>
                                    
                                        <!-- <tr>
                                            <td colspan="2" style="font-weight:bold;background-color:lightgrey;">REDCON</td>
                                            <td :class="redConStyle(redConResult(facilityReport.readinnesRating))">{{ redConResult(facilityReport.readinnesRating) }}</td>
                                        </tr> -->
                                    </tbody>
                                </v-simple-table>
                            </v-card>
                        </v-col>
                    </v-row>
                    <v-divider />
                    <br>
                        
                    <!------------------assessment table------------------------- -->
                    <h3>Assessment:</h3>
                        <v-card elevation="2">
                            <v-simple-table class="custom-table">
                                <tbody v-for="(val, e) in remarksData" :key="e">
                                    <tr>
                                        <td style="font-weight: bold;text-align: left !important;background-color: lightgray;">{{ val.Question }}</td>
                                    </tr>
                                    <tr height="90%">
                                        <td @dblclick="editing = true; editIndex = e" style="text-align: left !important;white-space: pre-wrap;">
                                            <template v-if="editing && editIndex === e">
                                                <div contenteditable  @blur="updateRemarks(val)" style="white-space: pre-wrap;">{{ val.Remarks }}</div>
                                            </template>
                                            <template v-else>
                                                <div style="white-space: pre-wrap;">{{ val.Remarks }}</div>
                                            </template>
                                    </td>
                                    </tr>
                                </tbody>  
                            </v-simple-table>
                        </v-card>
                </v-container>
            </v-tab-item>

        </v-tabs-items>


        <!-- table dialog-->
        <v-dialog v-model="dialog" persistent width="70%" >
            <v-card>
                <v-card-title style="background-color: #0275d8;color:white;">{{ dialogTitle }}
                    <v-spacer /><v-btn  @click="dialog = false" icon><v-icon color="white">mdi-close-circle-outline</v-icon></v-btn>
                </v-card-title>
                <v-card-text >
                    <br>
                    <v-simple-table  class="elevation-1 custom-table" fixed-header height="70vh">
                        <thead>
                            <tr>
                                <th width="40%">Facility</th>
                                <th width="10%">Percentage</th>
                                <th width="30%">Parameter</th>
                                <th width="20%">Rating</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(item, i) in dialogTable" :key="i">
                                <td 
                                :style="item.Code && !item.percent?'font-weight:bold;text-align:left !important;':'text-align:left !important;white-space: pre-wrap;'"
                                :colspan="!item.percent && !item.Code ?'4':!item.percent && item.Code?'3':'1'"
                                >{{ item.Detail }}
                                </td>
                                <td v-if="item.percent">{{ item.percent?item.percent+'%':'' }}</td>
                                <td v-if="item.percent">
                                    <v-autocomplete 
                                        v-model="item.parameter"
                                        :readonly="approved.Status != 0?true:false"
                                        :clearable="approved.Status == 0?true:false"
                                        dense
                                        outlined
                                        :items="parameter"
                                        item-text="text"
                                        item-value="value"
                                        class="custom-autocomplete"
                                        @change="parameterChange(item)"
                                    />
                                </td>
                                <td v-if="item.percent">{{ item.rating?item.rating+'%':'' }}</td>
                            </tr>
                        </tbody>
                    </v-simple-table>
                </v-card-text>
                <v-card-actions>
                    <v-btn color="success" block large @click="updateDialog()" :disabled="approved.Status != 0?true:false">UPDATE</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-overlay :value="loadingPage">
            <v-progress-circular
                indeterminate
                size="70"
            >
                
            </v-progress-circular>
            <br>
            <span>LOADING</span>
        </v-overlay>
                
        <FacilityPrint ref="FacilityPrint"/>
        <!-- <RemarksDialog ref="RemarksDialog" :getFacility="generate"/> -->
        <SubmitDialog ref="SubmitDialog" :getFacility="generate"/>
        <Details ref="Details"/>
        <DeclinedDialog ref="DeclinedDialog" @data-to-parent="handleDeclinedData" />
        <PrintDialog ref="PrintDialog" :printPdf="printPdf" :printExcel="extractExcel"/>
        <ExtractionExcel ref="ExtractionExcel" :excelData="dataDownload"/>
    </div>
  </template>
  
  <script>
  
    import {mapState} from 'vuex'
    import moment from 'moment'
    import FacilityPrint from './FacilityPrint.vue'
    import axios from 'axios'
    // import RemarksDialog from '../Personnel/RemarksDialog.vue'
    import SubmitDialog from '../Details/SubmitDialog.vue'
    import Details from '../Details/RedConDetails.vue';
    import Approved from '../Details/Approved'
    import DeclinedDialog from '../Details/DeclinedDialog'
    import DeclinedCard from '../Details/DeclinedCard'
    import ExtractionExcel from './ExtractionExcel.vue'
    import PrintDialog from '../Details/PrintDialog'
    import StatusTimeline from '../Details/StatusTimeline.vue'
  export default {
    name:'FacilityComponent',
    components:{
        FacilityPrint,
        // RemarksDialog,
        SubmitDialog,
        Details,
        DeclinedDialog,
        DeclinedCard,
        ExtractionExcel,
        PrintDialog,
        StatusTimeline
    },
    data:()=>({
        facility_tab:'fillup',
        tableData:[],
        department:null,
        editing:false,
        editActual:false,
        editRequired:false,
        editIndex:null,
        generateMonthDialog: false,
        generateDate: null,
        id:null,
        team:null,
        year:moment().year(),
        rules:{
            required: [
               (v) => !!v || 'Field is required'
            ],
            existing:[(v) => v !== '1' || "Value '1' is not accepted"],
        },
        snackbar:{
            show: false,
            message: null,
            color: null,
            icon: null,
        },
        timeout:1500,
        monthnow:moment().format('YYYY-MM'),
        dialogTitle:'',
        dialogTable:[],
        dialog:false,
        parameter:[
            {text:'Beyond economical repair/Document are incomplete and not updated',value:1},
            {text:'Needs major repair/Documents are complete but not updated',value:2},
            {text:'Needs minor repair/Documents are lacking but the lacking documents are under process',value:3},
            {text:'Need no repair/Documents are complete',value:4},
            {text:'Not Applicable',value:'4'},
        ],
        declinedData:false,
        timelineData:{},
        updateDetailStatus:false
    }),
    methods:{
        async callGetTimeline() {
            if (this.$refs.StatusTimeline) {
                await this.$refs.StatusTimeline.getTimeline();
            } else {
                console.error("StatusTimeline ref not available yet.");
            }
        },
  
        generate(){
            this.$store.state.loadingPage = true
            axios({
                method:'post',
                url:`${this.$baseUrl}api/getFacility`,
                data:{
                    Team : this.team,
                    Month : this.generateDate,
                    Name : this.user.name,
                    Time : moment().format('YYYY-MM-DD HH:mm:ss')
                }
            }).then(async r=>{
                if(r.status == 200){
                    this.tableData = r.data
                    this.$store.state.teamData = this.team
                    this.$store.state.monthData = this.generateDate
                    if(this.updateDetailStatus == false){
                        this.$store.dispatch('diplayRemarks','facility')
                        this.$refs.DeclinedCard.getReason()
                        await this.callGetTimeline()
                    }
                    this.$store.state.loadingPage = false
                    this.updateDetailStatus = false
                    
                }
            })
        },
        fillup(tab){
            this.facility_tab = tab
        },
        raiting(tab){
            this.facility_tab = tab
        },
        read(tab){
            this.facility_tab = tab
            this.$store.dispatch('diplayRemarks','facility')
        },
        updateRating(item){
            if(item.Actual > item.Required || item.ActualArea > item.RequiredArea){
                alert('Error')
                this.generate()
            }else{
                this.updateDetailStatus = true
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/updateRating`,
                    data:{
                        data : item,
                        rating : (item.Actual / item.Required) * 100
                    }
                }).then(r=>{
                    if(r.status == 200){
                        this.generate()
                    }else{
                        alert('Error:Reinput value')
                    }
                })     
            }
               
        },
        openDialog(item){
            this.id = item.id
            this.dialogTitle = item.Detail
            this.dialogTable = JSON.parse(item.Item)
            this.dialog = true
        },
        updateDialog(){
            this.updateDetailStatus = true
            // const table = []
            let obj = {
                    apperance : 0,
                    safety : 0,
                    sanitation : 0,
                    building : 0,
                    all : 0,
                }
            for (const item of this.dialogTable) {
               
                if(item.Code && item.Code.includes('PA') && item.rating){
                    obj.apperance += item.rating
                }
                if(item.Code && item.Code.includes('SA') && item.rating){
                    obj.safety += item.rating
                }
                if(item.Code && item.Code.includes('HS') && item.rating){
                    obj.sanitation += item.rating
                }
                if(item.Code && item.Code.includes('BP') && item.rating){
                    obj.building += item.rating
                }
                if(item.Code && item.rating){
                    obj.all += item.rating
                }
            }

            axios({
                method:'post',
                url:`${this.$baseUrl}api/updateDetail`,
                data:{
                    id : this.id,
                    data : this.dialogTable,
                    apperance : obj.apperance,
                    safety : obj.safety,
                    sanitation : obj.sanitation,
                    building : obj.building,
                    all : obj.all,
                    Name : this.user.name,
                    Time : moment().format('YYYY-MM-DD HH:mm:ss')
                }
            }).then(r=>{
                if(r.status == 200){
                    this.dialog = false
                    this.dialogTable = []
                    this.id = null
                    this.generate()
                }else{
                    alert('Error:Reinput value')
                }
            })

        },
        parameterChange(item){
            let rating = item.parameter/4 * item.percent
            item.rating = rating
        
        },
       

      
       // submit function
       submitData(month,team){
            const confirmed = window.confirm('Are you sure you want to submit this report?');
            if(confirmed){
                this.$store.dispatch('diplayRemarks','facility')
                this.$store.dispatch('getQuestion','facility')
                this.$refs.SubmitDialog.openDialog(month,team,'facility')
            }
        },
        //approvedData
        approvedData(month,team,status){
            const confirmed = window.confirm('Are you sure you want to approved this report?');
            if(confirmed){
                let data = {
                    month : month,
                    team : team,
                    table : 'facility',
                    name : this.$store.state.user.name,
                    url : this.$baseUrl,
                    status : status
                }
            return Promise.all([Approved(data),this.generate()]) ;
            }
        },
        //declinedData
        declined(month,team){
            let data = {
                month : month,
                title : `Reason for the Decline in the Reporting Month of ${this.formattedDate}`,
                team : team,
                table : 'facility',
                name : this.$store.state.user.name,
                url : this.$baseUrl,
                today : moment().format('YYYY-MM-DD HH:mm:ss')
            }
            this.$refs.DeclinedDialog.openDialog(data)
        },
        handleDeclinedData(data){
            this.declinedData = data 
        },
        handledTimelineData(data){
            this.timelineData = {...data[0]}
        },

        // print function
        print() {
            if(this.remarksData.length > 0){
                this.$store.dispatch('diplayRemarks','facility')
                this.$refs.PrintDialog.openDialog()
            }else{
                alert('Kindly ensure that you submit the report before proceeding to print.')
            }
            

            // const confirmed = window.confirm('Are you sure you want to print this report?');
            // if(confirmed){
            //     this.$store.dispatch('diplayRemarks','facility')
            //     let obj = {
            //         tableData : this.facilityTable,
            //         report : this.facilityReport,
            //         readiness : this.readiness,
            //         teamName :this.unitName,
            //         month : this.formattedDate,
            //         remarksData : this.remarksData,
            //         assesment : this.$store.state.remarksData.length > 0?true:false
                
            //     }
            //     this.$refs.FacilityPrint.print(obj)
            // }
        },
        printPdf(){
            this.$refs.FacilityPrint.print(this.dataDownload)
        },
        extractExcel(){
                this.$refs.ExtractionExcel.exportExcel()
        },
       
      
        redConResult(data){
            if(data){
                return this.$refs.Details.redCon(Number(data))
            }
        },
        redConStyle(data){
            if(data){
                return this.$refs.Details.redConStyle(data)
            }
        },

        

     
    },
    computed:{
        ...mapState([
            'departmentItems',
            'teamItems',
            'psgTable',
            'remarksData',
            'user',
            'loadingPage'
        ]),

      

        

         // function to show month name
         formattedDate() {
                if (this.generateDate) {
                    const date = new Date(this.generateDate);
                    const month = date.toLocaleString('default', { month: 'long' });
                    const year = date.getFullYear();
                    return `${month} ${year}`;
                }
                return null;
        },
        facilityTable(){
            return this.tableData.reduce((total,item)=>{
                let i = total.findIndex(res=> res.Info == item.Info)

                if(i == -1){
                    let obj = {...item}
                    obj.count = 1
                    obj.countQuanRating = 0
                    obj.countQualRating = 0
                    obj.quanRating = item.Rating
                    obj.qualRating = item.overall
                    obj.quanPercent = 0
                    obj.qualPercent = 0
                    obj.facilityRating = 0
                    total.push(obj)
                }else{
                    total[i].count += 1
                    total[i].quanRating += item.Rating
                    total[i].qualRating += item.overall
                    if(item.Rating){
                        total[i].countQuanRating += 1
                    }
                    if(item.overall){
                        total[i].countQualRating += 1
                    }
                    total[i].quanPercent  = (total[i].quanRating / total[i].countQuanRating) * 0.5;
                    total[i].qualPercent  = (total[i].qualRating / total[i].countQuanRating) * 0.5;
                    item.ratingQuanPercent = (total[i].quanRating / total[i].countQuanRating) * 0.5;
                    item.ratingQualPercent = (total[i].qualRating / total[i].countQualRating) * 0.5;
                    total[i].facilityRating = item.ratingQuanPercent +  item.ratingQualPercent
                    // }
                   
                   
                   

                    total.push(item)
                }
                return total
            },[])

        },
        facilityReport(){
           
             return this.facilityTable.reduce((total,item)=>{
           
            let i = total.findIndex(res=> res.Info == item.Info)
            if(i == -1){
                let obj = {...item}
                obj.Required = item.Required
                obj.Actual = item.Actual
                obj.Quantitative = 0
                obj.Qualitative = 0
                obj.total = 0
                obj.weight = 0
                total.push(obj)
            }else{
                total[i].Required += item.Required
                total[i].Actual += item.Actual
                total[i].Quantitative = item.ratingQuanPercent * 2
                total[i].Qualitative = item.ratingQualPercent * 2
                total[i].total = (total[i].Quantitative + total[i].Qualitative ) / 2
                total[i].weight =  total[i].total?total[i].total / 2:0
            }
            return total
            },[])

        },
        readiness(){
            let val = 0
            for (const item of this.facilityReport) {
                if(item.weight){
                    val += item.weight
                }
            }
            return val
        },
        // to check if the unit is submit
        submitted(){
            if(this.tableData.length > 0){
                return this.tableData.find(res=> res.Submit == '1')
            }else{
                return 0
            }
        },
        // to check if the unit is approved by approver
        approved(){
            if(this.tableData.length > 0){
                return this.tableData.find(res=> res.Status)
            }else{
                return 0
            }
        },
        // function for disable sumbit btn
        disableSubmit(){
            if(this.team){
                let office = this.user.main == 'D01'?this.user.office:this.user.unit
                if(office == this.team && this.approved.Status == 0){
                    return true
                }else{
                    return false
                }
                   
            }else{
                return false
            }
        },
        // disable commander approve->this function made due to restric offices
        disableApproved(){
            if(['T01','T02','T03'].includes(this.user.office)){
              
                if(this.approved.Status == 1){
                    return false
                }else{
                    return true
                }
            }else{
                // console.log('false')
                return true
            }
        },
        //unit name
        unitName(){
            let data
            this.teamItems.filter(r=>{
                if(r.UnitTeamCode == this.team){
                    data = r.UnitTeamName
                }
            })
            return data
        },
        
        yearItems(){
            const tempData = []
            const currentYear = moment().year();
            const endYear = currentYear + 5;
  
            for (let year = 2020; year <= endYear; year++) {
                tempData.push(year);
            }
  
            return tempData;
  
        },
        reasonDetails(){
            let data = {
                table : 'facility',
                month : this.generateDate,
                unit : this.team
            }
            return data
        },
        dataDownload(){
            let obj = {
                tableData : this.facilityTable,
                report : this.facilityReport,
                readiness : this.readiness,
                teamName :this.unitName,
                month : this.formattedDate,
                remarksData : this.remarksData,
                assesment : this.remarksData.length > 0?true:false,
                status : this.approved.Status,
                sign : this.timelineData
            }
            return obj
        }
    },
    created(){
        if(!this.$store.state.user.rights){
            this.$store.state.departmentData =  this.$store.state.user.main
            this.$store.dispatch('getTeam')
        }
    },
    mounted(){
        this.$store.dispatch('getDepartment')
        this.department = this.user.main
        this.$store.state.departmentData = this.department
        this.user.rights?this.team = null:this.user.main == 'D01'?this.team = this.user.office:this.team = this.user.unit
    },
    watch:{
        dialog(val){
            if(!val){
                this.id = null
            }
        },
      
       
        department(val){
            if(val){
                this.$store.state.departmentData = val
                this.$store.dispatch('getTeam')
            }
        },
        declinedData(val){
            if(val){
                this.generate()
            }
        },
        // timelineData(val){
        //     if(val){
        //         console.log(val.PreparedBy)
        //     }
        // }
       
     
    }
  }
  </script>
