import Vue from 'vue'
import VueRouter from 'vue-router'
import store from "../store";


import WelcomePage from '../components/WelcomePage'
import Login from '../components/Login'
import Personnel from '../components/Personnel/Personnel'
import Training from '../components/Training/Training'
import Equipment from '../components/Equipment/Equipment'
import Maintenance from '../components/Maintenance/Maintenance'
import Facility from '../components/Facility/Facility'
import Admin from '../components/Admin/Admin'
import Report from '../components/Admin/Report'
import TrainingAdmin from '../components/Admin/TrainingAdmin'
import EquipmentAdmin from '../components/Admin/EquipmentAdmin'
import FacilityAdmin from '../components/Admin/FacilityAdmin'
import AddUser from '../components/Admin/AddUser'
import Inventory from '../components/Admin/Inventory'
import Ceo from '../components/Ceo/AddUpdate'
import SectionReport from '../components/Report/SectionReport'
// import TeamReport from '../components/Report/TeamReport'
import HomeView from '../views/HomeView.vue'
Vue.use(VueRouter)

const routes = [
    {
        path: '/welcome',
        name: 'welcome',
        component: WelcomePage,
        meta:{
            hideNavbar: true,
        }
    },
    {
        path: '/login',
        name: 'login',
        component: Login,
        meta:{
            hideNavbar: true,
        }
    },
    {
        path: '/home',
        name: 'home',
        component: HomeView
    },
    {
        path: '/personnel',
        name: 'personnel',
        component: Personnel
    },
    {
        path: '/training',
        name: 'training',
        component: Training
    },

    {
        path: '/equipment',
        name: 'equipment',
        component : Equipment
    },
    {
        path: '/maintenance',
        name: 'maintenance',
        component : Maintenance
    },
    {
        path: '/facility',
        name: 'facility',
        component : Facility
    },
    {
        path: '/admin',
        name: 'admin',
        component : Admin
    },
    {
        path: '/ceo',
        name: 'ceo',
        component : Ceo
    },
    {
        path: '/report',
        name: 'report',
        component : Report
    },
    {
        path: '/trainingAdmin',
        name: 'trainingAdmin',
        component : TrainingAdmin
    },
    {
        path: '/equipmentAdmin',
        name: 'equipmentAdmin',
        component : EquipmentAdmin
    },
    {
        path: '/facilityAdmin',
        name: 'facilityAdmin',
        component : FacilityAdmin
    },
    {
        path: '/addUser',
        name: 'addUser',
        component : AddUser
    },
    {
        path: '/inventory',
        name: 'inventory',
        component : Inventory
    },
    {
        path: '/unitReport',
        name: 'unitReport',
        component : SectionReport
    }
    // ,
    // {
    //     path: '/officeReport',
    //     name: 'OfficeReport',
    //     component : TeamReport
    // }
]

const router = new VueRouter({
    // mode: 'history',
    // base: '/Afp_jrrs/',
    routes
})

router.beforeEach((to,from,next)=>{
    if(to.path !== '/login'){
        if(store.state.user.username){
            if(to.path ==='/home'){
                next();
            }else if(to.path  ==='/personnel'){
                next();
            }else if(to.path ==='/training'){
                next();
            }else if(to.path ==='/equipment'){
                next();
            }else if(to.path ==='/maintenance'){
                next();
            }else if(to.path ==='/facility'){
                next();
            }else if(to.path ==='/admin'){
                next();
            }else if(to.path ==='/ceo'){
                next();
            }else if(to.path ==='/report'){
                next();
            }else if(to.path ==='/trainingAdmin'){
                next();
            }else if(to.path ==='/equipmentAdmin'){
                next();
            }else if(to.path ==='/facilityAdmin'){
                next();
            }else if(to.path ==='/addUser'){
                next();
            }else if(to.path ==='/unitReport'){
                next();
            // }else if(to.path ==='/officeReport'){
            //     next();
            }else if(to.path ==='/inventory'){
                next();
            }else{
                next('/home');
            }
        }else{
            next('/login');
        }

    }else{
        // next('/login');
        if(store.state.user.username){
            next('home')
        }else{
            next();
        }
    }
})

export default router

