<template>
    <div>
        <h1><center>UPDATE TABLE FACILITY</center></h1>
        <v-divider/>
        <br>
        <v-row>
            <v-col cols="12" md="3" lg="3" cl="3">
                <v-card elevation="3" class="justify-center" >
                    <v-card-title style="background-color: #0275d8;color:white;"><center>CONTROL PANEL</center></v-card-title>
                    <br />
                    <v-card-text>
                        <v-form ref="add" @submit.prevent="add">
                            <v-autocomplete 
                                label="Category"
                                item-text="CategoryName"
                                item-value="CategoryCode"
                                dense
                                outlined
                                :items="categoryItems"
                                v-model="category"
                                @change="getDepartment()"
                                :rules="rules.required"
                                clearable
                            
                            />
                            <v-autocomplete 
                                label="Unit"
                                item-text="UnitDepartmentName"
                                item-value="UnitDepartmentCode"
                                dense
                                outlined
                                :items="departmentItems"
                                v-model="department"
                                @change="getSection()"
                                :rules="rules.required"
                                :clearable="category?true:false"
                            />
                            <v-autocomplete 
                                label="Sub-Unit"
                                item-text="UnitSectionName"
                                item-value="UnitSectionCode"
                                dense
                                outlined
                                :items="sectionItems"
                                v-model="section"
                                @change="getTeam()"
                                :rules="rules.required"
                                :clearable="category && department?true:false"
                            />
                            <v-autocomplete 
                                label="Office"
                                item-text="UnitTeamName"
                                item-value="UnitTeamCode"
                                dense
                                outlined
                                :items="teamItems"
                                v-model="team"
                                :rules="rules.required"
                                v-if="category == 'CA01'"
                                @change="getTable()"
                                :clearable="category && department && section?true:false"
                            />
                            <div v-show="(category == 'CA01' && team || category != 'CA01' && section) && tableData.length == 0">
                                <input type="file" ref="fileInput" @change="handleFileSelect" accept=".csv, .xls, .xlsx"   class="file-input"/>
                                <v-btn color="success" dark block medium @click="upload()" >UPLOAD</v-btn>
                            </div>
                            <div v-show="tableData.length > 0">
                                <v-btn color="error" dark block medium @click="deleteTable('facility')" >DELETE</v-btn>
                            </div>
                            

                        </v-form>
                    </v-card-text>
                </v-card>
            </v-col>
            <v-col cols="12" md="9" lg="9" xl="9">
                <v-card>
                    <v-simple-table fixed-header class="elevation-1 custom-table">
                       <thead>
                            <tr>
                                <th>Description</th>
                                <th>Required sq.m.</th>
                               
                            </tr>
                       </thead> 
                       <tbody>
                            <tr v-for="(item,i) in tableData" :key="i">
                                <td :style="item.Detail == item.Info?'font-weight:bold;text-align:left !important;background-color:lightgrey':'text-align:left !important;'">{{ item.Detail }}</td>
                                <td>{{ item.Required }}</td>
                                <!-- <td>{{ item.RequiredArea }}</td> -->
                                <!-- <td><v-btn v-if="!item.Title" icon><v-icon color="error">mdi-delete-outline</v-icon></v-btn></td> -->
                            </tr>
                       </tbody>
                    </v-simple-table>
                </v-card>
            </v-col>
        </v-row>
        <v-overlay :value="overlay">
            <v-progress-circular
                indeterminate
                size="64"
            ></v-progress-circular>
        </v-overlay>
    </div>
</template>

<script>
    import axios from 'axios'
    import Papa from 'papaparse';
    import moment from 'moment'
    import DeleteRequired from '../Details/DeleteTable'
    import {mapState} from 'vuex'
    export default {
        data() {
            return {
                category:null,
                categoryName:null,
                categoryItems:[],
                division:null,
                department:null,
                newDepartmentName:null,
                departmentItems:[],
                section:null,
                newSectionName:null,
                sectionItems:[],
                team:null,
                newTeamName:null,
                teamItems:[],
                tableData:[],
                rules:{
                    required: [
                        v => !!v || 'Field is required'
                    ],
                },
                tempFile:[],
                uploadFile:[],
                overlay:false,
            }
        },
        methods: {
            getCategory(){
                this.tableData = []
                this.department = null
                this.section = null
                this.team = null
                axios({
                    method:'get',
                    url:`${this.$baseUrl}api/getCategory`,
                }).then(r=>{
                    this.categoryItems = r.data
                })
            },
            getDepartment(){
                this.tableData = []
                this.section = null
                this.team = null
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/getDepartment`,
                    data:{
                        data : this.category
                    }
                }).then(r=>{
                    // console.log(r.data)
                    this.departmentItems = r.data
                })
            },
            getSection(){
                this.tableData = []
                this.team = null
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/getSection`,
                    data:{
                        data : this.department
                    }
                }).then(r=>{
                    this.sectionItems = r.data

                })
            },
            getTeam(){
                this.tableData = []
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/getTeam`,
                    data:{
                        data : this.section
                    }
                }).then(r=>{
                    this.teamItems = r.data
                    if(this.category != 'CA01'){
                        this.getTable()
                    }
                })
            },
            handleFileSelect(event) {
                const file = event.target.files[0];
                const reader = new FileReader();

                reader.onload = (e) => {
                    const contents = e.target.result;

                    const parsedData = Papa.parse(contents, {
                    header: true,
                    encoding: 'UTF-8',
                    });

                    const rows = parsedData.data;

                    // Process each row
                    const headers = Object.keys(rows[0]);

                    for (let i = 0; i < rows.length; i++) {
                    const row = rows[i];
                    const rowData = {};

                    for (let j = 0; j < headers.length; j++) {
                        let value = row[headers[j]];

            

                        rowData[headers[j]] = value;
                    }

                    this.tempFile.push(rowData);
                    }
                    // console.log(this.tempFile,'data')
                };
        
                reader.readAsText(file);
            },
           
            upload() {
                this.overlay =true
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/requiredFacility`,
                    data:{
                        department : this.department,
                        section: this.section,
                        team: this.category != 'CA01'?this.section:this.team,
                        date : moment().format('YYYY-MM-DD HH:mm:ss'),
                        name : this.$store.state.user.name,
                        data : this.tempFile
                    }
                }).then(res=>{
                    if(res.status == 200){
                        this.getTable()
                    }
                    
                })
            },
            getTable(){
                this.overlay = true
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/requiredFacilityTable`,
                    data:{
                        department : this.department,
                        section: this.section,
                        team: this.category != 'CA01'?this.section:this.team,
                    }
                }).then(res=>{
                    this.tableData = res.data
                    this.overlay = false
                })
            },
            deleteTable(table){
                const confirmed = window.confirm('Are you sure you want to delete this information?');
                if(confirmed){
                    this.overlay = true
                    let data = {
                        Table : table,
                        Team : this.department == 'D01'?this.team:this.section,
                        Name: this.user.name,
                        Time: moment().format('YYYY-MM-DD HH:mm:ss'),
                        Year : moment().format('YYYY'),
                        url : `${this.$baseUrl}api/deleteRequired`
                    }
                    return DeleteRequired(data,this.getTable)
                }
            }
        },
        computed:{
            ...mapState([
                'user'
            ]),
            // function to show month name
            formattedDate() {
                    if (this.generateDate) {
                        const date = new Date(this.generateDate);
                        const month = date.toLocaleString('default', { month: 'long' });
                        const year = date.getFullYear();
                        return `${month} ${year}`;
                    }
                    return null;
            },
            
            
        },
        mounted(){
            this.getCategory()
        },
        watch: {
        
        },
    }
</script>

