<template>
    <div>
        <v-card>
            <h4 v-show="timeLine.length > 0">TIMELINE STATUS</h4>
            <v-timeline dense
                align-top
                v-for="(item,i) in timeLine"
                :key="i"
            >
                <v-timeline-item small :class="font(item.Status,1)" :color="color(item.Status,1)">
                    {{ item.PreparedBy }}<br v-if="item.PreparedDate">{{ item.PreparedDate }}<br>ENCODER/DRAFTER
                </v-timeline-item>
                <v-timeline-item small :class="font(item.Status,2)" :color="color(item.Status,2)" v-if="!['T01','T02','T03'].includes(team)">
                    {{ item.NotedBy }}<br v-if="item.NotedDate">{{ item.NotedDate }}<br>OIC CONCURRING
                </v-timeline-item>
                <v-timeline-item small :class="font(item.Status,3)" :color="color(item.Status,3)" v-if="!['T01','T02','T03'].includes(team)">
                    {{ item.NotedBy2 }}<br v-if="item.NotedDate2">{{ item.NotedDate2 }}<br>{{ statusLabel(3) }}
                </v-timeline-item>
                <v-timeline-item small :class="font(item.Status,4)" :color="color(item.Status,4)" v-if="!['T01','T02','T03'].includes(team)">
                    {{ item.NotedBy3 }}<br v-if="item.NotedDate3">{{ item.NotedDate3 }}<br>DEPUTY CONCURRING
                </v-timeline-item>
                <v-timeline-item small :class="font(item.Status,5)" :color="color(item.Status,5)">
                    {{ item.ApprovedBy }}<br v-if="item.ApprovedDate">{{ item.ApprovedDate }}<br>{{ statusLabel(5) }}
                </v-timeline-item>
            </v-timeline>
            
        </v-card>
        
    </div>
</template>

<script>
    import { mapState } from 'vuex' 
    import axios from 'axios'
    export default {
        props:[
            'month',
            'team',
            'department',
            'table'
        ],
        data() {
            return {
                timeLine:[],
                runFunction:false
            }
        },
        methods:{
          async  getTimeline(){
            // console.log('test')
            // this.$store.state.signatories = []
              await  axios({
                    method:'post',
                    url:`${this.$baseUrl}api/getTimeline`,
                    data:{
                        team : this.team,
                        month : this.month,
                        table : this.table
                    }
                }).then(res=>{
                    
                    this.timeLine = res.data
                    this.$emit('fromTimeline',this.timeLine);

                })
            },
            color(status, no) {
                return status >= no ? 'blue' : 'gray';
            },
            font(status, no) {
                return status >= no ? 'approved' : 'notApproved';
            },
            statusLabel(val){
                if(this.department != 'D01'){
                   return val == 3?'CHIEF OF STAFF CONCURRING':'COMMANDER APPROVAL'
                }else{
                    if(!['T01','T02','T03'].includes(this.team)){
                        return val == 3?'EX-O CONCURRING':this.user.officeName
                    }else{
                        return 'OIC APPROVAL'
                    }
                    
                }
            },
            signBy(val){
                let data
                this.signatories.filter(res=>{
                    if(val == res.Approver){
                        data = res.Name
                    }
                })
                return data
            }

        },
        computed:{
            ...mapState([
                'user',
                'signatories'
            ])
        },
        beforeMount(){
            this.getTimeline()
            this.runFunction = true
        },
      async  mounted(){
            if(!this.runFunction){
                await this.getTimeline()
                this.runFunction = true
            }
        }
    }
</script>

<style scoped>
.approved {
    /* Set your approved style properties */
    font-size: 11px;
    font-weight:bold;
  }
  
  .notApproved {
    color: gray; /* Set your notApproved style properties */
    font-size: 11px;
  }
</style>