const axios = require("axios")
const moment = require('moment');
const Approved = (data) =>{
    axios({
        method:'post',
        url:`${data.url}api/approvedReport`,
        data:{
            Month : data.month,
            Team : data.team,
            Time : moment().format('YYYY-MM-DD HH:mm:ss'),
            Name : data.name,
            Table : data.table,
            Status : data.status
        }

    }).then(res=>{
        console.log(res.data)
    })
}

module.exports = Approved