<script>
    import { mapState } from 'vuex';
    import axios from 'axios';
    // import PrintDialog from '../Details/PrintDialog'
    import InventoryPrint from './InventoryPrint.vue'
    // import InventoryExcel from './ExtractionExcel.vue'
    
    export default {
        name:'InventoryFile',
        components:{
            InventoryPrint,
            // PrintDialog
        },
        data() {
            return {
                generateMonthDialog: false,
                generateDate: null,
                department:null,
                totalMP:null,
                equipment:[],
                search:null,
                overlay:false,
                tab:'',
                editing:false,
                editIndex:null
            }
        },
        methods:{
            mobilityTab(tab){
                this.tab = tab
                this.$store.dispatch('diplayRemarks','mobility')
            },
            weaponTab(tab){
                this.tab = tab
                this.$store.dispatch('diplayRemarks','weapon')
            },
            commTab(tab){
                this.tab = tab
                this.$store.dispatch('diplayRemarks','communication')
            },
            medicalTab(tab){
                this.tab = tab
                this.$store.dispatch('diplayRemarks','medical')
            },
            dentalTab(tab){
                this.tab = tab
                this.$store.dispatch('diplayRemarks','dental')
            },
            quarterTab(tab){
                this.tab = tab
                this.$store.dispatch('diplayRemarks','quartermaster')
            },
            allCategory(tab){
                this.tab = tab
            },
            manpower(){
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/getManpower`,
                    data:{
                        department : this.department,
                        Month : this.generateDate
                    }
                }).then(res=>{
                    this.totalMP = res.data
                })
            },
            getEquipment(){
                this.overlay = true
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/getEquipmentInventory`,
                    data:{
                        department : this.department,
                        searchKey : this.search,
                        Month : this.generateDate
                    }
                }).then(res=>{
                    this.equipment = res.data
                    this.overlay = false
                    // console.log(this.equipment)
                })
            },
            generate(){
                this.manpower()
                this.getEquipment()
                this.$store.state.monthData = this.generateDate
                this.$store.state.teamData = this.department
              
                this.$store.dispatch('diplayRemarks',this.tab)
                
                
            },
            print(){
                const confirmed = window.confirm('Are you sure you want to print this report?');
                if(confirmed){
                    this.$refs.InventoryPrint.print(this.dataDownload)
                }
                // this.$refs.PrintDialog.openDialog()
            },
            printPdf(){
                this.$refs.InventoryPrint.print(this.dataDownload)
            },
            // updating assessment remarks function
            updateRemarks(item){
                
                this.editing = false
                this.editIndex = null  
                const value = event.target.innerText
                let obj = {
                    id : item.id,
                    remarks : value
                }
                this.$store.dispatch('updateRemarks',obj)
            },
            
        },
        computed:{
            ...mapState([
                'departmentItems',
                'user',
                'loadingPage',
                'remarksData'
            ]),
            // function to show month name
            formattedDate() {
                if (this.generateDate) {
                    const date = new Date(this.generateDate);
                    const month = date.toLocaleString('default', { month: 'long' });
                    const year = date.getFullYear();
                    return `${month} ${year}`;
                }
                return null;
            },
            addNewDepartment() {
                let obj = { UnitDepartmentName: 'All', UnitDepartmentCode: 'All' };
                let data = this.departmentItems.concat(obj);
                return data;
            },
            //unit name
            unitName(){
                let data
                this.addNewDepartment.filter(r=>{
                    if(r.UnitDepartmentCode == this.department){
                        data = r.UnitDepartmentName
                    }
                })
                return data
            },
            mobility(){
                const data = this.equipment
               return data.reduce((total,item)=>{
                    let i = total.findIndex(res=> res.Detail == item.Detail )
                    let obj = {
                            Group : null,
                            Detail : null,
                            Required : 0,
                            Actual:0,
                        }
                    if(i == -1){
                        if(item.Required && item.Category == 'MOBILITY'){
                            obj.Group = item.Sub
                            obj.Detail = item.Detail
                            obj.Required = Number(item.Required)
                            obj.Actual = Number(item.Actual)
                            total.push(obj)
                        }
                    }
                    else{
                        if(item.Required){
                            total[i].Detail = item.Detail
                            total[i].Required += Number(item.Required)
                            total[i].Actual += Number(item.Actual)
                        }
                    }
                    return total
                },[]).sort((a, b) => {
                    let groupOrder = ['Ground', 'Air', 'Sea'];
