<template>
    <div>
        <h1><center>Add/Update TO</center></h1>
        <v-divider/>
        <br>
        <v-row>
            <v-col cols="12" md="3" lg="3" xl="3">
                <v-card elevation="3" class="justify-center" >
                    <v-card-title style="background-color: #0275d8;color:white;"><center>CONTROL PANEL</center></v-card-title>
                    <v-container>
                        <br>
                        <v-row class="d-flex justify-center" >
                            <v-radio-group v-model="radioTO" row v-if="radioGroup != 'delete' && radioGroup != 'search'">
                              <!-- <v-radio label="Exist TO" value="exist"></v-radio>
                              <v-radio label="New TO" value="new"></v-radio> -->
                            </v-radio-group>
                        </v-row>
                        <v-form ref="add" @submit.prevent="add">
                            <v-radio-group v-model="categoryRadio" row v-if="radioTO == 'new'">
                                <h3>Category : </h3><v-spacer />
                                <v-radio label="Existing" value="exist"></v-radio>
                                <v-radio label="New" value="new"></v-radio>
                            </v-radio-group>
                            
                            <v-autocomplete 
                                label="Category"
                                item-text="CategoryName"
                                item-value="CategoryCode"
                                dense
                                outlined
                                :items="categoryItems"
                                v-model="category"
                                @change="getDepartment()"
                                :rules="rules.required"
                                v-if="categoryRadio != 'new'"
                            />
                            <v-text-field 
                                label="New Category"
                                dense
                                outlined
                                v-model="categoryName"
                                :rules="rules.required"
                                v-else
                            />

                            <v-radio-group v-model="departmentRadio" row v-if="radioTO == 'new'" class="margin-top">
                                <h3>Department : </h3><v-spacer />
                                <v-radio label="Existing" value="exist"></v-radio>
                                <v-radio label="New" value="new"   @click="getDepartment()"></v-radio>
                            </v-radio-group>
                            <v-autocomplete 
                                label="Unit"
                                item-text="UnitDepartmentName"
                                item-value="UnitDepartmentCode"
                                dense
                                outlined
                                :items="departmentItems"
                                v-model="department"
                                @change="getSection()"
                                :rules="rules.required"
                                v-if="departmentRadio != 'new'"
                                :clearable="radioGroup == 'delete'?true:false"
                            />
                            <v-text-field 
                                label="New Department"
                                dense
                                outlined
                                v-model="newDepartmentName"
                                :rules="rules.required"
                                v-else
                            />

                            <v-radio-group v-model="sectionRadio" row v-if="radioTO == 'new'" class="margin-top">
                                <h3>Section : </h3><v-spacer />
                                <v-radio label="Existing" value="exist"></v-radio>
                                <v-radio label="New" value="new" @click="getSection()"></v-radio>
                            </v-radio-group>
                            <v-autocomplete 
                                label="Sub-Unit"
                                item-text="UnitSectionName"
                                item-value="UnitSectionCode"
                                dense
                                outlined
                                :items="sectionItems"
                                v-model="section"
                                @change="getTeam()"
                                :rules="rules.required"
                                v-if="sectionRadio != 'new'"
                                :clearable="radioGroup == 'delete'?true:false"
                            />
                            <v-text-field 
                                label="New Section"
                                dense
                                outlined
                                v-model="newSectionName"
                                :rules="rules.required"
                                @blur="getTeam()"
                                v-else
                            />


                            <v-autocomplete 
                                label="Office"
                                item-text="UnitTeamName"
                                item-value="UnitTeamCode"
                                dense
                                outlined
                                :items="teamItems"
                                v-model="team"
                                :rules="rules.required"
                                @change="getEmpty()"
                                v-if="radioTO != 'new' && category == 'CA01'"
                                :clearable="radioGroup == 'delete'?true:false"
                            />
                            <v-text-field 
                                label="New Team"
                                dense
                                outlined
                                v-model="newTeamName"
                                :rules="rules.required"
                                v-else
                                v-show="category == 'CA01'"
                            />
                            

                            <v-row class="d-flex justify-center" v-if="radioTO != 'new'">
                                <v-radio-group v-model="radioGroup" row>
                                  <!-- <v-radio label="Upload" value="upload"></v-radio> -->
                                  <!-- <v-radio label="Add" value="add"></v-radio> -->
                                  <!-- <v-radio label="Search" value="search"></v-radio> -->
                                  <!-- <v-radio label="Delete" value="delete"></v-radio> -->
                                </v-radio-group>
                            </v-row>
                            
                            <v-text-field
                                v-show="radioGroup == 'add'"
                                label="Branch"
                                dense
                                outlined
                                v-model="branch"
                                :rules="rules.required"
                                @blur="getBranch()"
                            />
                            <v-text-field
                                v-show="radioGroup == 'add'"
                                label="Position"
                                dense
                                outlined
                                v-model="position"
                                :rules="rules.required"
                                @blur="getPosition()"
                            />
                            <v-autocomplete 
                                v-show="radioGroup == 'add'"
                                label="Grade"
                                item-text="GradeName"
                                item-value="GradeCode"
                                dense
                                outlined
                                :items="gradeItems"
                                v-model="grade"
                                @change="getDivision()"
                                :rules="rules.required"
                            />
                            <v-text-field
                                v-show="radioGroup == 'add'"
                                label="AFPOS"
                                dense
                                outlined
                                v-model="afpos"
                                :rules="rules.required"
                                @blur="getAfpos()"
                            />
                            <v-text-field
                                v-show="radioGroup == 'add'"
                                label="Required MP"
                                dense
                                outlined
                                v-model="requiredmp"
                                :rules="rules.required"
                            />
                            <!-- <v-row align="center" justify="center">
                                <v-col cols="12" sm="6">
                                  <v-file-input
                                    accept=".csv"
                                    label="Upload CSV"
                                    outlined
                                    dense
                                    :value="selectedFile"
                                    ref="fileInput"
                                    @input="handleFileSelect"
                                    v-show="radioGroup === 'upload' && team"
                                  ></v-file-input>
                                </v-col>
                              </v-row> -->
                            <div v-show="this.tableData.length == 0 && department == 'D01' && team || this.tableData.length == 0 && department != 'D01' && section"> 
                                <input type="file" ref="fileInput" @change="handleFileSelect" accept=".csv"  v-show="radioGroup == 'upload'  && radioTO != 'new'" class="file-input"/>
                                <v-btn color="success" dark block medium @click="upload()" v-show="radioGroup == 'upload'  && radioTO != 'new'">UPLOAD</v-btn>
                            </div>
                            <div v-show="tableData.length > 0">
                                <v-btn color="error" dark block medium @click="deleteTable('personnel')" >DELETE</v-btn>
                            </div>

                            <v-btn color="primary" dark block medium type="submit" v-if="radioGroup == 'add'">ADD</v-btn>
                            
                            <v-btn color="success" dark block medium  v-if="radioTO == 'new'" @click="addUnitManual()">ADD</v-btn>
                            <!-- <v-btn color="warning" dark block medium @click="getTable()" v-show="radioGroup == 'search'">SEARCH</v-btn> -->
                            <v-btn color="error" dark block medium @click="deleteUnit()" v-show="radioGroup == 'delete'">DELETE {{ deleteNameDisplay }}</v-btn>
                            <!-- <v-btn color="primary" dark block medium @click="exportcsv()">CSV</v-btn> -->
                        </v-form>
                    </v-container>
                </v-card>
            </v-col>
       
            <v-col cols="12" md="9" lg="9" xl="9" >
                <v-card elevation="5">
                    <div class="d-flex flex-column justify-content-center text-center">
                        <h2>{{ departmentName }}</h2>
                        <h2>{{ sectionName }}</h2>
                        <h3>{{ teamName }}</h3>
                    </div>
                    <v-simple-table
                        fixed-header
                        class="elevation-1 custom-table"
                        height="80vh"
                    >
                        <thead>
                            <tr>
                                <th>DESCRIPTION/POSITION</th>
                                <th>GRADE</th>
                                <th>AFPOS</th>
                                <th>REQUIRED</th>
                                <th>DELETE</th>
                            </tr>
                        </thead>
                        <tbody v-for="(item, i) in tableData" :key="i">
                            <tr v-if="item.OfficeName">
                                <td style="font-weight:bold;font-size:15px;background-color:lightgrey !important;" colspan="5">{{ item.OfficeName }}</td>
                            </tr>
                            <tr v-if="item.GroupBranch && !item.OfficeName">
                                <td style="font-weight:bold;font-size:15px;">{{ item.GroupBranch }}</td>
                            </tr>
                           
                            <tr  v-if="item.GradeName">
                                <td style="text-align:left !important">{{ item.PositionName }}</td>
                                <td>{{ item.GradeName }}</td>
                                <td>{{ item.AfposName }}</td>
                                <td>{{ item.requiredMp }}</td>
                                <td><v-btn icon @click="deleteItem(item)"><v-icon color="error">mdi-trash-can-outline</v-icon></v-btn></td>
                            </tr>
                            <template v-if="!item.GroupBranch && (!tableData[i + 1] || tableData[i + 1].GroupBranch)">
                                <tr>
                                  <td style="font-weight:bold;font-size:15px;" colspan="2"></td>
                                  <td style="font-weight:bold;font-size:15px;">Total</td>
                                  <td style="font-weight:bold;font-size:15px;">{{ item.total }}</td>
                                </tr>
                            </template>
                            <template v-if="i === tableData.length - 1">
                                <tr>
                                  <td style="font-weight:bold;font-size:15px;" colspan="2"></td>
                                  <td style="font-weight:bold;font-size:15px;">Aggregate Total</td>
                                  <td style="font-weight:bold;font-size:15px;">{{ Manpower }}</td>
                                </tr>
                              </template>

                        </tbody>
                    </v-simple-table>
                    <v-overlay :value="overlay">
                        <v-progress-circular
                            indeterminate
                            size="64"
                        ></v-progress-circular>
                    </v-overlay>
                </v-card>
                
            </v-col>
        </v-row>
       
    </div>
</template>

<script>

import {mapState} from 'vuex'
import Papa from 'papaparse';
import axios from 'axios';
import moment from 'moment';
import DeleteRequired from '../Details/DeleteTable'
export default {
    components:{
    

    },
    data:()=>({
        radioGroup:'upload',
        category:null,
        categoryName:null,
        categoryItems:[],
        division:null,
        department:null,
        newDepartmentName:null,
        departmentItems:[],
        section:null,
        newSectionName:null,
        sectionItems:[],
        team:null,
        newTeamName:null,
        teamItems:[],
        branch:null,
        branchCode:null,
        position:null,
        positionCode:null,
        grade:null,
        gradeItems:[],
        afpos:null,
        afposCode:null,
        requiredmp:null,
        selectedFile: null,
        tableData:[],
        tempFile:[],
        tempInsert:[],
        tempBranch:[],
        tempPosition:[],
        tempAfpos:[],
        forUpload:[],
        categoryRadio:'exist',
        departmentRadio:'exist',
        sectionRadio:'exist',
        radioTO : 'exist',

        rules:{
            required: [
                v => !!v || 'Field is required'
            ],
        },
        overlay:false,
        dataToExport:[]
        

    }),
    methods:{
        exportcsv(){
            axios({
                method: 'post',
                url: `${this.$baseUrl}api/forExport`,
            }).then(res=>{
                const csvContent = this.convertToCSV(res.data);
                const blob = new Blob([csvContent], { type: 'text/csv' });
                const url = window.URL.createObjectURL(blob);
                const a = document.createElement('a');
                a.href = url;
                a.download = 'exported-data.csv';
                a.style.display = 'none';
                document.body.appendChild(a);
                a.click();
                window.URL.revokeObjectURL(url);
            })
        },
        convertToCSV(data) {
            const header = Object.keys(data[0]).join(',');
            const rows = data.map(obj => Object.values(obj).join(','));
            return `${header}\n${rows.join('\n')}`;
        },


        decryptData(encryptedData) {
            const decryptedData = atob(encryptedData);
            return decryptedData;
        },
        async getGradeInsert(val){
            if(val){
                let data
                this.gradeItems.filter(r=>{
                    if(r.GradeName == val){
                        data = r.GradeCode
                    }
                })
                return data
            }
           
       },

    //    function for uploading csv
       async getDivisionInsert(val) {
            if(val){
                // console.log(val,'val')
                // console.log(this.gradeItems,'grade')
                return new Promise((resolve) => {
                    let data
                    this.gradeItems.filter(r=>{
                        if(r.GradeCode == val){
                            data = r.DivisionCode
                        }
                        // console.log(data)
                    })
                    resolve(data)
                });
            }
           
        },

        // async getDivisionInsert(val) {
        //     if(val){
        //         // console.log(val,'val')
        //         // console.log(this.gradeItems,'grade')
        //         return new Promise((resolve, reject) => {
        //             let data
        //             this.gradeItems.filter(r=>{
        //                 if(r.GradeCode == val){
        //                     data = r.DivisionCode
        //                 }
        //                 // console.log(data)
        //             })
        //             resolve(data)
        //         });
        //     }
           
        // },
        getBranchInsert(){
                axios({
                method: 'post',
                url: `${this.$baseUrl}api/getBranch`,
                data:{
                    data : this.category == 'CA01'?this.team:this.section,
                }
         
                }).then(r => {
                    this.tempBranch = r.data
                });
        },
        async branchFilter(val) {
            if(val){
                // console.log(val,'val')
                // console.log(this.gradeItems,'grade')
                return new Promise((resolve) => {
                    let data
                    this.tempBranch.filter(r=>{
                        if(r.BranchName == val){
                            data = r.BranchCode
                        }
                    })
                    resolve(data)
                })
            }
        },
        getPositionInsert(){
                axios({
                method: 'post',
                url: `${this.$baseUrl}api/getPosition`,
                data:{
                        data : this.category == 'CA01'?this.team:this.section,
                    }
                }).then(r => {
                    this.tempPosition = r.data
                });
        },
        async positionFilter(val) {
            if(val){
                // console.log(val,'val')
                // console.log(this.gradeItems,'grade')
                return new Promise((resolve) => {
                    let data
                    this.tempPosition.filter(r=>{
                        if(r.PositionName == val){
                            data = r.PositionCode
                            // console.log(data,'please')
                           
                        }
                    })
                    resolve(data)
                })
            }
        },
        getAfposInsert(){
                axios({
                method: 'post',
                url: `${this.$baseUrl}api/getAfpos`,
            
                }).then(r => {
                    this.tempAfpos = r.data
                })
        },
        async afposFilter(val) {
            if(val){
                return new Promise((resolve) => {
                    let data
                    this.tempAfpos.filter(r=>{
                        if(r.AfposName == val){
                            data = r.AfposCode
                        }
                    })
                    resolve(data)
                });
            }
        },
        
        handleFileSelect(event) {
            // console.log(this.$refs.fileInput)
            const file = event.target.files[0];
            // this.selectedFile = event.target.files[0];
            // this.selectedFileValue = event.target.files[0];
         
            const reader = new FileReader();

            reader.onload = (e) => {
                const contents = e.target.result;

                const parsedData = Papa.parse(contents, {
                header: true,
                encoding: 'UTF-8',
                });

                const rows = parsedData.data;

                // Process each row
                const headers = Object.keys(rows[0]);

                for (let i = 0; i < rows.length; i++) {
                const row = rows[i];
                const rowData = {};

                for (let j = 0; j < headers.length; j++) {
                    let value = row[headers[j]];

                    // Check if value exists before applying replace
                    // if (value) {
                    //     value = value.replace(/\r/g, '');
                    // }

                    rowData[headers[j]] = value;
                }

                this.tempFile.push(rowData);
                }
                // console.log(this.tempFile,'data')
            };
      
            reader.readAsText(file);
        },
       async upload(){
        this.overlay = true
        let previosBranchCode = ''
        let previosBranchName = ''
        // let newBranchCode
     
           
            for (let i = 0; i < this.tempFile.length - 1; i++) {
                
                const item = this.tempFile[i];
                if(item.description != 'Total'){
                    if(previosBranchCode){
                        const lastRow = previosBranchCode.match(/\d+$/);
                        if (lastRow) {
                            // const getId = parseInt(lastRow[0], 10) + 1;
                            // newBranchCode = previosBranchCode.replace(/\d+$/, getId.toString().padStart(lastRow[0].length, '0'));
                        }
                    }
                    
                    const position = await this.positionFilter(item.description)
                    const afpos = await this.afposFilter(item.afpos)
                    let obj = {
                        DivisionCode : null,
                        CategoryCode : this.category,
                        DepartmentCode : this.department,
                        SectionCode : this.section,
                        TeamCode : this.category == 'CA01'?this.team:this.section,
                        BranchCode : null,
                        BranchName:null,
                        PositionCode : null,
                        PositionName : null,
                        GradeCode : null,
                        AfposCode : null,
                        AfposName : null,
                        RequiredMp : null,
                        Office : null,
                        process : 'upload'

                    }
                    obj.UnitCode = this.category == 'CA01'?this.team:this.section

                    if(item.description.trim() !== '' &&  item.grade.trim() == '' && item.afpos.trim() == '' && item.required.trim() == ''){
                        // console.log(item.description,'test')
                        // const branch = await this.branchFilter(item.description)
                        
                        // if(branch){
                        //     // console.log(position,'position1')
                        //     obj.BranchCode = branch
                            
                        // }else{
                            if(this.tempBranch.length > 0){
                                let lastId = this.tempBranch[this.tempBranch.length - 1]
                                let lastRow = lastId.BranchCode.match(/\d+/);
                                let getId = parseInt(lastRow[0], 10) + 1;
                                let newCode = lastId.BranchCode.replace(/\d+/, getId.toString().padStart(lastRow[0].length, '0'))
                                obj.BranchCode = newCode
                                let pushPosition =  {BranchCode:obj.BranchCode,BranchName:item.description}
                                this.tempBranch.push(pushPosition)
                            }else{
                                obj.BranchCode = `BS001${obj.UnitCode}`
                                let pushPosition =  {BranchCode:obj.BranchCode,BranchName:item.description}
                                this.tempBranch.push(pushPosition)
                            }    
                        // }
                        obj.PositionCode = item.office?'00000':null
                        obj.PositionName = item.office?'office_unit':null
                        obj.DivisionCode = item.office?'001':null
                        obj.BranchName = item.description
                        previosBranchCode = obj.BranchCode
                        previosBranchName = obj.BranchName 
                        
                    }else{

                        obj.BranchCode =  previosBranchCode
                        obj.BranchName = previosBranchName
                        if(position){
                            // console.log(position,'position1')
                            obj.PositionCode = position
                        }else{
                            
                            if(this.tempPosition.length > 0){
                                let lastId = this.tempPosition[this.tempPosition.length - 1]
                                let lastRow = lastId.PositionCode.match(/\d+/);
                                let getId = parseInt(lastRow[0], 10) + 1;
                                let newCode = lastId.PositionCode.replace(/\d+/, getId.toString().padStart(lastRow[0].length, '0'))
                                obj.PositionCode = newCode
                                let pushPosition =  {PositionCode:obj.PositionCode,PositionName:item.description}
                                this.tempPosition.push(pushPosition)
                            }else{
                                obj.PositionCode = `PS001${obj.UnitCode}`
                                let pushPosition =  {PositionCode:obj.PositionCode,PositionName:item.description}
                                this.tempPosition.push(pushPosition)
                            }    
                        }

                       
                        if(afpos){
                            obj.AfposCode = afpos
                        }else{
                            if(this.tempAfpos.length > 0){
                                let lastId = this.tempAfpos[this.tempAfpos.length - 1]
                                let lastRow = lastId.AfposCode.match(/\d+/);
                                let getId = parseInt(lastRow[0], 10) + 1;
                                let newCode = lastId.AfposCode.replace(/\d+/, getId.toString().padStart(lastRow[0].length, '0'))
                                obj.AfposCode = newCode
                                let pushAfpos =  {AfposCode:obj.AfposCode,AfposName:item.afpos}
                                this.tempAfpos.push(pushAfpos)
                            }else{
                                obj.AfposCode = 'AF001'
                                let pushAfpos =  {AfposCode:obj.AfposCode,AfposName:item.afpos}
                                this.tempAfpos.push(pushAfpos)
                            }
                            
                        }
                        obj.PositionName = item.description
                        obj.AfposName = item.afpos
                        obj.GradeCode = await this.getGradeInsert(item.grade)
                        obj.RequiredMp = item.required
                        obj.DivisionCode = await this.getDivisionInsert(obj.GradeCode)
                    }
                    if(obj.DivisionCode){
                        this.forUpload.push(obj)
                    }
                        
                }
            }
            this.$refs.fileInput.value = '';

        //    console.log(this.forUpload)
            
            // return
            this.addUpload(this.forUpload);
         
           

        },
        addUpload(val){
          
            axios({
                method:'post',
                url:`${this.$baseUrl}api/getInsertUpload`,
                data:{
                    data : val
        
                }
            }).then(res=>{
                if(res.status == 200){
                    alert('uploaded')
                    this.forUpload = []
                    this.tempFile = []
                    this.tempBranch = []
                    this.tempPosition = []
                    this.tempAfpos = []
                    this.getTable()
                    this.overlay = false
                }
            })
       },
//    end of function for uploading csv

// function for adding single details
        newCodeTO(array,codeName){
            // console.log(array,codeName,'test33')
            let newCode
                let lastId = array[array.length - 1]
                // console.log(lastId)
                // //for new branchCode
                let lastRow = lastId[codeName].match(/\d+/);
                let getId = parseInt(lastRow[0], 10) + 1;
                newCode = lastId[codeName].replace(/\d+/, getId.toString().padStart(lastRow[0].length, '0'))
            return newCode
        },
        getCategory(){
            this.tableData = []
            this.department = null
            this.section = null
            this.team = null
            axios({
                method:'get',
                url:`${this.$baseUrl}api/getCategory`,
            }).then(r=>{
                this.categoryItems = r.data
            })
        },
        getDepartment(){
        this.tableData = []
        this.section = null
        this.team = null
        axios({
            method:'post',
            url:`${this.$baseUrl}api/getDepartment`,
            data:{
                data : this.category,
                list : this.departmentRadio
            }
        }).then(r=>{
            // console.log(r.data)
            this.departmentItems = r.data
        })
        },
        getSection(){
        this.tableData = []
        this.team = null
        axios({
            method:'post',
            url:`${this.$baseUrl}api/getSection`,
            data:{
                data : this.department,
                list : this.sectionRadio
            }
        }).then(r=>{
            this.sectionItems = r.data
        })
        },
        getTeam(){
        this.tableData = []
        axios({
            method:'post',
            url:`${this.$baseUrl}api/getTeam`,
            data:{
                data : this.section,
                list : this.radioTO
            }
        }).then(r=>{
            this.teamItems = r.data
            if(this.category != 'CA01'){
                this.getTable()
            }
        })
        },
        getGrade(){
        axios({
            method:'post',
            url:`${this.$baseUrl}api/getGrade`,
        }).then((response) => {
            const encryptedData = response.data;
            const decryptedData = this.decryptData(encryptedData);
            this.gradeItems = JSON.parse(decryptedData);
        })
        .catch((error) => {
            console.error('Error fetching and decrypting grade data:', error);
        });
        },
        getDivision(){
        axios({
            method:'post',
            url:`${this.$baseUrl}api/getDivision`,
            data:{
                data : this.grade
            }
        }).then(r=>{
            r.data.filter(  res=>{
                this.division = res.DivisionCode
            })
          
        })
        },
        getEmpty(){
            this.tableData = []
            this.getTable()
        },
        addCode(array,codeName){
        // console.log(array,codeName,'test33')
        let newCode
            let lastId = array.pop()
            // console.log(lastId)
            // //for new branchCode
            let lastRow = lastId[codeName].match(/\d+/);
            let getId = parseInt(lastRow[0], 10) + 1;
            newCode = lastId[codeName].replace(/\d+/, getId.toString().padStart(lastRow[0].length, '0'))
         return newCode
        },
      
       getNewCode(address,data,resName,resCode){
            return new Promise((resolve, reject) => {
                let newCode;
                axios({
                method: 'post',
                url: `${this.$baseUrl}api/${address}`,
                data:{
                    data : data
                }
                }).then(r => {
                    // console.log(r.data)
                if (r.data.length > 0) {
                    const index = r.data.find(res=> res[resName] == data)
                    // console.log(index)
                    if(index){
                        newCode = index[resCode];
                    }else{
                        newCode = this.addCode(r.data, resCode);
                    }
                } else {
                    if (address == 'getBranch') {
                        newCode = 'BR001';
                    } else if (address == 'getPosition') {
                        newCode = 'PS001';
                    } else {
                        newCode = 'AF001';
                    }
                }

                    resolve(newCode); // Resolving the Promise with the newCode value
                }).catch(error => {
                    reject(error); // Rejecting the Promise if an error occurs during the API call
                });
            });
        
       },

      async getBranch(){
          this.branchCode =  await this.getNewCode('getBranch',this.branch,'BranchName','BranchCode')
        
       },
       async getPosition(){
          this.positionCode =  await this.getNewCode('getPosition',this.position,'PositionName','PositionCode')
        
       },
       async getAfpos(){
          this.afposCode =  await this.getNewCode('getAfpos',this.afpos,'AfposName','AfposCode')
       },
       
 
       add(){
        if(this.$refs.add.validate()){
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/getInsert`,
                    data:{
                        DivisionCode : this.division,
                        CategoryCode : this.category,
                        DepartmentCode : this.department,
                        SectionCode : this.section,
                        TeamCode : this.team,
                        BranchCode : this.branchCode,
                        BranchName : this.branch,
                        PositionCode : this.positionCode,
                        PositionName : this.position,
                        GradeCode : this.grade,
                        AfposCode : this.afposCode,
                        AfposName : this.afpos,
                        RequiredMp : this.requiredmp,
                        process : 'single' 
                    }
                }).then(res=>{
                    if(res.status == 200){
                        alert('added')
                        this.position = null
                        this.grade = null
                        this.afpos = null
                        this.requiredmp = null
                        this.getTable()
                    }
                })
            }
       },
       addUnitManual(){
        // console.log('test')
            // if(this.$refs.add.validate()){
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/getInsertManual`,
                    data:{
                        CategoryCode : this.categoryRadio == 'exist'?this.category:this.newCodeTO(this.categoryItems,'CategoryCode'),
                        CategoryName : this.categoryName?this.categoryName.toUpperCase():null,
                        DepartmentCode : this.departmentRadio == 'exist'?this.department:this.newCodeTO(this.departmentItems,'UnitDepartmentCode'),
                        DepartmentName : this.newDepartmentName?this.newDepartmentName.toUpperCase():null,
                        SectionCode :  this.sectionRadio == 'exist'?this.section:this.newCodeTO(this.sectionItems,'UnitSectionCode'),
                        SectionName : this.newSectionName?this.newSectionName.toUpperCase():null,
                        SectionAbr : this.newSectionName?this.getAbbreviation(this.newSectionName):null,
                        TeamCode :  this.newCodeTO(this.teamItems,'UnitTeamCode'),
                        TeamName : this.newTeamName.toUpperCase(),
                        TeamAbr : this.newTeamName?this.getAbbreviation(this.newTeamName):null,
                    }
                }).then(res=>{
                    if(res.status == 200){
                        this.radioTO = 'exist'
                        this.categoryRadio = 'exist'
                        this.departmentRadio = 'exist'
                        this.sectionRadio = 'exist'
                        this.getCategory()
                        this.getDepartment()
                        this.getSection()
                        this.getTeam()
                        alert('added')
                    }
                })
            // }
       },

       getTable(){
        this.overlay = true
        axios({
                method:'post',
                url:`${this.$baseUrl}api/getTable`,
                data:{
                    DivisionCode : this.division,
                    CategoryCode : this.category,
                    DepartmentCode : this.department,
                    SectionCode : this.section,
                    TeamCode : this.category == 'CA01'?this.team:this.section,
                    BranchCode : this.branchCode,
                    BranchName : this.branch,
                    PositionCode : this.positionCode,
                    PositionName : this.position,
                    GradeCode : this.grade,
                    AfposCode : this.afposCode,
                    AfposName : this.afpos,
                    RequiredMp : this.requiredmp
                }
            }).then((r) => {
            const encryptedData = r.data;
            const decryptedData = this.decryptData(encryptedData);
            let tempData = JSON.parse(decryptedData);
            // console.log(tempData,'tempData')
          
                this.tableData = tempData.reduce((total,item)=>{
                    let index = total.findIndex(res=> res.BranchCode == item.BranchCode)
                    if(index == -1){
                        let obj = {...item}
                        obj.OfficeName = item.PositionName == 'office_unit'?item.BranchName:null
                        obj.GroupBranch = item.BranchName
                        obj.TotalMP = isNaN(item.requiredMp) ? 0 : Number(item.requiredMp);
                        total.push(obj)
                    }else{
                        if (!isNaN(item.requiredMp)) {
                            total[index].TotalMP += Number(item.requiredMp);
                        }
                        total[index].count += 1
                        item.total = total[index].TotalMP
                        total.push(item)
                    }
                    return total
                },[])
                // console.log(this.tableData)
                this.getAfposInsert()
                this.getPositionInsert()
                this.getBranchInsert()
            this.overlay = false
        })
        .catch((error) => {
            console.error('Error fetching and decrypting grade data:', error);
        });
                
       
       },
       deleteItem(item){
            axios({
                method:'post',
                url:`${this.$baseUrl}api/deleteItem`,
                data:{
                    id : item.id
                }
            }).then(res=>{
                if(res.status == 200){
                    alert('Delete')
                    this.getTable()
                }
              
            })
       },
       deleteUnit(){
        if (window.confirm(`Are you sure you want to delete this ${this.deleteNameDisplay.toLowerCase()}?`)) {
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/deleteUnit`,
                    data:{
                        data : this.deleteNameDisplay == 'DEPARTMENT'?this.department:this.deleteNameDisplay == 'SECTION'?
                                this.section:this.deleteNameDisplay == 'TEAM'?this.team:null,
                        table : this.deleteNameDisplay,
                        department : this.department,
                        section : this.section
                      
                    }
                }).then(res=>{
                    if(res.status == 200){
                        alert(`This ${this.deleteNameDisplay.toLowerCase()} was deleted`)
                        this.radioTO = 'exist'
                        this.categoryRadio = 'exist'
                        this.departmentRadio = 'exist'
                        this.sectionRadio = 'exist'
                        this.getCategory()
                        this.getDepartment()
                        this.getSection()
                        this.getTeam()
                    }
                    
                })
            }
       },
       getAbbreviation(phrase) {
            const words = phrase.split(' ');
            let abbreviation = '';

            for (let i = 0; i < words.length; i++) {
                abbreviation += words[i][0].toUpperCase();
            }

            return abbreviation;
        },
        deleteTable(table){
            const confirmed = window.confirm('Are you sure you want to delete this information?');
            if(confirmed){
                this.overlay = true
                let data = {
                    Table : table,
                    Team : this.department == 'D01'?this.team:this.section,
                    Name: this.user.name,
                    Time: moment().format('YYYY-MM-DD HH:mm:ss'),
                    Year : moment().format('YYYY'),
                    url : `${this.$baseUrl}api/deleteRequired`
                }
                return DeleteRequired(data,this.getTable)
            }
        }
      


    },
    computed:{
        ...mapState([
            'user'
        ]),
        departmentName(){
            let data
            if(this.department){
                this.departmentItems.filter(r=>{
                    if(r.UnitDepartmentCode == this.department){
                        data = r.UnitDepartmentName
                    }
                })
            }
            return data
        },
        sectionName(){
            let data
            if(this.section){
                this.sectionItems.filter(r=>{
                    if(r.UnitSectionCode == this.section){
                        data = r.UnitSectionName
                    }
                })
            }
            return data
        },
        teamName(){
            let data
            if(this.team){
                this.teamItems.filter(r=>{
                    if(r.UnitTeamCode == this.team){
                        data = r.UnitTeamName
                    }
                })
            }
            return data
        },
        Manpower(){
            if(this.tableData.length > 0){
                return this.tableData.reduce((total, item) => {
                    if (item.TotalMP) {
                        return total + item.TotalMP;
                    }
                    return total;
                    }, 0);
            }else{
                return 0
            }
           
        },
        deleteNameDisplay(){
            if(this.department && !this.section && !this.team){
                return 'DEPARTMENT'
            }else if(this.department && this.section && !this.team){
                return 'SECTION'
            }else if(this.department && this.section && this.team){
                return 'TEAM'
            }else{
                return ''
            }
        }
        
        
    
        
    },
    mounted(){
        this.getCategory()
        this.getGrade()
        this.getAfposInsert()
        this.getPositionInsert()
        this.getBranchInsert()
       
    },
    watch:{
        radioTO(val){
            if(val == 'new'){
                this.category = null
                this.department = null
                this.section = null
                this.team = null
                this.newTeamName = null
            }
        }
    }
}
</script>
