<template>
    <div>

    </div>
</template>

<script>
    import ExcelJS from 'exceljs';
    export default {
        props:[
            'excelData'
        ],
        data() {
            return {
                
            }
        },
        methods: {
            exportExcel(){
                // console.log('test')
                // this.exportReadinnes();
                Promise.all([this.exportTable(),this.exportReadinnes()])
            },
            async exportTable() {
                let val = this.excelData
                // Create a new workbook
                const workbook = new ExcelJS.Workbook();
                // Add a worksheet
                const worksheet = workbook.addWorksheet('Sheet1');
                // Define the column headers
                const headers = ['Facility','Qualitative','','','Quantitative','Rating'];
                const headers2 = ['','Required','Actual','Rating','Rating',''];
                // Add the headers to the worksheet
                const headerRows = [
                    worksheet.addRow(headers),
                    worksheet.addRow(headers2)
                ];
                worksheet.mergeCells(1, 2, 1, 4);

                headerRows.forEach(row => {
                    row.eachCell((cell) => {
                        cell.alignment = { horizontal: 'center' };
                        cell.fill = {
                            type: 'pattern',
                            pattern: 'solid',
                            fgColor: { argb: 'FFFF00' } // Change 'FFFF00' to the desired color code
                        };
                        cell.font = { bold: true };

                        // Add border
                        cell.border = {
                            top: { style: 'thin' },
                            left: { style: 'thin' },
                            bottom: { style: 'thin' },
                            right: { style: 'thin' }
                        };
                    });
                });

             const selectedColumns = [
                'Detail',
                'Required',
                'Actual',
                'Rating',
                'overall',
                'facilityRating'
             ];
            //  let merged = false;
             // Add the data rows to the worksheet
             val.tableData.forEach(data => {
                const row = selectedColumns.map(column => {
                        if ((column === 'facilityRating' || column === 'Rating' ||  column === 'overall') && data[column]) {
                            return parseFloat(data[column]).toFixed(2)+'%'; // Apply toFixed(2) if column is "Actual"
                        } else {
                            return data[column] || '';
                        }
                    });
                     let excelRow = worksheet.addRow(row);
                    // console.log(row)
                        // Check if data.Title is equal to '1'
                        if (data.Title === '1') {
                            // Loop through each cell in the newly added row
                            excelRow.eachCell((cell) => {
                                cell.font = { bold: true };
                                cell.fill = {
                                    type: 'pattern',
                                    pattern: 'solid',
                                    fgColor: { argb: '808080' } // Change 'FFFF00' to the desired color code
                                };
                            });
                        }
                        selectedColumns.forEach(column => {
                            const columnIndex = selectedColumns.indexOf(column);

                            if (data[column] && columnIndex !== -1) {
                                const cell = excelRow.getCell(columnIndex + 1);

                                if (column !== 'Detail') {
                                    cell.alignment = { horizontal: 'center' };
                                }

                                if (column === 'facilityRating') {
                                    cell.font = {
                                        color: { argb: '0000FF' }, // Set font color to blue
                                        bold: true
                                    };
                                }
                            }
                        });

             });
            
            //  // Set the column widths
             worksheet.columns.forEach((column,i) => {
                if(i == 0){
                    column.width = 70;
                }
                 
             });
            worksheet.eachRow((row) => {
                row.eachCell((cell) => {
                    cell.border = {
                        top: { style: 'thin' },
                        left: { style: 'thin' },
                        bottom: { style: 'thin' },
                        right: { style: 'thin' },
                    };
               
                });
            });
                // Generate a buffer from the workbook
                const buffer = await workbook.xlsx.writeBuffer();
    
                // Create a Blob from the buffer
                const blob = new Blob([buffer], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
    
                // Create a download link
                const url = window.URL.createObjectURL(blob);
                const link = document.createElement('a');
                link.href = url;
                link.download = `${this.excelData.teamName}-${this.excelData.month}-Facility List.xlsx`;
    
                // Trigger the download
                link.click();
    
                // Clean up the URL object
                window.URL.revokeObjectURL(url);
            },



            async exportReadinnes() {
                let val = this.excelData
                // Create a new workbook
                // console.log(this.readinessData)
                const workbook = new ExcelJS.Workbook();
                // Add a worksheet
                const worksheet = workbook.addWorksheet('Sheet1');
                // Define the column headers
                const headers = ['Category','Required','Actual','Quantitative','Qualitative','Total','Percentage'];
                // Add the headers to the worksheet
                const headerRow = worksheet.addRow(headers);

             headerRow.eachCell((cell) => {
                cell.alignment = { horizontal: 'center' };
                cell.fill = {
                    type: 'pattern',
                    pattern: 'solid',
                    fgColor: { argb: 'FFFF00' } // Change 'FFFF00' to the desired color code
                };
                cell.font = { bold: true };
               
            });
                const selectedColumns = [
                    'Detail',
                    'Required',
                    'Actual',
                    'Quantitative',
                    'Qualitative',
                    'total',
                    'weight',
                ];
                // Add the data rows to the worksheet
                val.report.forEach(data => {
                        const validColumns = ['Quantitative', 'Qualitative', 'total', 'weight'];
                        const row = selectedColumns.map(column => {
                        if (validColumns.includes(column) && data[column]) {
                            return parseFloat(data[column]).toFixed(2)+'%'; // Apply toFixed(2) if column is "Actual"
                        } else {
                            return data[column] || '';
                        }
                    });
                      let excelRow =  worksheet.addRow(row);
                      excelRow.eachCell((cell) => {
                            cell.font = { bold: true };
                        });
                        
                        selectedColumns.forEach(column => {
                            const columnIndex = selectedColumns.indexOf(column);

                            if (data[column] && columnIndex !== -1) {
                                const cell = excelRow.getCell(columnIndex + 1);

                                if (column !== 'Detail') {
                                    cell.alignment = { horizontal: 'center' };
                                }

                                if (column === 'weight') {
                                    cell.font = {
                                        color: { argb: '0000FF' }, // Set font color to blue
                                        bold: true
                                    };
                                }
                            }
                        });
                      

                });

                const total = ['','','','','','TOTAL',val.readiness.toFixed(2)+'%'];
                const totalVal = worksheet.addRow(total);
                totalVal.eachCell((cell) => {
                    cell.alignment = { horizontal: 'center' };
                    cell.font = { bold: true };
                });

                 // Set the column widths
                worksheet.columns.forEach((column,i) => {
                    if(i == 0){
                        column.width = 40;
                    }else{
                        column.width = 15;
                    }
                    
                });
                worksheet.eachRow((row) => {
                    row.eachCell((cell) => {
                        cell.border = {
                            top: { style: 'thin' },
                            left: { style: 'thin' },
                            bottom: { style: 'thin' },
                            right: { style: 'thin' },
                        };
                
                    });
                });
                // Generate a buffer from the workbook
                const buffer = await workbook.xlsx.writeBuffer();
    
                // Create a Blob from the buffer
                const blob = new Blob([buffer], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
    
                // Create a download link
                const url = window.URL.createObjectURL(blob);
                const link = document.createElement('a');
                link.href = url;
                link.download = `${this.excelData.teamName}-${this.excelData.month}-Equipment Readiness Report.xlsx`;
    
                // Trigger the download
                link.click();
    
                // Clean up the URL object
                window.URL.revokeObjectURL(url);
            }



            
        },
        computed:{
            
        }

        
    }
</script>

