/<template>
    <div>

    </div>
</template>

<script>
    export default {
        data() {
            return {
                personnel:null,
                training:null,
                equipment:null,
                maintenance:null,
            }
        },
        methods:{
            redCon(data){
                // console.log(val,'val')
                let val
                if(data <= 100 && data >= 85){
                    val = 'R1 - Fully Mission Capable'
                }
                else if(data <= 84 && data >= 75){
                    val = 'R2 - Mission Capable'
                }
                else if(data <= 74 && data >= 51){
                    val = 'R3 - Partially Mission Capable'
                }
                else{
                    val = 'R4 - Not Mission Capable'
                }
               
                return val;
            },
            redConStyle(val){
                let style
                if(val.includes('R1')){
                    style = 'redCon1'
                }
                else if(val.includes('R2')){
                    style = 'redCon2'
                }
                else if(val.includes('R3')){
     
                    style = 'redCon3'
                }
                else{
                    style = 'redCon4'
                }
                return style
            }
        },
        watch: {
            
        },
    }
</script>

<style>
    .redCon1{
        background-color: green;
        color: white;
        font-weight: bold;
    }
    .redCon2{
        background-color: rgb(6, 133, 251);
        color: white;
        font-weight: bold;
    }
    .redCon3{
        background-color: yellow;
        font-weight: bold;
    }
    .redCon4{
        background-color:red;
        color: white;
        font-weight: bold;
    }
</style>