const axios = require("axios")
// const moment = require('moment');
const DelateTable = (val,realodFunc) =>{
    axios({
        method:'post',
        url: val.url,
        data:{
            Table : val.Table,
            Team : val.Team,
            Name: val.Name,
            Time: val.Time,
            Year : val.Year
        }
    }).then(res=>{
        if(res.status == 200){
            alert('Deleted')
            // realodFunc
            if (realodFunc && typeof realodFunc === 'function') {
                realodFunc();
                // console.log('go')
            }
        }
        
    })
}

module.exports = DelateTable