                <v-card elevation="3" class="justify-center" >
                    <v-card-title style="background-color: #0275d8;color:white;"><center>CONTROL PANEL</center></v-card-title>
                    <v-container>
                        <!-- <v-row class="d-flex justify-center">
                            <v-radio-group
                                v-model="reportRadio"
                                row
                                class="justify-center"
                            >
                                <v-radio
                                    label="Report"
                                    value="report"
                                ></v-radio>
                                <v-radio
                                    label="Summary"
                                    value="summary"
                                ></v-radio>
                            </v-radio-group>   
                        </v-row>
                   -->

                        <!-- <div v-if="reportRadio == 'report'"> -->
                            <!-- <v-container> -->
                                <v-row>
                                    <v-col cols="12">
                                        <v-dialog ref="dialog" v-model="generateMonthDialog" :return-value.sync="generateDate" persistent width="290px">
                                            <template v-slot:activator="{ on, attrs }">
                                            <v-text-field v-model="formattedDate" label="Select Month" append-icon="mdi-calendar" readonly v-bind="attrs" v-on="on" dense outlined class="text-center"></v-text-field>
                                            </template>
                                            <v-date-picker v-model="generateDate" type="month" scrollable>
                                            <v-spacer></v-spacer>
                                            <v-btn text color="primary" @click="generateMonthDialog = false">Cancel</v-btn>
                                            <v-btn text color="primary" @click="[$refs.dialog.save(generateDate),tempSubmit = []]">OK</v-btn>
                                            </v-date-picker>
                                        </v-dialog>
                                    </v-col>
                                    <v-col cols="12">
                                        <v-autocomplete 
                                            label="Submitted/Not Submitted"
                                            dense
                                            outlined
                                            style="margin-bottom:-40px;margin-top:-30px;"
                                            v-model="submit"
                                            :items="itemsSubmit"
                                            clearable
                                        />
                                    </v-col>
                                    <v-col cols="12">
                                        <v-autocomplete 
                                            label="Report Table"
                                            outlined
                                            dense
                                            style="margin-bottom:-40px;"
                                            v-model="report"
                                            :items="itemsReport"
                                            item-text="text"
                                            item-value="value"
                                            clearable
                                        />
                                    </v-col>
                                    <v-col cols="12">
                                        <v-autocomplete 
                                            label="Unit"
                                            outlined
                                            dense
                                            style="margin-bottom:-40px;"
                                            v-model="unit"
                                            :items="departmentItems"
                                            item-text="UnitDepartmentName"
                                            item-value="UnitDepartmentCode"
                                            clearable
                                        />
                                    </v-col>
                                   
                                    <v-col cols="12">
                                        <v-btn color="success" medium block :disabled="generateDate && submit &&  report && unit?false:true" @click="view()">VIEW</v-btn>
                                    </v-col>
                                </v-row>
                        <!-- </div> -->
                        <!-- <div v-else>
                            <v-dialog ref="dialog" v-model="generateMonthDialog" :return-value.sync="generateDate" persistent width="290px">
                                <template v-slot:activator="{ on, attrs }">
                                <v-text-field v-model="formattedDate" label="Select Month" prepend-icon="mdi-calendar" readonly v-bind="attrs" v-on="on" dense outlined class="text-center"></v-text-field>
                                </template>
                                <v-date-picker v-model="generateDate" type="month" scrollable>
                                <v-col cols="12">
                                    <v-autocomplete 
                                        label="Report Table"
                                        outlined
                                        dense
                                        style="margin-bottom:-40px;"
                                        v-model="report"
                                    />
                                </v-col>
                                <v-spacer></v-spacer>
                                <v-btn text color="primary" @click="generateMonthDialog = false">Cancel</v-btn>
                                <v-btn text color="primary" @click="[$refs.dialog.save(generateDate)]">OK</v-btn>
                                </v-date-picker>
                            </v-dialog>
                            <v-text-field
                                label="Search"
                                placeholder="Serial Number or Name"
                                dense
                                outlined
                            />
                            <v-btn color="primary" dark block medium :disabled="!generateDate?true:false">SEARCH</v-btn>
                        </div> -->
                    </v-container>
                </v-card>
            </v-col>

            <v-col cols="12" md="9" lg="9" xl="9">
                <v-card elevation="2">
                    <v-simple-table
                        fixed-header
                        height="80vh"
                        class="custom-table"
                    >
                        <thead>
                            <tr v-if="submitTable">
                                <th width="10%">No.</th>
                                <th v-if="submit == 1">Name of Unit Submitted</th>
                                <th v-else>Name of Unit Not Yet Submitted</th>
                                <th v-show="submit == 1">Prepared Date</th>
                                <th v-show="submit == 1">Prepared By</th>
                                <th v-show="submit == 1">Noted Date</th>
                                <th v-show="submit == 1">Noted By</th>
                                <th v-show="submit == 1">Approved Date</th>
                                <th v-show="submit == 1">Approved By</th>
                                <th v-show="submit == 1">Unlocked</th>
                               
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(item,i) in submitTable" :key="i">
                                <td>{{ i + 1 }}</td>
                                <td style="text-align:left !important;font-weight:bold;">{{ item.UnitTeamName }}</td>
                               
                                <td v-if="submit == 1" >{{ item.PreparedDate }}</td>
                                <td v-if="submit == 1" >{{ item.PreparedBy }}</td>
                                <td v-if="submit == 1" >{{ item.NotedDate }}</td>
                                <td v-if="submit == 1" >{{ item.NotedBy }}</td>
                                <td v-if="submit == 1" >{{ item.ApprovedDate }}</td>
                                <td v-if="submit == 1" >{{ item.ApprovedBy }}</td>
                             
                                
                                <!-- <td v-if="submit == 1"><v-btn icon @click="viewReport(item)"><v-icon color="success">mdi-eye</v-icon></v-btn></td> -->
                                <td v-if="submit == 1"><v-btn icon @click="unlocked(item)"><v-icon color="primary">mdi-lock-open</v-icon></v-btn></td>
                            </tr>
                        </tbody>
                    </v-simple-table>
                    <v-overlay :value="overlay">
                        <v-progress-circular
                            indeterminate
                            size="64"
                        ></v-progress-circular>
                    </v-overlay>
                </v-card>
            </v-col>
        </v-row>
        <ReportPersonnelDialog ref="ReportPersonnelDialog" />
        <ReportTrainingDialog ref="ReportTrainingDialog" />
        <ReportEquipmentDialog ref="ReportEquipmentDialog" />
    </div>
</template>

<script>
import ReportPersonnelDialog from './ReportPersonnelDialog.vue'
import ReportTrainingDialog from './ReportTrainingDialog.vue'
import ReportEquipmentDialog from './ReportEquipmentDialog.vue'

import {mapState} from 'vuex'
import axios from 'axios'
export default {
    components:{
        ReportPersonnelDialog,
        ReportTrainingDialog,
        ReportEquipmentDialog
    },
    name:'ReportComponent',
    data:()=>({
        reportRadio:'report',
        generateMonthDialog: false,
        generateDate: null,
        itemsSubmit:[{text:'Submitted',value:'1'},{text:'Not Yet Submit',value:'2'}],
        submit:null,
        // itemsReport:['Personnel','Training','Equipment','Maintenance','Facility'],
        report:null,
        unit:null,
        tempSubmit:[],
        overlay:false,
        departmentItems:[],

    }),
    methods:{
        getDepartment(){
            axios({
                method:'get',
                url:`${this.$baseUrl}api/getDepartmentListAdmin`,
            }).then(r=>{
                this.departmentItems = r.data
            })
        },
        view(){
            this.overlay = true
            axios({
                method:'post',
                url:`${this.$baseUrl}api/getSubmitted`,
                data:{
                    Month : this.generateDate,
                    Submit : this.submit,
                    Report : this.report.toLowerCase(),
                    Unit : this.unit
                }
            }).then(res=>{
                this.tempSubmit = res.data
                this.overlay = false
                // console.log(this.tempSubmit)
            })
        },
        unlocked(item){
            const confirmed = window.confirm('Are you sure you want to unlock?');
            if (confirmed) {
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/getUnlocked`,
                    data:{
                        Team : item.UnitTeamCode,
                        Month : item.Month,
                        Table : item.Table
                    }
                }).then(res=>{
                    if(res.status == 200){
                        this.view()
                    }
                    
                })
            }
        },
       async viewReport(item){
            this.$store.state.monthData = await item.Month
            this.$store.state.teamData = await item.UnitTeamCode
            if(this.report == 'Personnel'){
                await this.$refs.ReportPersonnelDialog.openDialog(item)
            }else if(this.report == 'Training'){
                await this.$refs.ReportTrainingDialog.openDialog(item)
            }else if(this.report == 'Equipment'){
                await this.$refs.ReportEquipmentDialog.openDialog(item)
            }
            
            
        }
    },
    computed:{
        ...mapState([
            'teamItems'
        ]),
        formattedDate() {
                if (this.generateDate) {
                    const date = new Date(this.generateDate);
                    const month = date.toLocaleString('default', { month: 'long' });
                    const year = date.getFullYear();
                    return `${month} ${year}`;
                }
                return null;
        },
        itemsReport(){
            let data
                let office = this.$store.state.user.office
                if(office == 'T12'){
                    data =['Personnel']
                }else if(office == '15'){
                    data = ['Equipment','Maintenance','Facility']
                }else if(office == 'T19'){
                    data = ['Training']
                }else{
                    data = ['Personnel','Training','Equipment','Maintenance','Facility']
                }
            return data
        },
        submitTable(){
            if (this.tempSubmit && this.submit) {
                return this.tempSubmit.reduce((total, item) => {
                    if (this.submit === '1') {
                        if (item.Submit === '5') {
                            total.push(item);
                        }
                    } else {
                        if (item.Submit !== '5') {
                            total.push(item);
                        }
                    }
                    return total;
                }, []);
            }else{
                return false
            }
        },
       
        
    },
    watch:{
        generateDate(val){
            if(val){
                this.getDepartment()
            }
        },
        report(val){
            if(!val){
                this.tempSubmit = []
            }
        }
    },
    mounted(){
        this.getDepartment();
    }
}
</script>
<style>
.v-slide-group__content {
    justify-content:center;
}
.v-data-table > .v-data-table__wrapper > table > thead > tr > th{
    font-size: 14px !important; 
    background-color: #0275d8 !important;
    text-align: center !important;
    height:50px; /* height para sa Header*/
    color:white !important;
    font-weight: bold !important;
}
.v-data-table > .v-data-table__wrapper > table > tbody > tr > td,
.v-data-table > .v-data-table__wrapper > table > tfoot > tr > th {
    font-size: 14px ;
    text-align: center;
}
.v-data-table > .v-data-table__wrapper > table {
    text-align:center; /* text naka center*/
}
.text-center .v-text-field__slot input {
    text-align: center;
  }
  .custom-container {
    margin-bottom: -10;
  }
  
  .custom-container .v-input__control {
    padding-top: -10px; /* Adjust top padding */
    padding-bottom: -10px; /* Adjust bottom padding */
  }
  
  .custom-container .v-label {
    margin-bottom: 1px; /* Adjust bottom margin */
  }
</style>