<template>
    <div>
        <v-dialog v-model="reportDialog" persistent scrollable>
            <v-card>
                <v-card-title style="background-color: #0275d8; color: white;">
                    TRAINING READINESS
                    <v-spacer/><v-btn  @click="reportDialog = false" icon><v-icon color="white">mdi-close-circle-outline</v-icon></v-btn>
                </v-card-title>
                <br>
                <v-card-text>
                    <h1><center>{{ teamName }}</center></h1>
                    <h2><center>{{ formattedDate }}</center></h2>
                <br>
        <v-divider/>
        <v-divider/>
        <v-col cols="12" md="12" lg="12" xl="12">
                <v-tabs v-model="training_tab" justify-center>
                    <v-tab href="#mett" @click="mett('mett')">MISSION ESSENNTIAL TASK TRAINING</v-tab>
                    <v-tab href="#orac" @click="orac('orac')" v-if="tableData.length > 0">CONSOLIDATED OPERATIONALLY & AVAILABLE CREW</v-tab>
                    <v-tab href="#trr" @click="trr('trr')" v-if="tableData.length > 0">TRAINING READINESS RAITING</v-tab>
                </v-tabs>
        </v-col>
        <v-tabs-items v-model="training_tab">
            <v-tab-item :value="'mett'">
            
                  
                            <v-simple-table class="elevation-1 custom-table" height="80vh" fixed-header>
                                <thead>
                                    <tr>
                                        <th>MISSION ESSENTIAL TASK</th>
                                        <th>1st Qtr</th>
                                        <th>2nd Qtr</th>
                                        <th>3rd Qtr</th>
                                        <th>4th Qtr</th>
                                        <th>Required</th>
                                        <th>Actual</th>
                                        <th>Percentage</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="(item,i) in tableData" :key="i">
                                        <td style="white-space: pre-wrap;font-weight:bold;">{{item.Met}}</td>
                                        <td style="white-space: pre-wrap;">{{ item.Quarter1 }}</td>
                                        <td style="white-space: pre-wrap;">{{ item.Quarter2 }}</td>
                                        <td style="white-space: pre-wrap;">{{ item.Quarter3 }}</td>
                                        <td style="white-space: pre-wrap;">{{ item.Quarter4 }}</td>
                                        <td style="white-space: pre-wrap;">{{ item.Required }}</td>
                                        <td style="white-space: pre-wrap;">{{ item.Actual }}</td>
                                        <td>{{itemPercentage(item)}}</td>
                                    </tr>
                                    <tr v-if="tableData.length > 0">
                                        <td colspan="5"></td>
                                        <td class="total">{{ totalRequired }}</td>
                                        <td class="total">{{ totalOrac }}</td>
                                        <td class="total">{{ metPercentage }}</td>
                                    </tr>
                                </tbody>
                            </v-simple-table>
            </v-tab-item>
            <!-- ORAC TABLE -->
            <v-tab-item :value="'orac'">
                <v-container>
                    <v-card>
                        <v-simple-table class="elevation-1 custom-table">
                            <thead>
                                <tr>
                                    <th>UNIT</th>
                                    <th>NR OF PERSONNEL<br>PER UMD</th>
                                    <th>NR ACTUAL<br>ASSIGNED</th>
                                    <th>ORAC</th>
                                    <th>PERCENTAGE</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td>{{ unitName }}</td>
                                    <td>{{ totalToe }}</td>
                                    <td>{{ totalAssigned }}</td>
                                    <td>{{ totalOrac }}</td>
                                    <td>{{ oracPercentage }}</td>
                                </tr>
                            </tbody>
                        </v-simple-table>
                    </v-card>
                </v-container>
            </v-tab-item>

            <!-- TRR -->
            <v-tab-item :value="'trr'">
                <v-container>
                    <v-card>
                        <v-simple-table class="elevation-1 custom-table">
                            <thead>
                                <tr>
                                    <th>UNIT</th>
                                    <th>PERCENTAGE<br>ORAC</th>
                                    <th>PERCENTAGE<br>METT</th>
                                    <th>TRR</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td>{{ unitName }}</td>
                                    <td>{{ oracPercentage }}</td>
                                    <td>{{ metPercentage }}</td>
                                    <td>{{ trRating }}</td>
                                </tr>
                            </tbody>
                        </v-simple-table>
                    </v-card>
                </v-container>
                <v-divider />
                <br>
                <v-container>
                <!------------------assessment table------------------------- -->
                <h3>Assessment:</h3>
                
                    <v-card elevation="2">
                        
                            <v-simple-table
                                class="custom-table"
                            >
                            
                            <tbody v-for="(val, e) in remarksData" :key="e">
                                <tr>
                                    <td style="font-weight: bold;text-align: left !important;background-color: lightgray;">{{ val.Question }}</td>
                                </tr>
                                <tr height="90%">
                                    <td style="white-space: pre-wrap;">{{ val.Remarks }}</td>
                                </tr>
                            </tbody>  
                            </v-simple-table>
                    
                    </v-card>
            </v-container>
            </v-tab-item>
        </v-tabs-items>
                </v-card-text>
            </v-card>
        
        </v-dialog>
    </div>
</template>

<script>
import moment from 'moment'
import { mapState } from 'vuex'
import axios from 'axios'
    export default {
        data() {
            return {
                reportDialog:false,
                training_tab:'mett',
                tableData:[],
                generateDate:null,
                teamName:null,
                team:null,
            }
        },
        methods: {
            async openDialog(item){
                this.generateDate = item.Month
                this.team = item.UnitTeamCode
                this.teamName = item.UnitTeamName
                this.reportDialog = true
                this.getTrainingData()
                await this.$store.dispatch('diplayRemarks')
                // this.tableContainer = this.lastMonthData
            },
            async  getTrainingData(){
       
                this.$store.state.teamData = this.team
                this.$store.state.monthData = this.generateDate
                
            
                axios({
                    method:'post',
                    url:`${this.$baseUrl}api/getTrainingData`,
                    data:{
                        Team : this.team,
                        Month : this.generateDate,
                    }
                }).then(res=>{
                    this.tableData = res.data
                    console.log(this.tableData,'ceo')
                })
            },
            mett(tab){
                this.training_tab = tab
            },
            orac(tab){
                this.training_tab = tab
            },
            trr(tab){
                this.training_tab = tab
                this.$store.dispatch('diplayRemarks','training')
            },
            itemPercentage(item){
                return  this.colPercentage =(item.Actual / item.Required * 100).toFixed(2)
            },
        },
        computed:{
       ...mapState([
            'teamItems',
            'psgTable',
            'remarksData',
            'trainingTableUpdate'
        ]),
         // function to show month name
         formattedDate() {
                if (this.generateDate) {
                    const date = new Date(this.generateDate);
                    const month = date.toLocaleString('default', { month: 'long' });
                    const year = date.getFullYear();
                    return `${month} ${year}`;
                }
                return null;
        },
        // to check if the unit is submit
        submitted(){
            if(this.tableData.length > 0){
                return this.tableData.find(res=> res.Submit == '1')
            }else{
                return 0
            }
        },
         // 
         totalRequired(){
            if(this.tableData.length > 0){
                return this.tableData.reduce((total, item) => {
                    if (item.Required) {
                        return total + Number(item.Required);
                    }
                    return total;
                    }, 0);
            }else{
                return 0
            }
        },
       
       
        totalOrac(){
            if(this.tableData.length > 0){
                return this.tableData.reduce((total, item) => {
                    if (item.Actual) {
                        return total + Number(item.Actual);
                    }
                    return total;
                    }, 0);
            }else{
                return 0
            }
        },
        // function to get total psg for psg tab
        metPercentage(){
            if(this.tableData.length > 0){
               let result
                result = (this.totalOrac / this.totalRequired * 100).toFixed(2) + '%'
               return result
            }else{
                return 0
            }
        },

        // function to get total Actual for psg tab
        totalAssigned(){
            if(this.psgTable.length > 0){
                return this.psgTable.reduce((total, item) => {
                    if (item.TotalActual) {
                        return total + item.TotalActual;
                    }
                    return total;
                    }, 0);
            }else{
                return 0
            }
        },
        // 
        oracPercentage(){
            if(this.tableData.length > 0){
               let result
                result = (this.totalOrac / this.totalAssigned * 100).toFixed(2) + '%'
               return result
            }else{
                return 0
            }
        },
        trRating(){
            if(this.tableData.length > 0){
               let result
               let met = this.totalOrac / this.totalRequired * 0.60
               let orac = this.totalOrac / this.totalAssigned * 0.40
                result = (orac + met  * 100).toFixed(2) + '%'
               return result
            }else{
                return 0
            }
        },
        
        //unit name
        unitName(){
            let data
            this.teamItems.filter(r=>{
                if(r.UnitTeamCode == this.teamCode){
                    data = r.UnitTeamName
                }
            })
            return data
        },
        
        yearItems(){
            const tempData = []
            const currentYear = moment().year();
            const endYear = currentYear + 5;

            for (let year = 2020; year <= endYear; year++) {
                tempData.push(year);
            }

            return tempData;

        },
         // function to get total TOE for psg tab
         totalToe(){
            if(this.psgTable.length > 0){
                return this.psgTable.reduce((total, item) => {
                    if (item.TotalRequired) {
                        return total + item.TotalRequired;
                    }
                    return total;
                    }, 0);
            }else{
                return 0
            }
        },
        
    },
    mounted(){
        this.$store.dispatch('getTeam')
    },
    watch:{
        tableData(val){
            if(val){
                this.$store.dispatch('getPsgTable')
            }
            
        },
        trainingTableUpdate(val){
            if(val){
                this.getTrainingData()
            }
        }
    }
    }
</script>

<style lang="scss" scoped>

</style>