<template>
  <v-container fluid class="container" >
    <!-- <h1>SAMPLE IMAGE HERE</h1> -->
    <div class="d-none d-md-block d-lg-block d-xl-block">
      <v-img :src="image[1].val"/>
    </div>
    <div class="d-sm-flex d-md-none">
      <v-img :src="image[5].val"/>
    </div>
   
    <!-- <v-carousel
      cycle
      height="90%"
      hide-delimiter
      show-arrows-on-hover
    >
      <v-carousel-item
        v-for="(item, i) in image"
        :key="i"
      >
            <v-img :src="item.val"/>
      </v-carousel-item>
    </v-carousel> -->
   
  </v-container>
</template>

<script>
  import axios from 'axios';
  import moment from 'moment'
  import image0 from '../../img/image0.jpg';
  import image1 from '../../img/home.png';
  import image2 from '../../img/image2.jpg';
  import image3 from '../../img/image3.jpg';
  import image4 from '../../img/image4.jpg';
  import image5 from '../../img/D01.png';
  export default {
    name: 'HomeView',
    data() {
      return {
        image:[
          {val:image0},
          {val:image1},
          {val:image2},
          {val:image3},
          {val:image4},
          {val:image5},
        ],
        isAuthenticated: false,
        inactivityTimeout: null,
      }
    },
    created() {
      // this.login(); // Perform login on component creation
    },
    methods: {
      login() {
        this.isAuthenticated = true;
        this.startInactivityTimer();
      },
      logout() {
        this.clearInactivityTimeout();
        this.isAuthenticated = false;
        this.getLogout()
        // Other logout actions
      },
      getLogout(){
            axios({
               method:'post',
               url:`${this.$baseUrl}api/logout`,
               data :{
                  id  : this.$store.state.user.id,
                  log : moment().format('YYYY-MM-DD HH:mm:ss')
               }
            }).then(res=>{
               if(res.status == 200){
                  this.$store.dispatch('getLogin','logout')
                  // this.$store.dispatch('logout')
                  this.$store.state.hideLogin = false
                  this.$router.push('/login')
               }
            })
            
         },
      startInactivityTimer() {
        const inactivityTime = 600000; // 10 minutes (adjust as needed)
        this.inactivityTimeout = setTimeout(() => {
          this.logout(); // Automatically logout after inactivityTime
        }, inactivityTime);

        const resetTimer = () => {
          clearTimeout(this.inactivityTimeout);
          this.inactivityTimeout = setTimeout(() => {
            this.logout(); // Reset the timer and logout after inactivityTime
          }, inactivityTime);
        };

        const resetTimerOnActivity = () => {
          resetTimer();
          window.addEventListener('mousemove', resetTimer);
          window.addEventListener('keydown', resetTimer);
          // Add more event listeners for other user activities
        };

        resetTimerOnActivity();

        // Clear event listeners when the component is destroyed
        window.addEventListener('beforeunload', () => {
          window.removeEventListener('mousemove', resetTimer);
          window.removeEventListener('keydown', resetTimer);
          // Remove other event listeners if added
        });
      },
      clearInactivityTimeout() {
        clearTimeout(this.inactivityTimeout);
        this.inactivityTimeout = null;
      },
  },

  }
</script>
<style scoped>
.container {
  opacity: 0.9;
  position: relative;
  background-size: cover;
}
